import { Injectable } from '@angular/core';
import {UserDetails} from '../Models/userProfileModel';
import { Observable,throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import {HttpClient, HttpErrorResponse} from '@angular/common/http'; 
import {DialogConfigServiceService} from '../Configurations/dialog-config-service.service';
@Injectable({
  providedIn: 'root'
})
export class UserProfileService {
  Url :string; 
  header : any;  
  errorMessage:string;
  constructor(private http : HttpClient,config:DialogConfigServiceService) {
    this.Url = config.getConfig().apiBaseUrl;
   }
  saveUserProfile(save:UserDetails)  
  {  
   return this.http.post(this.Url + '/dialogbuilder-ivr/api/user', save) 
   .pipe(
     retry(1),
   );
  }  

  getUserProfileDetail() : Observable<UserDetails[]> 
  {
   return this.http.get<UserDetails[]>(this.Url+ '/dialogbuilder-ivr/api/user') 
   .pipe(
     retry(1),
   );
  }
  getUserProfileDetailById(id: string) :Observable<UserDetails> 
  {
   return this.http.get<UserDetails>(this.Url+ '/dialogbuilder-ivr/api/user/'+id)
   .pipe(
     retry(1),
   
   );
  }
  updateUserProfile(update:UserDetails)
  {
    return this.http.put(this.Url +'/dialogbuilder-ivr/api/user',update)
    .pipe(
     retry(1),
  
   );
  }
  deleteUserProfile(id: string)
  {
    return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/user/'+id)
    .pipe(
     retry(1),
   
   );
  }


}
