import { Component, OnInit,ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
@Component({
  selector: 'app-apps-builer',
  templateUrl: './apps-builer.component.html',
  styleUrls: ['./apps-builer.component.css']
})
export class AppsBuilerComponent implements OnInit {

  displayedColumns: string[] = ['Actions','Name','DialogFile','NextItem','Type','AttachData','Delete'];
  dataSource = new MatTableDataSource(ELEMENT_DATA);

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  constructor(public router: Router)
   { }

  ngOnInit(){
  
  }
  GetTreeView()
  {
    this.router.navigateByUrl('dialogbuilder/treeview');
  }
}
export interface PeriodicElement {
  Name: string;
  DialogFile: string;
  NextItem: string;
  Type: string;
 
}

