import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {EnvironmentService} from '../Shared/Services/environment.service';
import {EnvironmentDetail} from '../Shared/Models/environmentModel';
import {MatSnackBar} from '@angular/material/snack-bar';
@Component({
  selector: 'app-environment-builder',
  templateUrl: './environment-builder.component.html',
  styleUrls: ['./environment-builder.component.css']
})
export class EnvironmentBuilderComponent implements OnInit {
  environmentDetail:EnvironmentDetail
  environmentForm: FormGroup;
  inEditPage: boolean;
  id:string;
  constructor(private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,private environmentService:EnvironmentService,private _snackBar: MatSnackBar,) { }

  ngOnInit() {
    this.environmentDetail={
      id:'',
      name:'',
      grammarDirectory:'',
      logDirectory:'',
      promptDirectory:''
    }
    if(this._router.snapshot.url[0].path =='environmentEdit')
    {
      this.inEditPage = true;
      this.id=this._router.snapshot.params["id"];
      this.environmentService.getEnvironmentById(this.id).subscribe(data=>{
        this.initForm(data)
      })
    }
    this.initForm(this.environmentDetail);
  }
  initForm(environmentDetail:EnvironmentDetail) {
   
    this.environmentForm = this.formBuilder.group({
      
      name: [environmentDetail.name, Validators.required],
      logDirectory: [environmentDetail.logDirectory, Validators.required],
      promptDirectory: [environmentDetail.promptDirectory, Validators.required],
      grammarDirectory: [environmentDetail.grammarDirectory, Validators.required],
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.environmentForm.controls; } 
  onSubmit()
  {

   if(this.environmentForm.valid){
    
     if(this.inEditPage)
     {
      
       this.UpdateEnvironment(new EnvironmentDetail(this.environmentForm.value));       
     }
     else
     {
       this.SaveEnvironment(new EnvironmentDetail(this.environmentForm.value));       
     }
   }
   }
   SaveEnvironment(environmentDetail:EnvironmentDetail)
   {
this.environmentService.SaveEnvironment(environmentDetail).subscribe(data=>{
console.log("data")
console.log(data)
  this._snackBar.open("Environment saved successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
  this.router.navigate(['dialogbuilder/environment']);
})
   }
   UpdateEnvironment(environmentDetail:EnvironmentDetail)
   {
     environmentDetail.id=this.id+"";
this.environmentService.updateEnvironment(environmentDetail).subscribe(data=>{
  this._snackBar.open("Environment update successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
  this.router.navigate(['dialogbuilder/environment']);
})
   }
}
