import { Component, OnInit,Inject ,ViewChild} from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import {GlobalNextStateDetail} from '../Shared/Models/globalerrorModel';
import {AppsService} from '../Shared/Services/apps.service';
import {NextStateDetail} from '../Shared/Models/nextStateModel';
import {StatePromptService} from '../Shared/Services/state-prompt.service';
import {MatSnackBar} from '@angular/material/snack-bar';
export interface DialogData {

}

@Component({
  selector: 'app-edit-option-popup',
  templateUrl: './edit-option-popup.component.html',
  styleUrls: ['./edit-option-popup.component.css']
})
export class EditOptionPopupComponent implements OnInit {
  isSelectedFirst:boolean=false;
  globalErrorForm: FormGroup;
  globalNextStateDetail:GlobalNextStateDetail
  stateTypes: stateType[] = []

  branchValue:string;
  action:string;
  appId:string;
  type:string;
  local_data:any;
  selectedState:string
  nextStateTypes: nextStateType[] = [
   
    {value: 'transfer', viewValue: 'Transfer'},
    {value: 'disconnect', viewValue: 'Disconnect'}
  ];
  selectedType:string="disconnect"
  constructor(private formBuilder: FormBuilder,private statePromptService:StatePromptService ,  public dialogRef: MatDialogRef<EditOptionPopupComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,private appsService:AppsService,private _snackBar: MatSnackBar,) 
    {
      
      console.log("popup data")
      this.local_data = data["obj"];
      console.log("get update data")
      console.log( this.local_data );
    
    this.appId=data["appId"]
     }

  ngOnInit() {
    this.globalNextStateDetail={
      id:'',
      option:'',
      nextState:'',
      nextStateName:'',
      nextStateType:''
    }
    this.globalNextStateDetail=this.local_data;
   this.selectedType= this.globalNextStateDetail.nextStateType;
   this.selectedState=this.globalNextStateDetail.nextStateName;

    console.log(this.globalNextStateDetail)
    this.initForm(this.globalNextStateDetail);
  }
  onNoClick(): void {
    this.dialogRef.close();
  }
  initForm(globalStateDetail:GlobalNextStateDetail) {
   
    this.globalErrorForm = this.formBuilder.group({
      
      option: [globalStateDetail.option,],
      nextState: [globalStateDetail.nextState, ],
     // nextStateName: [globalStateDetail.nextStateName, ],
      nextStateType: [globalStateDetail.nextStateType, ],
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.globalErrorForm.controls; } 
  updateGlobal()
  {
    this.updateGlobalNext(new GlobalNextStateDetail(this.globalErrorForm.value))
  }
  updateGlobalNext(GlobalDetail:GlobalNextStateDetail)
  {
    GlobalDetail.id=this.globalNextStateDetail.id+"";
    console.log("update global")
console.log(GlobalDetail)
    if(GlobalDetail.nextState != "undefined" && GlobalDetail.nextState != "" && GlobalDetail.nextState != null)
    {
      GlobalDetail.nextState=this.stateTypes.find(x=>x.stateName==GlobalDetail.nextState).id+""; 
      GlobalDetail.nextStateName=this.selectedState+"";
    }
    if(GlobalDetail.nextStateType != "undefined" && GlobalDetail.nextStateType != "" && GlobalDetail.nextStateType != null)
    {
      GlobalDetail.nextStateType=this.selectedType+""
    }
    else{
      GlobalDetail.nextState="";
      GlobalDetail.nextStateName="";
    }
    console.log("update global")
console.log(GlobalDetail)
this.appsService.UpdateGlobalError(GlobalDetail).subscribe(data=>{
console.log("update response global")
console.log(data);
this.dialogRef.close();
if(data["statusCode"] ==500)
{
  this._snackBar.open("Internal server error!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['error'],
});
}
else{
  this._snackBar.open("Updated !", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
}
})
  }
  onTypeSelect(){
    console.log("selectedtype")
    console.log(this.selectedType)
  //   if(!this.isSelectedFirst)
  //   {
  //     this.selectedState="";
  
  //   }
  // else{
  //   this.isSelectedFirst=false
  // }
  

  console.log(this.selectedState)
  this.selectedState=this.selectedState+""
  if(this.selectedType=="branching" )
  {
    this.branchValue="branch"
  }
  else if(this.selectedType=="nonBackend")
  {
    this.branchValue="nonbackend"
  }
 else{
   this.branchValue=this.selectedType+""
 }
 if(this.selectedType=="disconnect" )
 {
   this.selectedState="";
 }
 console.log("branch name")
 console.log(this.branchValue)
    this.statePromptService.getAllStateName(this.appId, this.branchValue).subscribe(data=>{
console.log("response")
console.log(data)
      console.log("")
      this.stateTypes=data
      if( this.stateTypes.length>0)
      this.stateTypes=data
      else
      this.stateTypes=[]
      console.log(this.stateTypes)
    })
  }
}
interface nextStateType {
  value: string;
  viewValue: string;
}
interface stateType{
  id:string;
  stateName:string;
}