import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {BranchingOptionDetail} from '../Shared/Models/branchingOptionModel';
import {MatSnackBar} from '@angular/material/snack-bar';
import {BranchOptionsService} from '../Shared/Services/branch-options.service';
import {BranchingOptionHolidayDetail} from '../Shared/Models/branchOptionHolidayModel';
import {HolidayService} from '../Shared/Services/holiday.service';
@Component({
  selector: 'app-branch-holiday',
  templateUrl: './branch-holiday.component.html',
  styleUrls: ['./branch-holiday.component.css']
})
export class BranchHolidayComponent implements OnInit {
  optionHolidayDetail:BranchingOptionHolidayDetail
  branchHolidayForm: FormGroup;
  inEditPage: boolean;
  appId:string;
  stateId:string;
  appName:string;
  type:string;
  optionId:string;
  branchType:string;
holidays: holiday[] = [
   
  ];
  constructor(private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,private _snackBar: MatSnackBar,private branchOptionsService:BranchOptionsService,private holidayService:HolidayService) { }

  ngOnInit() {
    this.optionHolidayDetail={
      id:'',
      holidayId:'',
      holidayName:'',
      option:'',
      type:'',
      date:''
    }
    if(this._router.snapshot.url[0].path =='createBranchHoliday')
    {
      this.inEditPage = false;
      this.appId=this._router.snapshot.params["appId"];
      this.stateId=this._router.snapshot.params["stateId"];
      this.appName=this._router.snapshot.params["appName"];
      this.type=this._router.snapshot.params["type"];
      this.branchType=this._router.snapshot.params["branchType"];
      this.holidayService.getHolidayName(this.appId).subscribe(data=>{
        console.log("get holiday")
        console.log(data)
        this.holidays=data
      })
    }
    if(this._router.snapshot.url[0].path =='editBranchHoliday')
    {
      this.inEditPage = true;
      this.appId=this._router.snapshot.params["appId"];
      this.stateId=this._router.snapshot.params["stateId"];
      this.appName=this._router.snapshot.params["appName"];
      this.type=this._router.snapshot.params["type"];
      this.branchType=this._router.snapshot.params["branchType"];
      this.optionId=this._router.snapshot.params["optionId"];
      this.holidayService.getHolidayName(this.appId).subscribe(data=>{
        console.log("get holiday")
        console.log(data)
    
        this.holidays=data
      })
      this.branchOptionsService.getBranchOptionHolidayById(this.optionId).subscribe(data=>{
        console.log(data)
        this.optionHolidayDetail=data;

      
        this.optionHolidayDetail.holidayId=  this.holidays.find(x=>x.holidayName==this.optionHolidayDetail.holidayName).id;
  
        this.initForm( this.optionHolidayDetail)
      })
    }
    this.initForm(this.optionHolidayDetail);
  }
initForm(holidayDetail:BranchingOptionHolidayDetail) {
   
    this.branchHolidayForm = this.formBuilder.group({
      
      option: [holidayDetail.option, Validators.required],
      holidayId: [holidayDetail.holidayId, Validators.required],
   
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.branchHolidayForm.controls; }  
  onSubmit()
  {

   if(this.branchHolidayForm.valid){
    
     if(this.inEditPage)
     {
      
       this.UpdateBranchOptionHOD(new BranchingOptionHolidayDetail(this.branchHolidayForm.value));       
     }
     else
     {
       this.SaveBranchOptionHOD(new BranchingOptionHolidayDetail(this.branchHolidayForm.value));       
     }
   }
   
   }
   SaveBranchOptionHOD(branchHOD:BranchingOptionHolidayDetail)
   {
     console.log("save holiday")
  
     branchHOD.holidayName=branchHOD.holidayId+"";
     console.log(branchHOD)
     this.branchOptionsService.SaveBranchOptionHoliday(this.stateId,branchHOD).subscribe(data=>{
      this._snackBar.open("Holiday saved successfully!", "", {
        duration: 2000,
        verticalPosition: "bottom",
        horizontalPosition:'right',
        panelClass: ['success'],
    });
    this.router.navigate(['dialogbuilder/nextState',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:"branching",branchType:this.branchType}])
     })
   }
   UpdateBranchOptionHOD(branchHOD:BranchingOptionHolidayDetail)
   {
    branchHOD.holidayName=branchHOD.holidayId+"";
this.branchOptionsService.updateBranchOptionHoliday(this.optionId,branchHOD).subscribe(data=>{
  this._snackBar.open("Holiday updated successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
})
   }
   backtoOption()
   {
     if(this.type.toLowerCase()== "branching")
     {
      console.log("branch type")
      console.log(this.branchType)
         this.router.navigate(['dialogbuilder/nextState',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:"branching",branchType:this.branchType}])
 
     }
   }
}
interface holiday {
  id: string;
  holidayName: string;
}