import { Component, OnInit,Inject } from '@angular/core';

import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {GroupPromptService} from '../Shared/Services/group-prompt.service';
import {PromptGroupDetail} from '../Shared/Models/promptsGroupModel';
import {MatSnackBar} from '@angular/material/snack-bar';
interface app {
  id: string;
  appName: string;
}

interface appGroup {

  name: string;
  app: app[];
}
@Component({
  selector: 'app-create-prompt-group',
  templateUrl: './create-prompt-group.component.html',
  styleUrls: ['./create-prompt-group.component.css']
})

export class CreatePromptGroupComponent implements OnInit {
  promptGroup:PromptGroupDetail
  promptId:string;
  selected = 'global';
  promptGroupForm: FormGroup;
  inEditPage: boolean;
  appGroups: appGroup[] = []
  type:string
  constructor(private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,private _snackBar: MatSnackBar,
 private promptGroupService:GroupPromptService) { }

  ngOnInit() {
  

    this.promptGroupService.getAllAppName().subscribe((data)=>{
 
      this.appGroups = [
        {
          name: 'Apps',
          app: 
            data
        },
      ];
      });
 this.promptGroup={
  id:'',
  promptGroupName:'',
  specification:'',
  description:'',
  promptName: '',
  interpretation: '',
  alternateText:'',
  type:'',
  promptGroupId:''
 }
    this.initForm(this.promptGroup);
    if(this._router.snapshot.url[0].path =='editPromptGroup')
    {
      this.inEditPage = true;
      this.promptId=this._router.snapshot.params["id"];
      this.promptGroupService.getPromptGroupById( this.promptId).subscribe(data=>{
        console.log(data)
        this.initForm(data)
      })
    }
  }
  initForm(prompt:PromptGroupDetail) {
   
    this.promptGroupForm = this.formBuilder.group({
      
      promptGroupName: [prompt.promptGroupName, Validators.required],
      description: [prompt.description,],
      specification: [prompt.specification, Validators.required],
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.promptGroupForm.controls; }  
  onSubmit()
  {

   if(this.promptGroupForm.valid){
    
    if(this.inEditPage)
     {
      
       this.UpdatePromptGroup(new PromptGroupDetail(this.promptGroupForm.value));       
     }
     else
     {
       this.SavePromptGroup(new PromptGroupDetail(this.promptGroupForm.value));       
     }
   }
   }
   SavePromptGroup(prompt:PromptGroupDetail)
   {
     console.log("save prompt data")
     console.log(prompt)
         this.promptGroupService.SavePromptGroup(prompt).subscribe(data=>{
       
            this._snackBar.open("Prompt group saved successfully!", "", {
              duration: 2000,
              verticalPosition: "bottom",
              horizontalPosition:'right',
              panelClass: ['success'],
          });
          this.router.navigateByUrl('/dialogbuilder/promptGroupDetail');
         });
   }
   UpdatePromptGroup(prompt:PromptGroupDetail)
   {
    console.log("updates")
     console.log(prompt)
     prompt.id=this.promptId;
     this.promptGroupService.updatePromptGroup(prompt).subscribe(data=>{
      this._snackBar.open("Prompt group updated successfully!", "", {
        duration: 2000,
        verticalPosition: "bottom",
        horizontalPosition:'right',
        panelClass: ['success'],
    });
    this.router.navigateByUrl('/dialogbuilder/promptGroupDetail');
     })


   }
   omit_special_char(event)
   {   
      var k;  
      k = event.charCode;  //k = event.keyCode; (Both can be used)
   
    return((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8  || (k >= 48 && k <= 57)); 
   }
}
export interface DialogData {
  id: string;
  type: string;
}