import { Injectable } from '@angular/core';
import { Observable,throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import {HttpClient, HttpErrorResponse} from '@angular/common/http'; 
import {StateMenuDetail} from '../Models/stateMenuModel';
import {DialogConfigServiceService} from '../Configurations/dialog-config-service.service';
@Injectable({
  providedIn: 'root'
})
export class StateMenuService {
  Url :string; 
  header : any;  
  errorMessage:string;
  constructor(private http : HttpClient,config:DialogConfigServiceService) {
    this.Url = config.getConfig().apiBaseUrl;
   }
   getStateMenu(id:string) : Observable<StateMenuDetail[]> 
   {
    return this.http.get<StateMenuDetail[]>(this.Url+ '/dialogbuilder-ivr/api/state/menu'+id) 
    .pipe(
      retry(1),
    
    );
   }

   getStateMenuById(id: string) :Observable<StateMenuDetail> 
   {
    return this.http.get<StateMenuDetail>(this.Url+ '/dialogbuilder-ivr/api/state/menu/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveStateMenu(id:string,data:StateMenuDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/state/menu/'+id, data) 
    .pipe(
      retry(1),
    );
   }
   updateStateMenu(appId:string,stateID:string,update:StateMenuDetail)
   {
     return this.http.put(this.Url +'/dialogbuilder-ivr/api/state/menu/',update)
     .pipe(
      retry(1),
   
    );
   }
   deleteStateMenu(id: number)
   {
     return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/state/menu/'+id)
     .pipe(
      retry(1),
    );
   }
}
