import { Injectable } from '@angular/core';
import { Observable,throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import {HttpClient, HttpErrorResponse} from '@angular/common/http';
import {OptionDetail} from '../Models/OptionsModel';
import {DialogConfigServiceService} from '../Configurations/dialog-config-service.service';
import {BranchingOptionDetail} from '../Models/branchingOptionModel';
import {BranchingOptionNoneDetail} from '../Models/branchNoneModel';
import {BranchingOptionHolidayDetail} from '../Models/branchOptionHolidayModel';
@Injectable({
  providedIn: 'root'
})
export class BranchOptionsService {

  Url :string; 
  header : any;  
  errorMessage:string;
  constructor(private http : HttpClient,config:DialogConfigServiceService) {
    this.Url = config.getConfig().apiBaseUrl;
   }
   //................Time of day.......................
   getAllBranchOptionTODS(stateId:string) : Observable<BranchingOptionDetail[]> 
   {
    return this.http.get<BranchingOptionDetail[]>(this.Url+ '/dialogbuilder-ivr/api/branch/todoption/all/'+stateId) 
    .pipe(
      retry(1),
    );
   }
   getBranchOptionById(id: string) :Observable<BranchingOptionDetail> 
   {
    return this.http.get<BranchingOptionDetail>(this.Url+ '/dialogbuilder-ivr/api/branch/todoption/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveBranchOption(stateId:string,data:BranchingOptionDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/branch/todoption/'+stateId, data) 
    .pipe(
      retry(1),
    );
   }
   updateBranchOption(stateId:string,update:BranchingOptionDetail)
   {
     return this.http.put(this.Url +'/dialogbuilder-ivr/api/branch/todoption/'+stateId,update)
     .pipe(
      retry(1),
    );
   }
   deleteBranchOptionTOD(id: number)
   {
     return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/branch/todoption/'+id)
     .pipe(
      retry(1),
    );
   }
  //......................day of week...............


  getAllBranchOptionDOW(id:string) : Observable<OptionDetail[]> 
  {
    return this.http.get<OptionDetail[]>(this.Url+ '/dialogbuilder-ivr/api/branch/dowoption/all/'+id) 
    .pipe(
      retry(1),
    
    );
  }
  getBranchOptionDOWById(id: string) :Observable<BranchingOptionDetail> 
  {
   return this.http.get<BranchingOptionDetail>(this.Url+ '/dialogbuilder-ivr/api/branch/dowoption/'+id)
   .pipe(
     retry(1),
   );
  }
  SaveBranchOptionDOW(stateId:string,data:BranchingOptionDetail)  
  { 
   return this.http.post(this.Url + '/dialogbuilder-ivr/api/branch/dowoption/'+stateId, data) 
   .pipe(
     retry(1),
   );
  }
  updateBranchOptionDOW(stateId:string,update:BranchingOptionDetail)
  {
    return this.http.put(this.Url +'/dialogbuilder-ivr/api/branch/dowoption/'+stateId,update)
    .pipe(
     retry(1),
   );
  }
  deleteBranchOptionDOW(id: number)
  {
    return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/branch/dowoption/'+id)
    .pipe(
     retry(1),
   );
  }
 //.......................None..........................

 
 getAllBranchOptionNone(id:string) : Observable<BranchingOptionNoneDetail[]> 
 {
   return this.http.get<BranchingOptionNoneDetail[]>(this.Url+ '/dialogbuilder-ivr/api/branch/optionlist/'+id) 
   .pipe(
     retry(1),
   
   );
 }
 getBranchOptionNoneById(id: string) :Observable<BranchingOptionNoneDetail> 
 {
  return this.http.get<BranchingOptionNoneDetail>(this.Url+ '/dialogbuilder-ivr/api/branch/option/'+id)
  .pipe(
    retry(1),
  );
 }
 SaveBranchOptionNone(stateId:string,data:BranchingOptionNoneDetail)  
 { 
  return this.http.post(this.Url + '/dialogbuilder-ivr/api/branch/option/'+stateId, data) 
  .pipe(
    retry(1),
  );
 }
 updateBranchOptionNone(stateId:string,update:BranchingOptionNoneDetail)
 {
   return this.http.put(this.Url +'/dialogbuilder-ivr/api/branch/option/'+stateId,update)
   .pipe(
    retry(1),
  );
 }
 deleteBranchOptionNone(id: number)
 {
   return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/branch/option/'+id)
   .pipe(
    retry(1),
  );
 }
 //..............Holiday........................
 
 
 getAllBranchOptionHoliday(id:string) : Observable<BranchingOptionHolidayDetail[]> 
 {
   return this.http.get<BranchingOptionHolidayDetail[]>(this.Url+ '/dialogbuilder-ivr/api/branch/hodoption/all/'+id) 
   .pipe(
     retry(1),
   
   );
 }
 getBranchOptionHolidayById(id: string) :Observable<BranchingOptionHolidayDetail> 
 {
  return this.http.get<BranchingOptionHolidayDetail>(this.Url+ '/dialogbuilder-ivr/api/branch/hodoption/'+id)
  .pipe(
    retry(1),
  );
 }
 SaveBranchOptionHoliday(optionId:string,data:BranchingOptionHolidayDetail)  
 { 
  return this.http.post(this.Url + '/dialogbuilder-ivr/api/branch/hodoption/'+optionId, data) 
  .pipe(
    retry(1),
  );
 }
 updateBranchOptionHoliday(stateId:string,update:BranchingOptionHolidayDetail)
 {
   return this.http.put(this.Url +'/dialogbuilder-ivr/api/branch/hodoption/'+stateId,update)
   .pipe(
    retry(1),
  );
 }
 deleteBranchOptionHoliday(id: number)
 {
   return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/branch/hodoption/'+id)
   .pipe(
    retry(1),
  );
 }
}
