import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {OptionDetail} from '../Shared/Models/OptionsModel';
import {OptionService} from '../Shared/Services/option.service';
import {MatSnackBar} from '@angular/material/snack-bar';
@Component({
  selector: 'app-menu-option',
  templateUrl: './menu-option.component.html',
  styleUrls: ['./menu-option.component.css']
})
export class MenuOptionComponent implements OnInit {
  selectedMode:string="dtmf"
  menuOptionForm: FormGroup;
  menuOptionDetail:OptionDetail
  appId:string;
  stateId:string;
  appName:string;
  type:string;
  optionId:string;
  branchType:string;
  inEditPage: boolean;

id:string;
  MethodTypes: MethodType[] = [
    {value: '1', viewValue: '1'},
    {value: '2', viewValue: '2'},
    {value: '3', viewValue: '3'},
    {value: '4', viewValue: '4'},
    {value: '5', viewValue: '5'},
    {value: '6', viewValue: '6'},
    {value: '7', viewValue: '7'},
    {value: '8', viewValue: '8'},
    {value: '9', viewValue: '9'},
    {value: '0', viewValue: '0'},
    {value: '*', viewValue: '*'},
    {value: '#', viewValue: '#'}
  ];
  constructor(private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,private menuOptionService:OptionService,private _snackBar: MatSnackBar,) { }

  ngOnInit() {
    this.menuOptionDetail={
      id:'',
      dtmf:'',
      option:'',
      stateName:'',
      voice:''
      
    }
    if(this._router.snapshot.url[0].path =='createMenuOption')
    {
      this.inEditPage = false;
      this.appId=this._router.snapshot.params["appId"];
      this.stateId=this._router.snapshot.params["stateId"];
      this.appName=this._router.snapshot.params["appName"];
      this.id=this._router.snapshot.params["id"];
    }
    if(this._router.snapshot.url[0].path =='editMenuOption')
    {
      this.inEditPage = true;
      this.appId=this._router.snapshot.params["appId"];
      this.stateId=this._router.snapshot.params["stateId"];
      this.appName=this._router.snapshot.params["appName"];
      this.id=this._router.snapshot.params["optionId"];
      this.menuOptionService.getStateMenuOptionById(this.id).subscribe(data=>{
console.log("get menu option")
console.log(data)
this.menuOptionDetail=data
this.initForm(data);
      })
    }
    this.initForm(this.menuOptionDetail);
  }
  initForm( menuOptionDetail:OptionDetail) {
   
    this.menuOptionForm = this.formBuilder.group({
      
      option: [menuOptionDetail.option, Validators.required],
      dtmf: [menuOptionDetail.dtmf],
   
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.menuOptionForm.controls; }  
  onSubmit()
  {

   if(this.menuOptionForm.valid){
    
     if(this.inEditPage)
     {
      
       this.UpdateMenuOption(new OptionDetail(this.menuOptionForm.value));       
     }
     else
     {
       this.SaveMenuOption(new OptionDetail(this.menuOptionForm.value));       
     }
   }
   }
   SaveMenuOption(Option:OptionDetail)
   {
     this.menuOptionService.SaveMenuOption(this.stateId,Option).subscribe(data=>{
      this._snackBar.open("Menu option saved successfully!", "", {
        duration: 2000,
        verticalPosition: "bottom",
        horizontalPosition:'right',
        panelClass: ['success'],
    });
    this.router.navigate(['dialogbuilder/nextState',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:"menu"}])
     })
   }
   UpdateMenuOption(option:OptionDetail)
   {
     option.id=this.menuOptionDetail.id+""
      this.menuOptionService.updateMenuOption(option.id,option).subscribe(data=>{
        this._snackBar.open("Menu option updateed successfully!", "", {
          duration: 2000,
          verticalPosition: "bottom",
          horizontalPosition:'right',
          panelClass: ['success'],
      });
      this.router.navigate(['dialogbuilder/nextState',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:"menu"}])
      })
   }
   backtoOption()
  {
  
        this.router.navigate(['dialogbuilder/nextState',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:"menu",branchType:this.branchType}])

  
  }
}
interface MethodType {
  value: string;
  viewValue: string;
}
