  stateName:string;
  selectedState:string
  inEditPage:boolean
  nextStateTypes: nextStateType[] = [
    {value: 'prompt', viewValue: 'Prompt'},
    {value: 'menu', viewValue: 'Menu'},
    {value: 'input', viewValue: 'Input'},
    {value: 'nonBackend', viewValue: 'Backend'},
    {value: 'branching', viewValue: 'Branching'},
    {value: 'transfer', viewValue: 'Transfer'},
    {value: 'hoursofoperation', viewValue: 'Hours of Operation'},
    {value: 'holiday', viewValue: 'Holiday'}
  ];
  holidayNextForm: FormGroup;
  constructor(private statePromptService:StatePromptService ,public dialogRef: MatDialogRef<CreateNextHolidayComponent>,
    
    @Optional() @Inject(MAT_DIALOG_DATA) public data: UsersData,private formBuilder: FormBuilder,) { 

      console.log("popup data")
      console.log(data["dataObj"].action);
      this.local_data = data;
      console.log("get update data")
      console.log( this.local_data );
      this.action = data["dataObj"].action;
      console.log("action find")
      console.log(this.action)
      this.type=data["type"]
this.appId=data["appId"]
console.log("appId")
console.log(this.appId)
this.stateName=data["stateName"]
console.log("stateName")
console.log(this.stateName)
    }

  ngOnInit(): void {

    this.holidayNextStateDetail={
      id :'',
      option:'',
      nextState:'',
      nextStateId:'',
      nextStateType:'',
      nextStateName:''
    }
    if(this.action=="Update")
    {
      this.inEditPage=true
      this.holidayNextStateDetail=this.local_data["dataObj"]
      console.log("grid row get")
      console.log(this.holidayNextStateDetail)
      this.isSelectedFirst=true
    this.selectedType=  this.holidayNextStateDetail.nextStateType;
    this.selectedState=this.holidayNextStateDetail.nextStateName+"";
    
    console.log("selectet update value")
    console.log(this.selectedState)
   
    }
    this.initForm(this.holidayNextStateDetail)
  }
  initForm(Holiday:HolidayNextStateDetail) {
   
    this.holidayNextForm = this.formBuilder.group({
      option: [Holiday.option, Validators.required],
      nextStateType: [Holiday.nextStateType, ],
      nextState: [Holiday.nextState, ],
    });
   
  }

  get f() { return this.holidayNextForm.controls; }  
  // saveHoliday()
  // {
  //   this.saveHolidayNext(new HolidayNextStateDetail(this.holidayNextForm.value))
  // }
  updateHoliday()
  {
    console.log("update form data ")
    console.log(this.holidayNextForm.value)
    this.updateHolidayNext(new HolidayNextStateDetail(this.holidayNextForm.value))
  }
  saveHolidayNext(HolidayNextState:HolidayNextStateDetail)
  {
    console.log("save form ")
    console.log(this.holidayNextForm.value)
    console.log("save hour data")
    console.log(HolidayNextState)
    HolidayNextState.nextState=HolidayNextState.nextState+"";
    
    HolidayNextState.nextState=this.stateTypes.find(x=>x.stateName==HolidayNextState.nextState).id
    console.log("next state id")
    console.log(HolidayNextState.nextState)
    HolidayNextState.nextState=HolidayNextState.nextState+"";
    HolidayNextState.nextStateName=this.selectedState+"";
    
    console.log(HolidayNextState)
    this.dialogRef.close({event:this.action,data:HolidayNextState});
  }
  updateHolidayNext(HolidayNextState:HolidayNextStateDetail)
  {
    console.log("save form ")
    console.log(this.holidayNextForm.value)
    console.log("save hour data")
    console.log(HolidayNextState)
  console.log(this.stateTypes)
    if(HolidayNextState.nextState != undefined && HolidayNextState.nextState != "")
    {
      HolidayNextState.nextStateName=HolidayNextState.nextState+"";
      HolidayNextState.nextState=this.stateTypes.find(x=>x.stateName==HolidayNextState.nextStateName).id;
    }
    else if(HolidayNextState.nextStateType != undefined && HolidayNextState.nextStateType != "")
    {
      HolidayNextState.nextStateType=this.selectedType+""
    }
  else{
    HolidayNextState.nextState=HolidayNextState.nextState+"";
    HolidayNextState.nextStateName=this.selectedState+"";
  }
  
  HolidayNextState.id=HolidayNextState.id+""

  
    console.log(HolidayNextState)
    this.dialogRef.close({event:this.action,data:HolidayNextState});
  }
