import { Injectable } from '@angular/core';
import {HttpClient, HttpRequest, HttpEvent} from '@angular/common/http';
import {Observable} from 'rxjs';
import {DialogConfigServiceService} from '../Configurations/dialog-config-service.service';
@Injectable({
  providedIn: 'root'
})
export class UploadService {
  Url :string; 
  constructor(private http: HttpClient,config:DialogConfigServiceService) {
    this.Url = config.getConfig().apiBaseUrl;
  }
 
  pushFileToStorage(file: File,appName:string): Observable<HttpEvent<{}>> {
    let formdata: FormData = new FormData();
 
    formdata.append('file', file);
 
    const req = new HttpRequest('POST', this.Url+'/dialogbuilder-ivr/api/upload/'+appName+'/', formdata, {
      reportProgress: true,
      responseType: 'text'
    });
 
    return this.http.request(req);
  }
  getUploadAudio(appName:string) :Observable<string[]> 
  {
   return this.http.get<string[]>(this.Url+ '/dialogbuilder-ivr/api/upload/'+appName)
  
  }
  getFiles(): Observable<any> {
    return this.http.get('/getallfiles')
  }
}