import { Injectable } from '@angular/core';
import { Observable,throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import {HttpClient, HttpErrorResponse} from '@angular/common/http'; 
import{DIDGroupDetails} from '../Models/DIDgroupModel';
import {AppDetails} from '../Models/appsModel'
import {DialogConfigServiceService} from '../Configurations/dialog-config-service.service';
import {DIDNumberDetails} from '../Models/didNumberModel';

@Injectable({
  providedIn: 'root'
})
export class DIDgroupService {
  Url :string; 
  header : any;  
  errorMessage:string;
  constructor(private http : HttpClient,config:DialogConfigServiceService) {
    this.Url = config.getConfig().apiBaseUrl;
   }
   

  getDIDgroupDetail() : Observable<DIDGroupDetails[]> 
  {
   return this.http.get<DIDGroupDetails[]>(this.Url+ '/dialogbuilder-ivr/api/didGroup') 
   .pipe(
     retry(1),
   
   );
  }
 

  getAllAppName() : Observable<AppDetails[]> 
  {
   return this.http.get<AppDetails[]>(this.Url+ '/dialogbuilder-ivr/api/app') 
   .pipe(
     retry(1),
   );
  }
  getDIDgroupDetailById(id: string) :Observable<DIDGroupDetails> 
  {
   return this.http.get<DIDGroupDetails>(this.Url+ '/dialogbuilder-ivr/api/didGroup/'+id)
   .pipe(
     retry(1),
   );
  }
  getDIDNumberDetailById(id: string) :Observable<DIDNumberDetails> 
  {
   return this.http.get<DIDNumberDetails>(this.Url+ '/dialogbuilder-ivr/api/didNumber/'+id)
   .pipe(
     retry(1),
   );
  }
  SaveDIDgroup(data:DIDGroupDetails)  
  { 
   return this.http.post(this.Url + '/dialogbuilder-ivr/api/didGroup', data) 
   .pipe(
     retry(1),
   );
  }
  updateDIDgroupDetail(update:DIDGroupDetails)
  {
    return this.http.put(this.Url +'/dialogbuilder-ivr/api/didGroup',update)
    .pipe(
     retry(1),
  
   );
  }
  SaveDIDNumber(data:DIDNumberDetails)  
  { 
   return this.http.post(this.Url + '/dialogbuilder-ivr/api/didNumber', data) 
   .pipe(
     retry(1),
   );
  }
  updateDIDNumber(update:DIDNumberDetails)
  {
    return this.http.put(this.Url +'/dialogbuilder-ivr/api/didNumber',update)
    .pipe(
     retry(1),
  
   );
  }
  deleteDIDgroupDetail(id: number)
  {
    return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/didGroup/'+id)
    .pipe(
     retry(1),
   );
  }
  deleteDIDNumber(id: number)
  {
    return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/didNumber/'+id)
    .pipe(
     retry(1),
   
   );
  }
}
