import { Component ,ViewChild,OnInit} from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import { ActivatedRoute,Router } from '@angular/router';
import {GroupPromptService} from '../Shared/Services/group-prompt.service';
import {PromptGroupDetail} from '../Shared/Models/promptsGroupModel';
import {DeleteConfirmPopupComponent} from 'src/app/delete-confirm-popup/delete-confirm-popup.component';
import {MatSnackBar} from '@angular/material/snack-bar';
import {MatDialog,MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
export interface PeriodicElement {
  PromptName: string;
  Interpretation: string;
  AlternateText:string;
 }

 const ELEMENT_DATA: PeriodicElement[] = [
   { PromptName: 'prompt1',Interpretation:'ptl1',AlternateText:'text'},
   { PromptName: 'prompt2',Interpretation:'ptl1',AlternateText:'text'},
   { PromptName: 'prompt3',Interpretation:'ptl1',AlternateText:'text'},
 ];
@Component({
  selector: 'app-prompt-details',
  templateUrl: './prompt-details.component.html',
  styleUrls: ['./prompt-details.component.css']
})
export class PromptDetailsComponent implements OnInit {
  isConfirm:boolean;
  displayedColumns: string[] = ['PromptName','Interpretation','AlternateText','type','Actions'];
  inEditPage: boolean;
  dataSource = new MatTableDataSource(); 
  isRecordFound = false;
  promptId:string;
  type:string;
  appId:string;
  Id:string
   @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;

  constructor(public dialog: MatDialog,private _router: ActivatedRoute,private _snackBar: MatSnackBar,private router: Router,private promptGroupService:GroupPromptService) {
   }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  ngOnInit(){
    this.dataSource.paginator = this.paginator;
    
    if(this._router.snapshot.url[0].path =='promptdetails')
    {
      this.inEditPage = true;
      this.promptId=this._router.snapshot.params["PromptId"];
      this.appId=this._router.snapshot.params["appId"];
      this.type=this._router.snapshot.params["type"];
     console.log(this.appId)
      this.promptTable();
    }
  }
promptTable()
{
  this.promptGroupService.getPromp(this.promptId).subscribe(data=>{
    console.log("data prompt")
  
   
    if(data["commonPrompt"].length >0)
    {
      console.log(data["commonPrompt"])
      this.dataSource.data=data["commonPrompt"]
    }
   
    else
    this.isRecordFound = true;
  })
  this.dataSource.paginator = this.paginator;
}
GetPromptByID(id){
this.Id=id
console.log("id", this.Id)
  if(this.type.toLowerCase()=="global")
  this.router.navigate(['dialogbuilder/editprompt',{PromptId:this.promptId,prompt:this.Id,type:this.type}])
  else
  this.router.navigate(['dialogbuilder/editprompt',{PromptId:this.promptId,prompt:this.Id,appId:this.appId}])
}
createPrompts()
{
  if(this.type.toLowerCase()=="global")
  this.router.navigate(['dialogbuilder/createprompt',{PromptId:this.promptId,appId:"",type:this.type}])
  else
  this.router.navigate(['dialogbuilder/createprompt',{PromptId:this.promptId,appId:this.appId}])
}
deletePopUp(promptId){
  const dialogRef = this.dialog.open(DeleteConfirmPopupComponent, {
    width: '450px'
  });

  dialogRef.afterClosed().subscribe(result => {
 
    this.isConfirm=result;
     if(this.isConfirm)
    this.deletePrompt(promptId)
  });
}
deletePrompt(promptId){
  this.promptGroupService.deletePrompt(promptId).subscribe(data=>{
  console.log("delete response");
  console.log(data);
  this.promptTable()
  this._snackBar.open("Prompt group deleted!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
  });
  })
    }
}
