import { Component, OnInit,ViewChild ,ElementRef} from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {TransferService} from '../Shared/Services/transfer.service';
import {TransferDetails} from '../Shared/Models/transferModel';
import {MatSnackBar} from '@angular/material/snack-bar';
import {AppsService} from '../Shared/Services/apps.service';
import {AppDetails} from '../Shared/Models/appsModel';
import { HttpClient, HttpResponse, HttpEventType } from '@angular/common/http';
import {StatePromptDetail} from '../Shared/Models/statePromptModel';
import {StatePromptService} from '../Shared/Services/state-prompt.service';
import {UploadService} from '../Shared/Services/upload.service';
import { MatRadioChange } from '@angular/material/radio';
@Component({
  selector: 'app-transfer',
  templateUrl: './transfer.component.html',
  styleUrls: ['./transfer.component.css']
})
export class TransferComponent implements OnInit {
  showPromptName:boolean
  isRadioPromptChange:boolean
   isradioChange:boolean
 
   audioName:string;
   Url :string; 
   promptUrl:string;
 audio = new Audio();
 
 Playstate:boolean=true;
   
 audioURl:any;
 AudioCollections: Type[] = []
 showPrompts:boolean=false
 selectedAudio:string;
   selectedFiles: FileList
   currentFileUpload: File
   showprogress:boolean=false
   editAudio:boolean=false;
   showAudioOptions:boolean=false;
   progress: { percentage: number } = { percentage: 0 }
   @ViewChild("selectFile") selectFile;
   @ViewChild('audioOption') audioPlayerRef: ElementRef;
   branchValue:string
   showOptions:boolean
  stateId:string;
  appId:string;
  appName:string;
  transferDetail:TransferDetails
  transferForm: FormGroup;
  inEditPage: boolean;
  editBtn:boolean;
  MethodTypes: MethodType[] = [
    {id: 'blind', viewValue: 'Blind'},

  ];
  selectedType:string="blind"
  constructor(private uploadService: UploadService,private appService:AppsService,private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,private _snackBar: MatSnackBar,private transferService:TransferService) { }

  ngOnInit() {
    this.transferDetail={
      id:'',
      type:'',
      stateName:'',
      destination:''
    }
    if(this._router.snapshot.url[0].path =='createTransfer')
    {
      this.isRadioPromptChange=true
      this.showOptions=false
      this.appId=this._router.snapshot.params["appId"];
      this.appName=this._router.snapshot.params["appName"];
      var d = new Date();
      let transferName="Transfer_"+d.getTime()+"";
      this.transferDetail.stateName=transferName+"";
      this.initForm(this.transferDetail)
    }
    if(this._router.snapshot.url[0].path =='editTransfer')
    {
      this.appId=this._router.snapshot.params["appId"];
      this.appName=this._router.snapshot.params["appName"];
      this.stateId=this._router.snapshot.params["stateId"];
      this.editBtn=true
      this.isRadioPromptChange=false
      this.showOptions=true
      this.inEditPage = true;
      this.showPromptName=true
      this.showAudioOptions=true
      this.transferService.getTransferById(this.stateId).subscribe(data=>{
        console.log("data tran")
        console.log(data)
        this.transferDetail=data
        console.log("destination")
        console.log( this.transferDetail.destination)
       
        this.initForm(this.transferDetail)
      })
    }
    this.initForm(this.transferDetail);
  }
  initForm(transfer:TransferDetails) {
   
    this.transferForm = this.formBuilder.group({
      
      stateName: [transfer.stateName, Validators.required],
      type: [transfer.type, Validators.required],
      destination: [transfer.destination, Validators.required],
    
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.transferForm.controls; }  
  
   saveTransfer()
   {
    if(this.transferForm.valid)
    {
     
      this.SaveTransfer(new TransferDetails(this.transferForm.value));       
    }
   }
   updateTransfer(){
    if(this.transferForm.valid){
    
        this.UpdateTransfer(new TransferDetails(this.transferForm.value));       
      
    }
   }
   SaveTransfer(transfer:TransferDetails)
   {
     console.log("save ")
     console.log(transfer)
    this.transferService.SaveTransfer(this.appId,transfer).subscribe(data=>{
      this._snackBar.open("Transfer saved successfully!", "", {
        duration: 2000,
        verticalPosition: "bottom",
        horizontalPosition:'right',
        panelClass: ['success'],
    });
    this.router.navigate(['dialogbuilder/stateDetail',{appName:this.appName,appId:this.appId}]);
    
    })
   }
   UpdateTransfer(transfer:TransferDetails)
   {
     console.log("update transfer")
     console.log(transfer)
     console.log("state id")
     console.log(this.stateId)
    this.transferService.updateTransfer(this.stateId,transfer).subscribe(data=>{
    this._snackBar.open("Transfer updated successfully!", "", {
      duration: 2000,
      verticalPosition: "bottom",
      horizontalPosition:'right',
      panelClass: ['success'],
  });
  this.router.navigate(['dialogbuilder/stateDetail',{appName:this.appName,appId:this.appId}]);
});
   }
   backStateDetail()
   {
    this.router.navigate(['dialogbuilder/stateDetail',{appName:this.appName,appId:this.appId}]);
   }
   radioChange($event: MatRadioChange)
   {
     console.log( $event.value);
   
     
     if ( $event.value === 'Exists') 
     {
       this.isRadioPromptChange=false
       this.isradioChange=false
       this.showPrompts=true
       this.showOptions=true
       this.showPromptName=false
       this.showAudioOptions=true
    
       // this.showOptions=true
       this.uploadService.getUploadAudio(this.appName).subscribe(data=>{
         console.log("audio files")
         console.log(data)
 
         for(var k in data) {
 
           this.AudioCollections.push({'value':data[k],'viewValue':k})
         }
     
      
       })
     }
     else if($event.value === 'New')
     {
       this.isRadioPromptChange=true
        this.showAudioOptions=false
       this.isradioChange=true
       this.showPrompts=false
       this.showOptions=false
  
     }

   }
   onAudioSelection(){
     this.audio.src=""
     console.log("selected audio name")
     console.log(this.selectedAudio)
     
    // this.statePrompt.prompt=this.audio+""
     this.promptUrl=this.selectedAudio;
     this.audio.src = this.selectedAudio
     this.audio.load();
   
   }
  selectFile2(event) {
    this.selectedFiles = event.target.files;
    this.upload() ;
  }
  upload() {
    this.progress.percentage = 0;
    this.showprogress=true
    this.currentFileUpload = this.selectedFiles.item(0)
    this.uploadService.pushFileToStorage(this.currentFileUpload,this.appName).subscribe(event => {
      console.log("response")
      console.log(event["body"])
      this.audioURl=event["body"]
    this.audio.src = this.audioURl;
    this.promptUrl=this.audioURl;
    this.audio.load();
    this.showprogress=false
      console.log("get audio")
  
      console.log(this.audioURl)
     
      if (event.type === HttpEventType.UploadProgress) {
        this.progress.percentage = Math.round(100 * event.loaded / event.total);
      } else if (event instanceof HttpResponse) {
        console.log('File is completely uploaded!');
  
      //   this.audioURl=event["body"]
      //  let audio = new Audio();
      //  audio.src =this.audioURl
      //  audio.load();
      //  audio.play();
      }
    })
 
    this.selectedFiles = undefined
  }
  play()
  {
    this.Playstate=false
    this.audio.play();
    this.audio.onended=()=>{
      this.Playstate=true
  
    }
  }
  pause()
  {
    this.Playstate=true;
  this.audio.pause();

  }
  audioEdit(){
    this.showPromptName=false;
    this.editBtn=false
    this.isRadioPromptChange=true;
    this.showOptions=false
    this.editAudio=true
    this.showPrompts=false;
  }
  audioCancel()
  {
    this.editBtn=true
    this.showPromptName=true;
    this.isRadioPromptChange=false;
    this.showOptions=true
    this.editAudio=false
  }
}
interface MethodType {
  id: string;
  viewValue: string;
}
interface Type {
  value: string;
  viewValue: string;
}