import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
@Component({
  selector: 'app-connections',
  templateUrl: './connections.component.html',
  styleUrls: ['./connections.component.css']
})
export class ConnectionsComponent implements OnInit {
  formGroup: FormGroup;
  inEditPage: boolean;
  constructor(private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute) { }

  ngOnInit() {
    this.createForm();
    if(this._router.snapshot.url[0].path =='connectionEdit')
    {
      this.inEditPage = true;
    }
  }
  createForm() {
   
    this.formGroup = this.formBuilder.group({
      
      EnvironmentName: [null, Validators.required],
      URL: [null, Validators.required],
      UserName: [null, Validators.required],
      Password: [null, Validators.required],
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.formGroup.controls; }  
}
