import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {BranchingOptionDetail} from '../Shared/Models/branchingOptionModel';
import {MatSnackBar} from '@angular/material/snack-bar';
import {BranchOptionsService} from '../Shared/Services/branch-options.service';

@Component({
  selector: 'app-branch-time',
  templateUrl: './branch-time.component.html',
  styleUrls: ['./branch-time.component.css']
})
export class BranchTimeComponent implements OnInit {
  branchingOptionDetail:BranchingOptionDetail
  appId:string;
  stateId:string;
  appName:string;
  type:string;
  optionId:string;
  branchType:string;
  branchOptionDayForm: FormGroup;
  inEditPage: boolean;
  MethodTypes: MethodType[] = [
    {value: '1', viewValue: 'GET'},
    {value: '2', viewValue: 'POST'},
    {value: '3', viewValue: 'PUT'},
    {value: '4', viewValue: 'DELETE'}
  ];
  constructor( private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute, private _snackBar: MatSnackBar,private branchOptionsService:BranchOptionsService,) { }

  ngOnInit() {
    this.branchingOptionDetail={
      id:'',
      option:'',
      from:'',
      to:'',
      type:'',
      day:''
    }
    if(this._router.snapshot.url[0].path =='editBranchOptionTime')
    {
      this.inEditPage = true;
      this.appId=this._router.snapshot.params["appId"];
      this.stateId=this._router.snapshot.params["stateId"];
      this.appName=this._router.snapshot.params["appName"];
      this.type=this._router.snapshot.params["type"];
      this.optionId=this._router.snapshot.params["optionId"];
      this.type=this._router.snapshot.params["type"];
      this.branchType=this._router.snapshot.params["branchType"];
      this.branchOptionsService.getBranchOptionById(this.optionId).subscribe(data=>{
        console.log("by id")
        console.log(data)
        this.branchingOptionDetail=data
        this.initForm(data)
      })
    }
    if(this._router.snapshot.url[0].path =='createBranchOptionTime')
    {
      this.appId=this._router.snapshot.params["appId"];
      this.stateId=this._router.snapshot.params["stateId"];
      this.appName=this._router.snapshot.params["appName"];
      this.type=this._router.snapshot.params["type"];
      this.branchType=this._router.snapshot.params["branchType"];
      console.log("crea barnch type")
      console.log(this.branchType)
    console.log(this.stateId)
    }
    this.initForm(this.branchingOptionDetail);
  }
  initForm(branchOption:BranchingOptionDetail) {
   
    this.branchOptionDayForm = this.formBuilder.group({
      
      option: [branchOption.option, Validators.required],
      from: [branchOption.from, Validators.required],
      to: [branchOption.to, Validators.required],
     
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.branchOptionDayForm.controls; }  
  onSubmit()
  {

   if(this.branchOptionDayForm.valid){
    
     if(this.inEditPage)
     {
      
       this.UpdateBranchOption(new BranchingOptionDetail(this.branchOptionDayForm.value));       
     }
     else
     {
       this.SaveBranchOption(new BranchingOptionDetail(this.branchOptionDayForm.value));       
     }
   }
   
   }
   SaveBranchOption(branchOption:BranchingOptionDetail)
   {
    console.log("from date")
     branchOption.from=branchOption.from+"";
     branchOption.to=branchOption.to+"";
     console.log(branchOption)
this.branchOptionsService.SaveBranchOption(this.stateId,branchOption).subscribe(data=>{
  this._snackBar.open("Option saved successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
this.router.navigate(['dialogbuilder/stateDetail',{appName:this.appName,appId:this.appId}]);

})
   }
   UpdateBranchOption(branchOption:BranchingOptionDetail)
   {
     branchOption.id= this.optionId+"";
   console.log(branchOption)
this.branchOptionsService.updateBranchOption(this.stateId,branchOption).subscribe(data=>
  {
    this._snackBar.open("Option updated successfully!", "", {
      duration: 2000,
      verticalPosition: "bottom",
      horizontalPosition:'right',
      panelClass: ['success'],
  });
  this.router.navigate(['dialogbuilder/stateDetail',{appName:this.appName,appId:this.appId}]);
  
  })
   }
   
  backtoOption()
  {
    if(this.type.toLowerCase()== "branching")
    {
     console.log("branch type")
     console.log(this.branchType)
        this.router.navigate(['dialogbuilder/nextState',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:"branching",branchType:this.branchType}])

    }
  }
}
interface MethodType {
  value: string;
  viewValue: string;
}
