import { Component, OnInit ,Inject,ViewChild} from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {MatSnackBar} from '@angular/material/snack-bar';
import {AppsService} from '../Shared/Services/apps.service';
import { HttpClient, HttpResponse, HttpEventType } from '@angular/common/http';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import{RetryPromptDetail} from '../Shared/Models/reTryPromptModel';
import {GroupPromptService} from '../Shared/Services/group-prompt.service';
import {StatePromptDetail} from '../Shared/Models/statePromptModel';
import {StatePromptService} from '../Shared/Services/state-prompt.service';
import {UploadService} from '../Shared/Services/upload.service';
import { MatRadioChange } from '@angular/material/radio';

@Component({
  selector: 'app-edit-announcement-popup',
  templateUrl: './edit-announcement-popup.component.html',
  styleUrls: ['./edit-announcement-popup.component.css']
})
export class EditAnnouncementPopupComponent implements OnInit {
  statePromptDetail:StatePromptDetail
  audioName:string;
  isRadioPromptChange:boolean
  editBtn:boolean
  isradioChange:boolean
  showPromptName:boolean;
  showOptions:boolean
  audioURl:any;
AudioCollections: AType[] = []
showPrompts:boolean=false
  audio = new Audio();
Playstate:boolean=true;
  selectedAudio:string;
  selectedFiles: FileList
  currentFileUpload: File
  showprogress:boolean=false
  editAudio:boolean=false;
  showAudioOptions:boolean=false;
  stateId:string;
  progress: { percentage: number } = { percentage: 0 }
  @ViewChild("selectFile") selectFile;

 
  
  Types: Type[] = [{id:'NoMatch',retryType:'No Match'},
  {id:'NoInput',retryType:'No Input'}];
  Prompts:Prompt[]=[]
  AnnouncePromptForm: FormGroup;
  inEditPage: boolean;
  id:string;
  appId:string;
  appNameRetry:string;
  appVariableType
  retryId;
  promptUrl:string;
  match:string;
  retryname:string;
  promptName:string;
  bargin:string;
  language:string;
  appName:string;
  constructor(private uploadService: UploadService,private statePromptService:StatePromptService,private promptGroupService:GroupPromptService,private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,private _snackBar: MatSnackBar,private appService:AppsService,
    public dialogRef: MatDialogRef<EditAnnouncementPopupComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any) {
      
    }

  ngOnInit(): void {
    this.statePromptDetail={
      stateName:'',
      id:'',
      prompt:'',
      promptname:'',
      language:'',
      bargin:'',
      promptStatus:'',
      promptType:'',
      nextState:'',
      nextStateName:'',
      nextStateType:''
    
     }
    this.showPromptName=true
    this.inEditPage=true
    this.editBtn=true
    console.log("data")
    console.log(this.data)
    console.log(this.data["appId"])
    this.appId=this.data["appId"];
    this.appName=this.data["appName"];
console.log(this.appId)
this.statePromptDetail=this.data["obj"]
console.log("state prompt detail")
console.log(this.statePromptDetail)
this.promptName=this.statePromptDetail.promptname;
this.bargin=this.statePromptDetail.bargin;
this.language=this.statePromptDetail.language;
this.promptUrl=this.statePromptDetail.prompt;
this.audio.src=this.promptUrl;
this.audio.load();
console.log(this.data)


this.initForm( this.statePromptDetail);
  }
  initForm(statePromptDetail:StatePromptDetail) 
  {
     
    this.AnnouncePromptForm = this.formBuilder.group({
      
      stateName:[statePromptDetail.stateName],
      prompt: [statePromptDetail.prompt,],
      choosePrompt:[statePromptDetail.prompt,],
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.AnnouncePromptForm.controls; }  

  saveAnnouncePrompt()
  {
    if(this.AnnouncePromptForm.valid){
      this.UpdateAnnouncePrompt();
    }
   
  }
  UpdateAnnouncePrompt()
  {
 
    this.statePromptDetail.prompt=this.promptUrl+""
    this.statePromptDetail.bargin=this.bargin+"";
    this.statePromptDetail.language=this.language+"";
    this.statePromptDetail.promptStatus= this.statePromptDetail.promptStatus+"";
    this.stateId=this.statePromptDetail.id+"";
    this.statePromptDetail.promptname="";
   console.log("update annoucement data")
   console.log(this.appId)
   console.log(this.stateId)
   console.log(this.statePromptDetail)
   this.statePromptService.updateStatePrompt(this.appId,this.stateId,this.statePromptDetail).subscribe(data=>{
console.log("announce data")
console.log(data)
this.dialogRef.close({event:"updated"})
this._snackBar.open("Updated successfully!", "", {
  duration: 2000,
  verticalPosition: "bottom",
  horizontalPosition:'right',
  panelClass: ['success'],
});
  })
  }



  radioChange($event: MatRadioChange)
    {
      console.log( $event.value);
      const promptEvents =this.AnnouncePromptForm.get('prompt')
     const choosePromptEvents =this.AnnouncePromptForm.get('choosePrompt')
      
      if ( $event.value === 'Exists') 
      {
        this.isRadioPromptChange=false
        this.isradioChange=false
        this.showPrompts=true
        this.showOptions=true
        this.showPromptName=false
        this.showAudioOptions=true
       promptEvents.setValidators(Validators.required);
     choosePromptEvents.clearValidators();
        // this.showOptions=true
        this.uploadService.getUploadAudio(this.appName).subscribe(data=>{
          console.log("audio files")
          console.log(data)
  
          for(var k in data) {
  
            this.AudioCollections.push({'value':data[k],'viewValue':k})
          }
      
       
        })
      }
      else if($event.value === 'New')
      {
        this.isRadioPromptChange=true
         this.showAudioOptions=false
        this.isradioChange=true
        this.showPrompts=false
        this.showOptions=false
      promptEvents.clearValidators();
      choosePromptEvents.setValidators(Validators.required);
      }
     promptEvents.updateValueAndValidity();
     choosePromptEvents.updateValueAndValidity();
    }
    onAudioSelection(){
      this.audio.src=""
      console.log("selected audio name")
      console.log(this.selectedAudio)
      this.promptUrl=this.selectedAudio;
      this.audio.src = this.selectedAudio;
      this.audio.load();
  
    }
    selectFile2(event) {
      this.selectedFiles = event.target.files;
      this.upload() ;
    }
    upload() {
      this.progress.percentage = 0;
      this.showprogress=true
      this.currentFileUpload = this.selectedFiles.item(0)
      this.uploadService.pushFileToStorage(this.currentFileUpload,this.appName).subscribe(event => {
        console.log("response")
        console.log(event["body"])
        this.audioURl=event["body"]
      this.audio.src = this.audioURl;
      this.audio.load();
      this.showprogress=false
      this.promptUrl=this.audioURl;
        console.log("get audio")
        console.log(this.audioURl)
       
        if (event.type === HttpEventType.UploadProgress) {
          this.progress.percentage = Math.round(100 * event.loaded / event.total);
        } else if (event instanceof HttpResponse) {
          console.log('File is completely uploaded!');
    
        //   this.audioURl=event["body"]
        //  let audio = new Audio();
        //  audio.src =this.audioURl
        //  audio.load();
        //  audio.play();
        }
      })
   
      this.selectedFiles = undefined
    }
    play()
    {
      this.Playstate=false
      this.audio.play();
      this.audio.onended=()=>{
        this.Playstate=true
    
      }
    }
    pause()
    {
      this.Playstate=true
    this.audio.pause()
    }
    audioEdit(){
      this.showPromptName=false;
      this.editBtn=false
      this.isRadioPromptChange=true;
      this.showOptions=false
      this.editAudio=true
      this.showPrompts=false;
    }
    audioCancel()
    {
      this.editBtn=true
      this.showPromptName=true;
      this.isRadioPromptChange=false;
      this.showOptions=true
      this.editAudio=false
    }
    onNoClick()
    {
      this.dialogRef.close({event:"closed"});
    }
}
interface Type {
  id:string;
  retryType: string;
}
interface AType {
  value: string;
  viewValue: string;
}
interface Prompt{
  id:string;
  promptGroupName: string;
}