import { Injectable } from '@angular/core';
import { Observable,throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import {HttpClient, HttpErrorResponse} from '@angular/common/http'; 
import {AppDetails} from '../Models/appsModel';
import {DialogConfigServiceService} from '../Configurations/dialog-config-service.service';
//import {AppsVariable} from '../models/appVariableModel';
import {VariableApps} from '../Models/variablesAppsModel';
import{Apps} from '../Models/appModel';
import{RetryPromptDetail} from '../Models/reTryPromptModel';
import {HolidayDetail} from '../Models/holidayModel';
import {GlobalNextStateDetail} from '../Models/globalerrorModel';
import {MenuNextStateDetail} from '../Models/menuNextoptionModel';
@Injectable({
  providedIn: 'root'
})
export class AppsService {
  Url :string; 
  header : any;  
  errorMessage:string;
  constructor(private http : HttpClient,config:DialogConfigServiceService) {
    this.Url = config.getConfig().apiBaseUrl;
   }
   getApps() : Observable<Apps[]> 
   {
    return this.http.get<Apps[]>(this.Url+ '/dialogbuilder-ivr/api/app') 
    .pipe(
      retry(1),
    );
   }
   getAppsVariable(id:string) : Observable<VariableApps[]> 
   {
    return this.http.get<VariableApps[]>(this.Url+ '/dialogbuilder-ivr/api/app/variables/'+id) 
    .pipe(
      retry(1),
    );
   }
   getAppsKey(id:string) : Observable<VariableApps[]> 
   {
    return this.http.get<VariableApps[]>(this.Url+ '/dialogbuilder-ivr/api/app/variables/'+id) 
    .pipe(
      retry(1),
    );
   }
   getAppsById(id: string) :Observable<AppDetails> 
   {
    return this.http.get<AppDetails>(this.Url+ '/dialogbuilder-ivr/api/app/'+id)
    .pipe(
      retry(1),
    );
   }
   getAppsVariableById(id: string) :Observable<VariableApps> 
   {
    return this.http.get<VariableApps>(this.Url+ '/dialogbuilder-ivr/api/app/appvariable/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveApps(data:AppDetails)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/app', data) 
    .pipe(
      retry(1),
    );
   }
   updateApps(update:AppDetails)
   {
     return this.http.put(this.Url +'/dialogbuilder-ivr/api/app',update)
     .pipe(
      retry(1),
   
    );
   }
   SaveAppsVariable(id: string,data:VariableApps)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/app/appvariable/'+id, data) 
    .pipe(
      retry(1),
    );
   }
   updateAppsVariable(update:VariableApps)
   {
     return this.http.put(this.Url +'/dialogbuilder-ivr/api/app/appvariable',update)
     .pipe(
      retry(1),
   
    );
   }
   deleteApps(id: number)
   {
     return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/app/'+id)
     .pipe(
      retry(1),
    );
   }
   deleteAppsVariable(id: number)
   {
     return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/app/appvariable/'+id)
     .pipe(
      retry(1),
    
    );
   }
   //Retry prompt.............................
   getRetryPrompt(id:string,type:string) : Observable<RetryPromptDetail[]> 
   {
    return this.http.get<RetryPromptDetail[]>(this.Url+ '/dialogbuilder-ivr/api/retryprompt/'+type+'/'+id) 
    .pipe(
      retry(1),
    );
   }
   getRetryPromptById(id: string) :Observable<RetryPromptDetail> 
   {
    return this.http.get<RetryPromptDetail>(this.Url+ '/dialogbuilder-ivr/api/retryprompt/'+id)
    .pipe(
      retry(1),
    );
   }
   getAllHolday(appId) : Observable<HolidayDetail[]> 
   {
    return this.http.get<HolidayDetail[]>(this.Url+ '/dialogbuilder-ivr/api/app/holidays/'+appId) 
    .pipe(
      retry(1),
    );
   }
   getHolidayById(id: string) :Observable<HolidayDetail> 
   {
    return this.http.get<HolidayDetail>(this.Url+ '/dialogbuilder-ivr/api/app/holiday/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveRetryPrompt(id: string,data:RetryPromptDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/retryprompt/'+id, data) 
    .pipe(
      retry(1),
    );
   }
   updateRetryPrompt(update:RetryPromptDetail)
   {
     return this.http.put(this.Url +'/dialogbuilder-ivr/api/retryprompt',update)
     .pipe(
      retry(1),
   
    );
   }
   deleteRetryPrompt(id: number)
   {
     return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/retryprompt/'+id)
     .pipe(
      retry(1),
    
    );
   }
   deleteHoliday(id: number)
   {
     return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/app/holiday/'+id)
     .pipe(
      retry(1),
    
    );
   }
   //...........Treeview..........................
   getTreeView(id:string) : Observable<any> 
   {
    return this.http.get<any>(this.Url+ '/dialogbuilder-ivr/api/app/treeview/'+id) 
    .pipe(
      retry(1),
    );
   }

   //.............global error...........................

 
   getGlobalErrorById(id: string) :Observable<GlobalNextStateDetail> 
   {
    return this.http.get<GlobalNextStateDetail>(this.Url+ '/dialogbuilder-ivr/api/app/globalerror/'+id)
    .pipe(
      retry(1),
    );
   }
   getAllGlobalError(appId) : Observable<GlobalNextStateDetail[]> 
   {
    return this.http.get<GlobalNextStateDetail[]>(this.Url+ '/dialogbuilder-ivr/api/app/globalerror/all/'+appId) 
    .pipe(
      retry(1),
    );
   }
   
   getAllGlobalErrorMenu(appId) : Observable<MenuNextStateDetail[]> 
   {
    return this.http.get<MenuNextStateDetail[]>(this.Url+ '/dialogbuilder-ivr/api/app/globalerror/all/'+appId) 
    .pipe(
      retry(1),
    );
   }
   UpdateGlobalError(data:GlobalNextStateDetail)  
   { 
    return this.http.put(this.Url + '/dialogbuilder-ivr/api/app/globalerror/', data) 
    .pipe(
      retry(1),
    );
   }
}
