import { Injectable } from '@angular/core';
import { Observable,throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import {HttpClient, HttpErrorResponse} from '@angular/common/http';
import {BranchingDetail} from '../Models/branchingModel';
import {DialogConfigServiceService} from '../Configurations/dialog-config-service.service';

import {BranchingOptionNoneDetail} from '../Models/branchNoneModel';

@Injectable({
  providedIn: 'root'
})
export class BranchingService {
  Url :string; 
  header : any;  
  errorMessage:string;
  constructor(private http : HttpClient,config:DialogConfigServiceService) {
    this.Url = config.getConfig().apiBaseUrl;
   }
   getAllBranching() : Observable<BranchingDetail[]> 
   {
    return this.http.get<BranchingDetail[]>(this.Url+ '/dialogbuilder-ivr/api/state/branch') 
    .pipe(
      retry(1),
    );
   }
   getBranchingById(id: string) :Observable<BranchingDetail> 
   {
    return this.http.get<BranchingDetail>(this.Url+ '/dialogbuilder-ivr/api/state/branch/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveBranching(appId:string,data:BranchingDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/state/branch/'+appId, data) 
    .pipe(
      retry(1),
    );
   }
   updateBranching(appId:string,update:BranchingDetail)
   {
     return this.http.put(this.Url +'/dialogbuilder-ivr/api/state/branch/'+appId,update)
     .pipe(
      retry(1),
    );
   }
   deleteBranching(id: number)
   {
     return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/state/branch/'+id)
     .pipe(
      retry(1),
    );
   }
   getAllStateName(appId: string) :Observable<BranchingDetail> 
   {
    return this.http.get<BranchingDetail>(this.Url+ '/dialogbuilder-ivr/api/nextstate/branch/'+appId)
    .pipe(
      retry(1),
    );
   }
   getBranchingByType(id: string) :Observable<BranchingOptionNoneDetail> 
   {
    return this.http.get<BranchingOptionNoneDetail>(this.Url+ '/dialogbuilder-ivr/api/state/branch/'+id)
    .pipe(
      retry(1),
    );
   }
}
