import { Injectable ,EventEmitter} from '@angular/core';
import {DIDGroupDetails} from '../Models/DIDgroupModel';
import {LoggedInUser} from '../Models/loginModel';
import { from } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class DataServiceService {
$isLoggedIn= new EventEmitter();
$isDidGroup=new EventEmitter();
user:LoggedInUser={
  username:"",
  password:"",
 
}

  constructor() { }

login(UserName:string,password:string)
{
  console.log("login service")
  this.user.username=UserName;
  this.user.username=password;
  // this.user.role='Admin';
  this.$isLoggedIn.emit(this.user);
}
getDidGroupData(DidGroup:DIDGroupDetails){
console.log(DidGroup)

this.$isDidGroup.emit(DidGroup)
}
}
