import { Component, OnInit,Inject ,ViewChild} from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
export interface DialogData {

}

@Component({
  selector: 'app-edit-state-option',
  templateUrl: './edit-state-option.component.html',
  styleUrls: ['./edit-state-option.component.css']
})
export class EditStateOptionComponent implements OnInit {
  formGroup: FormGroup;
  constructor(private formBuilder: FormBuilder,  public dialogRef: MatDialogRef<EditStateOptionComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) { }

  ngOnInit() {
    this.createForm();
  }
  onNoClick(): void {
    this.dialogRef.close();
  }
  createForm() {
   
    this.formGroup = this.formBuilder.group({
      
      DialogName: [null, Validators.required],
      NextItem: [null, Validators.required],
     
      StateType: [null, Validators.required],
      NextDialog: [null, Validators.required],
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.formGroup.controls; } 
}
