import { Component, OnInit ,ViewChild} from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {BranchingService} from '../Shared/Services/branching.service';
import {BranchingDetail} from '../Shared/Models/branchingModel';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {MatSnackBar} from '@angular/material/snack-bar';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
@Component({
  selector: 'app-create-branching',
  templateUrl: './create-branching.component.html',
  styleUrls: ['./create-branching.component.css']
})
export class CreateBranchingComponent implements OnInit {
  displayedColumns: string[] = [ 'name', 'weight', 'symbol','value','NextStateType','NextState'];
  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  appId:string;
  appName:string;
  branchingDetail:BranchingDetail
  stateId:string;
  branchingForm: FormGroup;
  inEditPage: boolean;
  MethodTypes: MethodType[] = [
    {type: 'Day of Week', viewValue: 'Day of week'},
    {type: 'Time of Day', viewValue: 'Time of day'},
    {type: 'Holiday', viewValue: 'Holiday'},
    {type: 'None', viewValue: 'None'}
  ];
  constructor(private branchingService:BranchingService,private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,private dialog: MatDialog,
    private _snackBar: MatSnackBar,) { }

  ngOnInit() {
    this.dataSource.paginator = this.paginator;
this.branchingDetail={
  id:'',
  stateName:'',
  type:'',

}
    if(this._router.snapshot.url[0].path =='creatBranch')
    {
      
      this.appId=this._router.snapshot.params["appId"];
      this.appName=this._router.snapshot.params["appName"];
    }
    this.initForm(this.branchingDetail);
    if(this._router.snapshot.url[0].path =='editBranch')
    {
      this.inEditPage=true;
      this.appId=this._router.snapshot.params["appId"];
      this.stateId=this._router.snapshot.params["stateId"];
      this.appName=this._router.snapshot.params["appName"];
      this.branchingService.getBranchingById(this.stateId).subscribe(data=>{
        console.log("data branching edit")
        console.log(data)
        this.branchingDetail=data
        this.initForm(this.branchingDetail)
      })
    }
  }
  initForm( branching:BranchingDetail) {
   
    this.branchingForm = this.formBuilder.group({
      
      stateName: [branching.stateName, Validators.required],
      type: [branching.type, Validators.required],
   
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.branchingForm.controls; }  

  onSubmit()
  {

   if(this.branchingForm.valid){
    
     if(this.inEditPage)
     {
      
       this.UpdateBranching(new BranchingDetail(this.branchingForm.value));       
     }
     else
     {
       this.SaveBranching(new BranchingDetail(this.branchingForm.value));       
     }
   }
   }
SaveBranching(branchingDetail:BranchingDetail)
{
  console.log("branching save")
  console.log(branchingDetail)
this.branchingService.SaveBranching(this.appId,branchingDetail).subscribe(data=>{
  this._snackBar.open("Branching saved successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
this.router.navigate(['dialogbuilder/stateDetail',{appName:this.appName,appId:this.appId}]);


})
}
UpdateBranching(branchingDetail:BranchingDetail)
{
  branchingDetail.id=this.branchingDetail.id+""
  console.log("branching update")
  console.log(branchingDetail)
  this.branchingService.updateBranching(this.stateId,branchingDetail).subscribe(data=>{

    this._snackBar.open("Branching updated successfully!", "", {
      duration: 2000,
      verticalPosition: "bottom",
      horizontalPosition:'right',
      panelClass: ['success'],
  });
  this.router.navigate(['dialogbuilder/stateDetail',{appName:this.appName,appId:this.appId}]);
})

}
backStateDetail()
{
 this.router.navigate(['dialogbuilder/stateDetail',{appName:this.appName,appId:this.appId}]);
}
}
interface MethodType {
  type: string;
  viewValue: string;
}
export interface PeriodicElement {
  name: string;
  position: number;
  weight: string;
  symbol: string;
  value:string;
  NextState:string
  NextStateType:string
}

const ELEMENT_DATA: PeriodicElement[] = [
  {position: 1, name: 'option1', weight: 'ANI', symbol: '==',value:'2345345',NextStateType:'',NextState:''},
  {position: 1, name: 'Default', weight: '', symbol: '',value:'',NextStateType:'',NextState:''},
  
];