import { Injectable } from '@angular/core';
import {HolidayNextStateDetail} from '../Models/holidaynextstateModel';
import { Observable,throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import {HttpClient, HttpErrorResponse} from '@angular/common/http';
import {DialogConfigServiceService} from '../Configurations/dialog-config-service.service';
import {HolidayMainDetail} from '../Models/hoidaymainModel';
@Injectable({
  providedIn: 'root'
})
export class StateholidayService {
  Url :string; 
  header : any;  
  errorMessage:string;
  constructor(private http : HttpClient,config:DialogConfigServiceService) {
    this.Url = config.getConfig().apiBaseUrl;
   }
   getAllHoliday(appId) : Observable<HolidayMainDetail[]> 
   {
    return this.http.get<HolidayMainDetail[]>(this.Url+ '/dialogbuilder-ivr/api/state/holiday/'+appId) 
    .pipe(
      retry(1),
    );
   }
   getHolidayById(id: string) :Observable<HolidayMainDetail> 
   {
    return this.http.get<HolidayMainDetail>(this.Url+ '/dialogbuilder-ivr/api/state/holiday/'+id)
    .pipe(
      retry(1),
    );
   }
   
   SaveHoliday(appId:string,data:HolidayMainDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/state/holiday/'+appId, data) 
    .pipe(
      retry(1),
    );
   }
   updateHoliday(update:HolidayMainDetail)
   {
     return this.http.put(this.Url +'/dialogbuilder-ivr/api/state/holiday/',update)
     .pipe(
      retry(1),
    );
   }
 
}