import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {GroupPromptService} from '../Shared/Services/group-prompt.service';
import {PromptGroupDetail} from '../Shared/Models/promptsGroupModel';
import {MatSnackBar} from '@angular/material/snack-bar';
import {AppsService} from '../Shared/Services/apps.service';
//import {AppsVariable} from '../Shared/models/appVariableModel';
import {VariableApps} from '../Shared/Models/variablesAppsModel';
@Component({
  selector: 'app-global-prompts',
  templateUrl: './global-prompts.component.html',
  styleUrls: ['./global-prompts.component.css']
})
export class GlobalPromptsComponent implements OnInit {
  promptDetail:PromptGroupDetail
  variables: Variable[] = []
  conditionKeys: VariableApps[] = [];
  promptForm: FormGroup;
  inEditPage: boolean;
  show: boolean;
  selectedInter: string;
  selectedType: string="value";
  promptId:string;
  Id:string
  appId:string;
  type:string;
  interpretations = [
    {value: 'audio', viewValue: 'audio'},
    {value: 'text', viewValue: 'text'},
    {value: 'string', viewValue: 'string'},
    {value: 'date', viewValue: 'date'},
    {value: 'time', viewValue: 'time'},
    {value: 'currency', viewValue: 'currency'},
    {value: 'number', viewValue: 'number'},
    {value: 'ordinal', viewValue: 'ordinal'}
  ];
  types = [
    {value: 'value', viewValue: 'Value'},
    {value: 'variable', viewValue: 'Variable'},
   
  ];
  constructor(private _snackBar: MatSnackBar,private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,private promptService:GroupPromptService,private appsService:AppsService) { }

  ngOnInit() {
    console.log("global prompts")

this.promptDetail={
  id:'',
  promptGroupName:'',
  specification:'',
  description:'',
  promptName: '',
  interpretation: '',
  alternateText:'',
  type: '',
  promptGroupId:''
}

this.initForm(this.promptDetail);
    if(this._router.snapshot.url[0].path =='createprompt')
    {
      this.promptId=this._router.snapshot.params["id"];
      this.appId=this._router.snapshot.params["appId"];
      this.type=this._router.snapshot.params["type"];
      this.promptId=this._router.snapshot.params["PromptId"];
      if(this.type=="global")
      {
        this.show=false
        this.types = [
          {value: 'value', viewValue: 'Value'},
       

        ];
      }
      else{
        this.appsService.getAppsKey(this.appId).subscribe(data=>{
          console.log("appp variables");
          console.log(data)
          this.conditionKeys=data
        })
      }
  
    }
 
    if(this._router.snapshot.url[0].path =='editprompt')
    {
      this.inEditPage = true;
     this.Id= this._router.snapshot.params["prompt"];
     this.appId=this._router.snapshot.params["appId"];
     this.type=this._router.snapshot.params["type"];
     this.promptId=this._router.snapshot.params["PromptId"];

     if(this.type=="global")
     {this.show=false}
     else{
      this.appsService.getAppsKey(this.appId).subscribe(data=>{
        console.log("appp variables");
        console.log(data)
        this.conditionKeys=data
      })
    }
 this.promptService.getPromptNameById(this.Id).subscribe(data=>{
   
  this.promptDetail=data
  if(this.promptDetail.type=="variable")
  {
this.show=false
  }
this.initForm(  this.promptDetail)
 })

    }
   
  }
  initForm(promptDetail:PromptGroupDetail) {
   
    this.promptForm = this.formBuilder.group({
      
      promptName: [promptDetail.promptName,],
      interpretation: [promptDetail.interpretation, Validators.required],
      alternateText: [promptDetail.alternateText, Validators.required],
      type: [promptDetail.type, Validators.required],
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.promptForm.controls; }  
  onSubmit()
  {

   if(this.promptForm.valid){
    
     if(this.inEditPage)
     {
      
       this.UpdatePrompt(new PromptGroupDetail(this.promptForm.value));       
     }
     else
     {
       this.SavePrompt(new PromptGroupDetail(this.promptForm.value));       
     }
   }
   }
   SavePrompt(prompt:PromptGroupDetail)
   {
    prompt.promptGroupId=this.promptId+"";
    console.log("save prompt data")
    console.log(prompt)
     this.promptService.savePrompt(prompt).subscribe(data=>{
      this._snackBar.open("Prompt saved uccessfully!", "", {
        duration: 2000,
        verticalPosition: "bottom",
        horizontalPosition:'right',
        panelClass: ['success'],
    });
      this.router.navigate(['dialogbuilder/promptdetails',{PromptId:this.promptId,type:this.type}]);
     })
   }
   UpdatePrompt(prompt:PromptGroupDetail)
   {
    prompt.promptGroupId=this.promptId;
    prompt.id=this.Id;
     this.promptService.updatePrompt(prompt).subscribe(data=>{
      this._snackBar.open("Prompt updated successfully!", "", {
        duration: 2000,
        verticalPosition: "bottom",
        horizontalPosition:'right',
        panelClass: ['success'],
    });
      this.router.navigate(['dialogbuilder/promptdetails',{PromptId:this.promptId,type:this.type}]);
     })
   }
   backtoPromptDetails()
   {
    this.router.navigate(['dialogbuilder/promptdetails',{PromptId:this.promptId,type:this.type}]);
   }
  onselectedInter(){
    console.log(this.selectedInter);
  }
  onselectedType() {
    console.log(this.selectedType);
    if(this.selectedType=="variable")
    {
      this.show=true;
    }
    else {
      this.show=false;
    }
  }
  omit_special_char(event)
{   
   var k;  
   k = event.charCode;  //k = event.keyCode; (Both can be used)

 return((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8  || (k >= 48 && k <= 57)); 
}
backToPrompt(){
  
}
}
interface Variable{
  id:string;
  stateName:string;
}