
import { Injectable } from '@angular/core';
import { Observable,throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import {HttpClient, HttpErrorResponse} from '@angular/common/http';
import {BranchingDetail} from '../Models/branchingModel';
import {DialogConfigServiceService} from '../Configurations/dialog-config-service.service';
import {TransferDetails} from '../Models/transferModel';
@Injectable({
  providedIn: 'root'
})
export class TransferService {
  Url :string; 
  header : any;  
  errorMessage:string;
  constructor(private http : HttpClient,config:DialogConfigServiceService) {
    this.Url = config.getConfig().apiBaseUrl;
   }
   getAllTransfer() : Observable<TransferDetails[]> 
   {
    return this.http.get<TransferDetails[]>(this.Url+ '/dialogbuilder-ivr/api/state/transfer') 
    .pipe(
      retry(1),
    );
   }
   getTransferById(id: string) :Observable<TransferDetails> 
   {
    return this.http.get<TransferDetails>(this.Url+ '/dialogbuilder-ivr/api/state/transfer/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveTransfer(appId:string,data:TransferDetails)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/state/transfer/'+appId, data) 
    .pipe(
      retry(1),
    );
   }
   updateTransfer(stateId:string,update:TransferDetails)
   {
     return this.http.put(this.Url +'/dialogbuilder-ivr/api/state/transfer/'+stateId,update)
     .pipe(
      retry(1),
    );
   }
   deleteTransfer(id: number)
   {
     return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/state/transfer/'+id)
     .pipe(
      retry(1),
    );
   }
  
}
