import { Component ,ViewChild,OnInit} from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import {Router} from "@angular/router";
import {UserDetails} from '../Shared/Models/userProfileModel';
import {UserProfileService} from '../Shared/Services/user-profile.service';
import {DeleteConfirmPopupComponent} from 'src/app/delete-confirm-popup/delete-confirm-popup.component';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';

import {MatSnackBar} from '@angular/material/snack-bar';
 
@Component({
  selector: 'app-profile-details',
  templateUrl: './profile-details.component.html',
  styleUrls: ['./profile-details.component.css']
})
export class ProfileDetailsComponent implements OnInit {

  
  profileImg = "assets/images/sample3.jpg";
  displayedColumns: string[] = ['firstName','lastName','userName','role','Actions'];
  dataSource = new MatTableDataSource(); 
  isRecordFound = false;
  isConfirm:boolean
   @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;

  constructor(private _snackBar: MatSnackBar,public dialog: MatDialog,private router: Router,private UserService:UserProfileService,
    ) {
    
   }
   ngOnInit(){
    
     this.UserService.getUserProfileDetail().subscribe(data=>{
      if(data.length >0)
        this.dataSource.data=data
        else
        this.isRecordFound = true;
     })
    this.dataSource.paginator = this.paginator;

  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  getColor(state) {
    switch (state.toLowerCase()) {
      case 'admin':
        return 'green';
      case 'superadmin':
        return 'Blue';
        case 'user':
        return 'Orange';
     
    
    }
  }  
  getImage(state)
  {
    switch (state.role.toLowerCase()) {
      case 'admin':
        return this.profileImg = "assets/images/admin.png";
      case 'superadmin':
        return this.profileImg = "assets/images/superAdmin.png";
        case 'user':
        return this.profileImg = "assets/images/user2.png";
    }
  }

  GetUserByID(userId){   
    
    this.router.navigate(['dialogbuilder/editprofile',userId]);
  }
  deletePopUp(userId){
    const dialogRef = this.dialog.open(DeleteConfirmPopupComponent, {
      width: '450px'
    });

    dialogRef.afterClosed().subscribe(result => {
      
      this.isConfirm=result;
       if(this.isConfirm)
      this.deleteDidGroup(userId)
    });
  }
  deleteDidGroup(UserId){
    this.UserService.deleteUserProfile(UserId).subscribe(data=>{
      console.log("delete response");
      console.log(data);
      this.UserService.getUserProfileDetail().subscribe(data=>{
        console.log(data)
        if(data.length >0)
        this.dataSource.data=data
        else
        this.isRecordFound = true;
      })
      this._snackBar.open("User deleted!", "", {
        duration: 2000,
        verticalPosition: "bottom",
        horizontalPosition:'right',
        panelClass: ['success'],
    });
    })
     
}


}
