import { Component, OnInit,Inject ,ViewChild} from '@angular/core';
import {GroupPromptService} from '../Shared/Services/group-prompt.service';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { concatAll } from 'rxjs/operators';
@Component({
  selector: 'app-refered-apps-popup',
  templateUrl: './refered-apps-popup.component.html',
  styleUrls: ['./refered-apps-popup.component.css']
})
export class ReferedAppsPopupComponent implements OnInit {
  promptGrooupId:string
  referedApps=[]
  constructor(private GroupPromptService :GroupPromptService, public dialogRef: MatDialogRef<ReferedAppsPopupComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) { }

  ngOnInit() {
    
    this.promptGrooupId= this.data["promptGrooupId"]
    this.GroupPromptService.getReferedAppsById(this.promptGrooupId).subscribe(data=>{
      console.log("data")
      console.log(data)
      this.referedApps=data
    })
  }

}
export interface DialogData {
  isConfirm:boolean;
  type:string;
}
