import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import{DataServiceService} from '../../Shared/Services/data-service.service';
import {LoggedInUser} from '../../Shared/Models/loginModel';
import {LogoutPopupComponent} from 'src/app/logout-popup/logout-popup.component';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
@Component({
  selector: 'app-admin-layout',
  templateUrl: './admin-layout.component.html',
  styleUrls: ['./admin-layout.component.css']
})
export class AdminLayoutComponent implements OnInit {
  title = 'User Name';
  role:string="";
  isConfirm:boolean;
  username:string="";
  userId;
  constructor(public router: Router,private dataService: DataServiceService,public dialog: MatDialog,) { }
  loginUser:LoggedInUser
  ngOnInit() {
    var currentUser = JSON.parse(localStorage.getItem('currentUser'));
    console.log("currentUser")
    console.log(currentUser)
    if(currentUser !=null)
    {
      var token = currentUser.user;
      console.log("token.username;")
      console.log(token.username)
       this.username=token.username;
       this.role=token.role;
      this.userId=token.id;
    }else{
      this.username=""
      this.role=""
     this.userId=""
    }


  }

  logout() {
    const dialogRef = this.dialog.open(LogoutPopupComponent, {
      width: '450px'
    });

    dialogRef.afterClosed().subscribe(result => {
   
      this.isConfirm=result;
       if(this.isConfirm)
       {
        localStorage.removeItem('currentUser');
        this.router.navigate(['login'])
       }
    });
   
}
UserDetail()
{
 if(this.role=="Admin" || this.role=="SuperAdmin")
  this.router.navigate(['/dialogbuilder/profiledetails'])
   else
   this.router.navigate(['dialogbuilder/editprofile',this.userId]);
}
}
