import { Injectable } from '@angular/core';
import {DialogConfigServiceService} from '../Configurations/dialog-config-service.service';
import { Observable,throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import {HttpClient, HttpErrorResponse} from '@angular/common/http'; 
import {PromptGroupDetail} from './../Models/promptsGroupModel';
import {AppDetails} from '../Models/appsModel';
@Injectable({
  providedIn: 'root'
})
export class GroupPromptService {
  Url :string; 
  header : any;  
  errorMessage:string;
  constructor(private http : HttpClient,config:DialogConfigServiceService) {
    this.Url = config.getConfig().apiBaseUrl;
   }
   
   getPrompGroup(group:string) : Observable<PromptGroupDetail[]> 
   {
    return this.http.get<PromptGroupDetail[]>(this.Url+ '/dialogbuilder-ivr/api/group/prompts/specification/'+group) 
    .pipe(
      retry(1),
    );
   }
   getPromp(group:string) : Observable<PromptGroupDetail[]> 
   {
    return this.http.get<PromptGroupDetail[]>(this.Url+ '/dialogbuilder-ivr/api/group/prompts/'+group) 
    .pipe(
      retry(1),
    );
   }
 
   getAllAppName() : Observable<AppDetails[]> 
   {
    return this.http.get<AppDetails[]>(this.Url+ '/dialogbuilder-ivr/api/app') 
    .pipe(
      retry(1),
    );
   }
   getPromptGroupById(id: string) :Observable<PromptGroupDetail> 
   {
    return this.http.get<PromptGroupDetail>(this.Url+ '/dialogbuilder-ivr/api/group/prompts/'+id)
    .pipe(
      retry(1),
    );
   }
   getReferedAppsById(id: string) :Observable<any> 
   {
    return this.http.get<any>(this.Url+ '/dialogbuilder-ivr/api/group/prompts/referredapp/'+id)
    .pipe(
      retry(1),
    );
   }
   getPromptNameById(id: string) :Observable<PromptGroupDetail> 
   {
    return this.http.get<PromptGroupDetail>(this.Url+ '/dialogbuilder-ivr/api/common/prompts/'+id)
    .pipe(
      retry(1),
    );
   }
   SavePromptGroup(data:PromptGroupDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/group/prompts', data) 
    .pipe(
      retry(1),
    );
   }
   updatePromptGroup(update:PromptGroupDetail)
   {
     return this.http.put(this.Url +'/dialogbuilder-ivr/api/group/prompts',update)
     .pipe(
      retry(1),
   
    );
   }
   savePrompt(data:PromptGroupDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/common/prompts', data) 
    .pipe(
      retry(1),
    );
   }
   updatePrompt(update:PromptGroupDetail)
   {
     return this.http.put(this.Url +'/dialogbuilder-ivr/api/common/prompts',update)
     .pipe(
      retry(1),
   
    );
   }
   deletePromptGroup(id: number)
   {
     return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/group/prompts/'+id)
     .pipe(
      retry(1),
    );
   }
   deletePrompt(id: number)
   {
     return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/common/prompts/'+id)
     .pipe(
      retry(1),
    
    );
   }
 
}
