import { Component ,ViewChild,OnInit} from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import {Router} from "@angular/router";
import {EnvironmentService} from '../Shared/Services/environment.service';
import {EnvironmentDetail} from '../Shared/Models/environmentModel';
import {MatSnackBar} from '@angular/material/snack-bar';
import {DeleteConfirmPopupComponent} from 'src/app/delete-confirm-popup/delete-confirm-popup.component';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
export interface PeriodicElement {
  EnvironmentName: string;
  PromptDirectory: string;
  LogPath: string;
  GrammarPath: string;
 }
 interface Appname {
   value: string;
   viewValue: string;
 }
 const ELEMENT_DATA: PeriodicElement[] = [
   { EnvironmentName: '1234',PromptDirectory:'ptl1',  LogPath: 'Test',GrammarPath: 'Test'},
   { EnvironmentName: '1234',PromptDirectory:'ptl1',  LogPath: 'Test',GrammarPath: 'Test'},
   { EnvironmentName: '1234',PromptDirectory:'ptl1',  LogPath: 'Test',GrammarPath: 'Test'},
 ];
@Component({
  selector: 'app-environment-detail',
  templateUrl: './environment-detail.component.html',
  styleUrls: ['./environment-detail.component.css']
})
export class EnvironmentDetailComponent implements OnInit {
  isConfirm:string;
  displayedColumns: string[] = ['EnvironmentName','PromptDirectory', 'LogPath','GrammarPath','Actions'];
  dataSource = new MatTableDataSource();
  Appnames: Appname[] = [
     {value: '1', viewValue: 'PTL-Demo'},
     {value: '2', viewValue: 'PTL-Demo1'},
     {value: '3', viewValue: 'PTL-Demo2'},
     {value: '3', viewValue: 'PTL-Demo2'},

   ];
   @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;

  constructor(public dialog: MatDialog,private router: Router,private environmentService:EnvironmentService,private _snackBar: MatSnackBar,) {
   }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }


  ngOnInit(){
    this.environmentTable()
  }
  GetEnvConfigByID(id){
    this.router.navigate(['dialogbuilder/environmentEdit/',id]);
  
  }
  environmentTable()
  {
    this.environmentService.getAllenvironment().subscribe(data=>{
      console.log("end")
      console.log(data)
      
       this.dataSource.data=data
     },(error)=>{
      if (error.status === 500)
     {
      this._snackBar.open("Backend failure!", "", {
        duration: 2000,
        verticalPosition: "bottom",
        horizontalPosition:'right',
        panelClass: ['error'],
    });
     }
     else{
      this._snackBar.open("Server not connected!", "", {
        duration: 2000,
        verticalPosition: "bottom",
        horizontalPosition:'right',
        panelClass: ['error'],
    });
     }
    },)
     this.dataSource.paginator = this.paginator;
  }
  deletePopUp(Id){
    const dialogRef = this.dialog.open(DeleteConfirmPopupComponent, {
      width: '450px'
    });

    dialogRef.afterClosed().subscribe(result => {
   
      this.isConfirm=result;
       if(this.isConfirm)
      this.deleteEnvironment(Id)
    });
  }
deleteEnvironment(id)
{
this.environmentService.deleteEnvironment(id).subscribe(data=>{
  this.environmentTable()
  this._snackBar.open("Environment deleted!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
})
}
}
