import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
@Component({
  selector: 'app-branching-options',
  templateUrl: './branching-options.component.html',
  styleUrls: ['./branching-options.component.css']
})
export class BranchingOptionsComponent implements OnInit {
  formGroup: FormGroup;
  inEditPage: boolean;
  MethodTypes: MethodType[] = [
    {value: '1', viewValue: 'GET'},
    {value: '2', viewValue: 'POST'},
    {value: '3', viewValue: 'PUT'},
    {value: '4', viewValue: 'DELETE'}
  ];
  constructor(private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,) { }

  ngOnInit() {
    if(this._router.snapshot.url[0].path =='restEdit')
    {
      this.inEditPage = true;
    }
    this.createForm();
  }
  createForm() {
   
    this.formGroup = this.formBuilder.group({
      
      WebServiceName: [null, Validators.required],
      MethodType: [null, Validators.required],
      AuthenticationURL: [null, Validators.required],
      UserName: [null, Validators.required],
      Password: [null, Validators.required],
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.formGroup.controls; }  
}
interface MethodType {
  value: string;
  viewValue: string;
}
