import { Component, OnInit,ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource,MatTable, } from '@angular/material/table';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {MatSnackBar} from '@angular/material/snack-bar';
import {HolidayNextStateDetail} from '../Shared/Models/holidaynextstateModel';
import {CreateNextHolidayComponent} from 'src/app/create-next-holiday/create-next-holiday.component';
import {StateholidayService } from '../Shared/Services/stateholiday.service';
import {HolidayMainDetail} from '../Shared/Models/hoidaymainModel';
import {HolidaydetailsComponent} from 'src/app/holidaydetails/holidaydetails.component';
@Component({
  selector: 'app-state-holiday',
  templateUrl: './state-holiday.component.html',
  styleUrls: ['./state-holiday.component.css']
})
export class StateHolidayComponent implements OnInit {
  displayedColumns: string[] = ['option','nextStateType','nextState', 'action'];
  DefaultOptions:string[]=[
    'NoInput','NoMatch','Error','Success','Failure','Default','Holiday' ]
    stateName:string
    dataSource:HolidayNextStateDetail[]
    stateNameDetail:HolidayNextStateDetail
    holidayStateName:HolidayStateName;
    holidayMain:HolidayMainDetail
    optionCollection:HolidayNextStateDetail[]=[]
    statename:string;
    appId:string;
    stateId:string;
    @ViewChild(MatTable,{static:true}) table: MatTable<any>;
    selectedType:string;
    branchValue:string;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  holidayForm: FormGroup;
  inEditPage: boolean;

  constructor(private stateholidayService:StateholidayService,public dialog: MatDialog,private fb: FormBuilder,private _snackBar: MatSnackBar,private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,) { }

  ngOnInit() {
  
    this.optionCollection.push(new HolidayNextStateDetail( { option: 'Holiday',nextStateType:'',nextState:'',nextStateName:''}));
    this.optionCollection.push(new HolidayNextStateDetail( { option: 'Default',nextStateType:'',nextState:'',nextStateName:''}));
    this.holidayMain={
      id:'',
      stateName:'',
      options:[]
    }
    if(this._router.snapshot.url[0].path =='createStateHoliday')
    { 
      var d = new Date();
      let holidayName="Holiday_"+d.getTime()+"";
      this.holidayMain.stateName=holidayName+"";
      this.inEditPage = false;
      this.appId=this._router.snapshot.params["appId"];
      console.log("default data")
    console.log(this.optionCollection)
      this.dataSource=this.optionCollection
      this.initFormForm(this.holidayMain)
    }
    if(this._router.snapshot.url[0].path =='editStateHoliday')
    { 
      this.inEditPage = true;
      this.appId=this._router.snapshot.params["appId"];
      this.stateId=this._router.snapshot.params["stateId"];
      this.stateholidayService.getHolidayById( this.stateId).subscribe(data=>{
        console.log("get data")
        console.log(data)
        this.holidayMain.stateName=data["stateName"]
        this.holidayMain.id=this.stateId+""
        console.log(this.holidayMain.stateName)
        this.optionCollection=data["options"]
      this.dataSource=data["options"]
      this.initFormForm(this.holidayMain)
      })
    }
   
  }
  initFormForm(holidayStateName:HolidayMainDetail) {
   
    this.holidayForm = this.formBuilder.group({
      
      stateName: [holidayStateName.stateName, Validators.required],
     
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.holidayForm.controls; }  
  SaveHoliday()
  {
    if(this.holidayForm.valid){

      this.SaveStateHoliday(new HolidayMainDetail(this.holidayForm.value));  
    }
  }
  UpdateHolidayState()
  {
    this.UpdateStateHoliday(new HolidayMainDetail(this.holidayForm.value)); 
  }
  SaveStateHoliday(holiday:HolidayMainDetail)
  {
    console.log("form data")
    console.log(holiday)
    holiday.options=this.optionCollection
this.stateholidayService.SaveHoliday(this.appId,holiday).subscribe(data=>{
  console.log("sava holiday ")
  console.log(data)
  console.log("save response")
  if(data.hasOwnProperty("errorId"))
 {
  this._snackBar.open("oops somthing went wrong !", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['infor'],
});
 }
 else{
  this._snackBar.open("Saved successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
this.router.navigate(['dialogbuilder/stateDetail',{appId:this.appId}]);
 }
    
  })
  }
  UpdateStateHoliday(holiday:HolidayMainDetail)
  {
    console.log("form data update")
    console.log(holiday)
    holiday.id=this.stateId+""
    holiday.options=this.optionCollection
this.stateholidayService.updateHoliday(holiday).subscribe(data=>{
console.log("response")
console.log(data)
if(data.hasOwnProperty("errorId"))
 {
  this._snackBar.open("oops somthing went wrong !", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['infor'],
});
 }
 else{
  this._snackBar.open("Holiday Updated successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
this.router.navigate(['dialogbuilder/stateDetail',{appId:this.appId}]);
 }
})
  }
  openDialog(action,obj) {
   
console.log("obj data ")
    console.log(obj)
    if(action =="Add"){
    var name="option"+this.dataSource.length+"";
    obj["option"]=name;
  }
   this.stateName= this.holidayForm.get('stateName').value
    obj.action = action;
    const dialogRef = this.dialog.open(CreateNextHolidayComponent, {
      width: '500px',
      data: {appId:this.appId, dataObj:obj,type:"create",stateName:this.stateName }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log("grid added")
      console.log(result)
      if(result.event == 'Add'){
        this.addRowData(result.data);
      }else if(result.event == 'Update'){
        this.updateRowData(result.data);
      }
    });
  
  }
  addRowData(row_obj){
   
    var name="option"+this.dataSource.length+"";
    console.log("name");
    console.log(name)
    this.dataSource.unshift(new HolidayNextStateDetail({
      id:'0',
      option:name,
      nextState:row_obj.nextState,
      nextStateType:row_obj.nextStateType,
      nextStateName:row_obj.nextStateName
    }));

    
    this.table.renderRows();
    
  }
  updateRowData(row_obj){
    console.log("sample row update")
    console.log(row_obj)
    this.dataSource= this.dataSource.filter((value,key)=>{
      
      if(value.option == row_obj.option){
        value.option = row_obj.option;
        value.nextState = row_obj.nextState;
        value.nextStateType = row_obj.nextStateType;
        value.nextStateName=row_obj.nextStateName;
      }
      return true;
    });
  }
  backStateDetail()
  {
    this.router.navigate(['dialogbuilder/stateDetail',{appId:this.appId}]);
  }
  openHolidayDialog()
  {
    const dialogRef = this.dialog.open(HolidaydetailsComponent, {
    data:{appId:this.appId}
    
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
     
    });
  }
}
interface MethodType {
  value: string;
  viewValue: string;
}

export class HolidayStateName{
  stateName:string;
}
