import { Injectable } from '@angular/core';
import { Observable,throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import {HttpClient, HttpErrorResponse} from '@angular/common/http'; 

import {DialogConfigServiceService} from '../Configurations/dialog-config-service.service';
import {StateInputDetail} from '../Models/stateInputModel';
@Injectable({
  providedIn: 'root'
})
export class StateInputService {
  Url :string; 
  header : any;  
  errorMessage:string;
  constructor(private http : HttpClient,config:DialogConfigServiceService) {
    this.Url = config.getConfig().apiBaseUrl;
   }
   getStateInput(id:string) : Observable<StateInputDetail[]> 
   {
    return this.http.get<StateInputDetail[]>(this.Url+ '/dialogbuilder-ivr/api/state/input'+id) 
    .pipe(
      retry(1),
    
    );
   }

   getStateInputById(id: string) :Observable<StateInputDetail> 
   {
    return this.http.get<StateInputDetail>(this.Url+ '/dialogbuilder-ivr/api/state/input/'+id)
    .pipe(
      retry(1),
    );
   }
   getAllAppKey(id: string) :Observable<string[]> 
   {
    return this.http.get<string[]>(this.Url+ '/dialogbuilder-ivr/api/appkeys/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveStateInput(id:string,data:StateInputDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/state/input/'+id, data) 
    .pipe(
      retry(1),
    );
   }
   updateStateInput(appId:string,stateID:string,update:StateInputDetail)
   {
     return this.http.put(this.Url +'/dialogbuilder-ivr/api/state/input/'+appId+'/'+stateID,update)
     .pipe(
      retry(1),
   
    );
   }
   deleteStateInput(id: number)
   {
     return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/state/input/'+id)
     .pipe(
      retry(1),
    );
   }
}
