import { Injectable } from '@angular/core';
import { Observable,throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import {HttpClient, HttpErrorResponse} from '@angular/common/http'; 
import {OptionDetail} from '../Models/OptionsModel';
import {DialogConfigServiceService} from '../Configurations/dialog-config-service.service';
@Injectable({
  providedIn: 'root'
})
export class OptionService {
  Url :string; 
  header : any;  
  errorMessage:string;
  constructor(private http : HttpClient,config:DialogConfigServiceService) {
    this.Url = config.getConfig().apiBaseUrl;
   }
   getAllStateMenuOption(id:string) : Observable<OptionDetail[]> 
   {
    return this.http.get<OptionDetail[]>(this.Url+ '/dialogbuilder-ivr/api/menu/optionlist/'+id) 
    .pipe(
      retry(1),
    
    );
   }
   getAllStateInputOption(id:string) : Observable<OptionDetail[]> 
   {
    return this.http.get<OptionDetail[]>(this.Url+ '/dialogbuilder-ivr/api/input/option/'+id) 
    .pipe(
      retry(1),
    
    );
   }
   getAllStateBackendOption(id:string) : Observable<OptionDetail[]> 
   {
    return this.http.get<OptionDetail[]>(this.Url+ '/dialogbuilder-ivr/api//nonbackend/optionlist/'+id) 
    .pipe(
      retry(1),
    
    );
   }
   getStateMenuOptionById(id: string) :Observable<OptionDetail> 
   {
    return this.http.get<OptionDetail>(this.Url+ '/dialogbuilder-ivr/api/menu/option/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveMenuOption(id:string,data:OptionDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/menu/option/'+id, data) 
    .pipe(
      retry(1),
    );
   }
   updateMenuOption(menuOptionID:string,update:OptionDetail)
   {
     return this.http.put(this.Url +'/dialogbuilder-ivr/api/menu/option/'+menuOptionID,update)
     .pipe(
      retry(1),
   
    );
   }
   deleteMenuOption(id: number)
   {
     return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/menu/option'+id)
     .pipe(
      retry(1),
    );
   }
   getStateOptionMode(stateId: string) :Observable<OptionDetail> 
   {
    return this.http.get<OptionDetail>(this.Url+ '/dialogbuilder-ivr/api/menu/inputmode/'+stateId)
    .pipe(
      retry(1),
    );
   }
   getOptionStateName(appId: string) :Observable<OptionDetail> 
   {
    return this.http.get<OptionDetail>(this.Url+ '/dialogbuilder-ivr/api/nextstate/menu/'+appId)
    .pipe(
      retry(1),
    );
   }
}
