import { Injectable } from '@angular/core';
import {HoOptionDetail} from '../Models/hoursOfOperationModel';
import { Observable,throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import {HttpClient, HttpErrorResponse} from '@angular/common/http';
import {DialogConfigServiceService} from '../Configurations/dialog-config-service.service';
import {HoOptionMainDetail} from '../Models/hooMainOptionModel';
@Injectable({
  providedIn: 'root'
})
export class HoursOfoperationService {
  Url :string; 
  header : any;  
  errorMessage:string;
  constructor(private http : HttpClient,config:DialogConfigServiceService) {
    this.Url = config.getConfig().apiBaseUrl;
   }
   getAllHourOperation(appId) : Observable<HoOptionDetail[]> 
   {
    return this.http.get<HoOptionDetail[]>(this.Url+ '/dialogbuilder-ivr/api/state/hoo/'+appId) 
    .pipe(
      retry(1),
    );
   }
   getHourOperationById(id: string) :Observable<HoOptionDetail> 
   {
    return this.http.get<HoOptionDetail>(this.Url+ '/dialogbuilder-ivr/api/state/hoo/'+id)
    .pipe(
      retry(1),
    );
   }
   getHourOperationName(id: string) :Observable<HoOptionDetail[]> 
   {
    return this.http.get<HoOptionDetail[]>(this.Url+ '/dialogbuilder-ivr/api/state/hoo/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveHourOperation(appId:string,data:HoOptionMainDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/state/hoo/'+appId, data) 
    .pipe(
      retry(1),
    );
   }
   updateHourOperation(stateId:string,update:HoOptionMainDetail)
   {
     return this.http.put(this.Url +'/dialogbuilder-ivr/api/state/hoo/',update)
     .pipe(
      retry(1),
    );
   }
   deleteHourOperation(id: number)
   {
     return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/state/hoo/'+id)
     .pipe(
      retry(1),
    );
   }
}
