import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {MatSnackBar} from '@angular/material/snack-bar';
import {BranchingOptionNoneDetail} from '../Shared/Models/branchNoneModel';
import {BranchOptionsService} from '../Shared/Services/branch-options.service';
import {AppsService} from '../Shared/Services/apps.service';

import {VariableApps} from '../Shared/Models/variablesAppsModel';
@Component({
  selector: 'app-branching-none',
  templateUrl: './branching-none.component.html',
  styleUrls: ['./branching-none.component.css']
})
export class BranchingNoneComponent implements OnInit {
  branchType:string
  appId:string;
  stateId:string;
  appName:string;
  type:string;
  optionId:string;
  branchNoneForm: FormGroup;
  branchingOptionNone:BranchingOptionNoneDetail
  inEditPage: boolean;
  conditionValues: ConditionValue[] = [
    {value: '==', viewValue: '=='},
    {value: '>', viewValue: '>'},
    {value: '<', viewValue: '<'},

  ];
  conditionKeys: VariableApps[] = [];
  constructor(private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,private _snackBar: MatSnackBar,private branchNoneService:BranchOptionsService,
    private appsService:AppsService) { }

  ngOnInit() {
    this.branchingOptionNone={
      id:'',
      conditionKey:'',
      conditionValue:'',
      option:'',
      operator:'',
      type:''
    }
    if(this._router.snapshot.url[0].path =='createNone')
    {
      this.appId=this._router.snapshot.params["appId"];
      this.stateId=this._router.snapshot.params["stateId"];
      this.appName=this._router.snapshot.params["appName"];
      this.branchType=this._router.snapshot.params["branchType"];
      this.inEditPage = false;
      this.appsService.getAppsKey(this.appId).subscribe(data=>{
        console.log("appp variables");
        console.log(data)
        this.conditionKeys=data
      })
    }
    if(this._router.snapshot.url[0].path =='editNone')
    {
      this.appId=this._router.snapshot.params["appId"];
      this.stateId=this._router.snapshot.params["stateId"];
      this.appName=this._router.snapshot.params["appName"];
      this.optionId=this._router.snapshot.params["optionId"];
      this.branchType=this._router.snapshot.params["branchType"];
      this.inEditPage = true;
      this.appsService.getAppsKey(this.appId).subscribe(data=>{
        console.log("appp variables");
        console.log(data)
      })
      this.branchNoneService.getBranchOptionNoneById(this.optionId).subscribe(data=>{
console.log("get by id ")
console.log(data)
this.branchingOptionNone=data
this.initForm(this.branchingOptionNone)
      })
    }
    this.initForm(this.branchingOptionNone);
  }
  initForm(optionNone:BranchingOptionNoneDetail) {
   
    this.branchNoneForm = this.formBuilder.group({
      option: [optionNone.option, Validators.required],
      conditionKey: [optionNone.conditionKey, Validators.required],
      conditionValue: [optionNone.conditionValue, Validators.required],
      operator: [optionNone.operator, Validators.required],
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.branchNoneForm.controls; }  
  onSubmit()
  {

   if(this.branchNoneForm.valid){
    
     if(this.inEditPage)
     {
      
       this.UpdateBranchOptionNone(new BranchingOptionNoneDetail(this.branchNoneForm.value));       
     }
     else
     {
       this.SaveBranchOptionNone(new BranchingOptionNoneDetail(this.branchNoneForm.value));       
     }
   }
   
   }
   SaveBranchOptionNone(branchNone:BranchingOptionNoneDetail)
   {
console.log("branch none")
console.log(branchNone)
this.branchNoneService.SaveBranchOptionNone(this.stateId,branchNone).subscribe(data=>{
  this._snackBar.open("Option saved successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
this.router.navigate(['dialogbuilder/nextState',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:"branching",branchType:this.branchType}])
})
   }
   UpdateBranchOptionNone(brachNone:BranchingOptionNoneDetail)
   {
this.branchNoneService.updateBranchOptionNone(this.branchingOptionNone.id,brachNone).subscribe(data=>{
  this._snackBar.open("Option updated successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
this.router.navigate(['dialogbuilder/nextState',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:"branching",branchType:this.branchType}])
})
   }
   backtoOption()
   {
   
    
         this.router.navigate(['dialogbuilder/nextState',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:"branching",branchType:this.branchType}])
 
     
   }
}
interface ConditionKey {
  value: string;
  viewValue: string;
}
interface ConditionValue {
  value: string;
  viewValue: string;
}