import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {OptionDetail} from '../Shared/Models/OptionsModel';
import {OptionService} from '../Shared/Services/option.service';
@Component({
  selector: 'app-create-options',
  templateUrl: './create-options.component.html',
  styleUrls: ['./create-options.component.css']
})
export class CreateOptionsComponent implements OnInit {
  optionDetail:OptionDetail
  appId:string;
  stateId:string;
  appName:string;
  bargin:string;
  lan:string;
  environmentForm: FormGroup;
  inEditPage: boolean;
  MethodTypes: MethodType[] = [
    {value: '1', viewValue: 'Prompt'},
    {value: '2', viewValue: 'Menu'},
    {value: '3', viewValue: 'Application'},
    {value: '4', viewValue: 'Input'},
    {value: '4', viewValue: 'Backend'},
    {value: '4', viewValue: 'Branch'}
  ];
  constructor(private optionService:OptionService,private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,) { }

  ngOnInit() {
    this.optionDetail={
      id:'',
    option:'',
    dtmf:'',
    voice:'',
    stateName:''
    }
    if(this._router.snapshot.url[0].path =='createOption')
    {
      this.inEditPage = true;
      this.appId=this._router.snapshot.params["appId"];
      this.appName=this._router.snapshot.params["appName"];
      this.stateId= this._router.snapshot.params["stateId"];
    }
    this.initForm();
  }
  initForm() {
   
    this.environmentForm = this.formBuilder.group({
      
      name: [null, Validators.required],
      stateType: [null, Validators.required],
      stateName: [null, Validators.required],
     
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.environmentForm.controls; }  

  backStateDetail()
  {
   this.router.navigate(['dialogbuilder/nextState',{appName:this.appName,appId:this.appId}]);
  }
}
interface MethodType {
  value: string;
  viewValue: string;
}