import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import{StateMenuDetail} from '../Shared/Models/stateMenuModel';
import {StatePromptDetail} from '../Shared/Models/statePromptModel';
import {StatePromptService} from '../Shared/Services/state-prompt.service';
import {StateInputDetail} from '../Shared/Models/stateInputModel';
import {StateInputService} from '../Shared/Services/state-input.service';
import {MatSnackBar} from '@angular/material/snack-bar';
@Component({
  selector: 'app-state-input',
  templateUrl: './state-input.component.html',
  styleUrls: ['./state-input.component.css']
})
export class StateInputComponent implements OnInit {
  selectedType:string="Built-in"
  selectedStatus:  number ;
  stateInputDetail:StateInputDetail
  appGroups: appGroup[] = []
  GrammarTypes: Type[] = [
    {value: 'Built-in', viewValue: 'Built-in'},
    {value: 'UserDefined', viewValue: 'User Defined'},

  ];
type:string;
  options:boolean=true
  options2:boolean=false
  appId:string;
  stateId:string;
  appName:string;
  bargin:string;
  termChar:string;
  inputMode:string;
  interDigit:string;
  timeout:string;
  rePrompt:string;
  lan:string;
  selectedPrompt: string;
  stateInputForm: FormGroup;
  inEditPage: boolean;
  OutputResults:string[] = [];
  InputModes: InputMode[] = [
    {value: 'dtmf', viewValue: 'DTMF'},
    {value: 'voice', viewValue: 'Voice'},
    {value: 'hybrid', viewValue: 'Hybrid'}, 
  ];
  constructor(private _snackBar: MatSnackBar,private stateInputService:StateInputService,private statePromptService:StatePromptService,private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,) { }

  ngOnInit() {
    this.stateInputDetail={
      stateId:'',
      stateName:'',
      grammar:'',
      voiceGrammar:'',
      dtmfGrammar:'',
      minInputLength:'',
      maxInputLength:'',
      grammarType:'',
      bargin:'',
      promptGroupId:'',
      inputMode:'',
      timeout:'',
      retryPrompt:'',
      outputResult:'',
      interDigitTimeout:'',
      termChar:''
    }
    if(this._router.snapshot.url[0].path =='stateInput')
    {
      this.selectedStatus=1;
     
      this.inEditPage = false;
      this.appId=this._router.snapshot.params["appId"];
      this.appName=this._router.snapshot.params["appName"];
      this.bargin=this._router.snapshot.params["bargin"];
      this.timeout= this._router.snapshot.params["timeout"];
      this.interDigit= this._router.snapshot.params["interDigit"];
      this.inputMode= this._router.snapshot.params["inputMode"];
      this.rePrompt= this._router.snapshot.params["rePrompt"];
   this.getPromptName();
     this.stateInputDetail.bargin=this.bargin;
     this.stateInputDetail.inputMode=this.inputMode;
     this.stateInputDetail.interDigitTimeout=this.interDigit;
     this.stateInputDetail.termChar=this.termChar;
     this.stateInputDetail.timeout=this.timeout;
     this.stateInputDetail.retryPrompt=this.rePrompt;


     this.initForm(this.stateInputDetail);
    }
    

    if(this._router.snapshot.url[0].path =='editStateInput')
    {
      this.inEditPage = true;
      this.appId=this._router.snapshot.params["appId"];
      this.stateId=this._router.snapshot.params["stateId"];
      this.appName=this._router.snapshot.params["appName"];
      this.bargin=this._router.snapshot.params["bargin"];
      this.timeout= this._router.snapshot.params["timeout"];
      this.interDigit= this._router.snapshot.params["interDigit"];
      this.inputMode= this._router.snapshot.params["inputMode"];
      this.rePrompt= this._router.snapshot.params["rePrompt"];
   this.getPromptName();
  this.stateInputService.getStateInputById(this.stateId).subscribe(data=>{
    console.log("data")
    console.log(data)
    this.stateInputDetail=data;
   this.selectedType=this.stateInputDetail.grammarType
   if(  this.selectedType=="Built-in")
   {this.options=true
     this.options2=false
   } else if(  this.selectedType=="UserDefined")
   { this.options=false
    this.options2=true

   }
this.initForm(this.stateInputDetail)
  })
     
 
    }
    this.stateInputService.getAllAppKey(this.appId).subscribe(data=>{
      this.OutputResults=data
 
    })

    this.initForm(this.stateInputDetail);
    const GrammerEvents =this.stateInputForm.get('grammar')
    GrammerEvents.setValidators(Validators.required);
  }
  initForm( stateInputDetail:StateInputDetail) {
   
    this.stateInputForm = this.formBuilder.group({
      
      stateName: [stateInputDetail.stateName, Validators.required],
      outputResult: [stateInputDetail.outputResult, ],
      timeout: [stateInputDetail.timeout, Validators.required],
      promptGroupId: [stateInputDetail.promptGroupId, Validators.required],
      bargin: [stateInputDetail.bargin,],
      retryPrompt: [stateInputDetail.retryPrompt, ],
      inputMode: [stateInputDetail.inputMode, Validators.required],
      maxInputLength:[stateInputDetail.maxInputLength, Validators.required],
      minInputLength:[stateInputDetail.minInputLength, Validators.required],
      interDigitTimeout: [stateInputDetail.interDigitTimeout, Validators.required],
      termChar: [stateInputDetail.termChar, Validators.required],
      grammar: [stateInputDetail.grammar],
      dtmfGrammar: [stateInputDetail.dtmfGrammar],
      voiceGrammar: [stateInputDetail.voiceGrammar],
      grammarType:[stateInputDetail.grammarType]
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.stateInputForm.controls; }  
  onSubmit()
  {

   if(this.stateInputForm.valid){
    
    if(this.inEditPage)
     {
      
       this.UpdateStateInput(new StateInputDetail(this.stateInputForm.value));       
     }
     else
     {
       this.SaveStateInput(new StateInputDetail(this.stateInputForm.value));       
     }
   }
   }
SaveStateInput(stateInputDetail:StateInputDetail)
{
 
  stateInputDetail.bargin=stateInputDetail.bargin+"";
  stateInputDetail.promptGroupId=stateInputDetail.promptGroupId+"";
  stateInputDetail.retryPrompt=stateInputDetail.retryPrompt+"";
 if(this.selectedType=="Built-in")
 {
   stateInputDetail.voiceGrammar="";
   stateInputDetail.dtmfGrammar="";
 }
 else{
   stateInputDetail.grammar=""
 }

console.log("save state input")
console.log(stateInputDetail)
  this.stateInputService.SaveStateInput(this.appId,stateInputDetail).subscribe(data=>{
    this._snackBar.open("State input saved successfully!", "", {
      duration: 2000,
      verticalPosition: "bottom",
      horizontalPosition:'right',
      panelClass: ['success'],
  });
  this.router.navigate(['dialogbuilder/stateDetail',{appName:this.appName,appId:this.appId}]);
  })
}
UpdateStateInput(stateInputDetail:StateInputDetail)
{
  stateInputDetail.bargin=stateInputDetail.bargin+"";
  stateInputDetail.promptGroupId=stateInputDetail.promptGroupId+"";
  stateInputDetail.retryPrompt=stateInputDetail.retryPrompt+"";
  if(this.selectedType=="Built-in")
  {
    stateInputDetail.voiceGrammar="";
    stateInputDetail.dtmfGrammar="";
  }
  else{
    stateInputDetail.grammar=""
  }
this.stateInputService.updateStateInput(this.appId,this.stateId,stateInputDetail).subscribe(data=>{
 

 this._snackBar.open("State input updated successfully!", "", {
  duration: 2000,
  verticalPosition: "bottom",
  horizontalPosition:'right',
  panelClass: ['success'],
});
this.router.navigate(['dialogbuilder/stateDetail',{appName:this.appName,appId:this.appId}]);
})
}
  getPromptName()
  {
    this.statePromptService.getGroupPrompt(this.appId).subscribe(data=>{
   
      this.appGroups = [
        {
          name: 'Global',
          apps:data["global"]
        
        },
        {
          name:'Apps',
          apps: data[this.appName]
          
        }
      ];
    
    });
  }
  backStateDetail()
  {
   this.router.navigate(['dialogbuilder/stateDetail',{appName:this.appName,appId:this.appId}]);
  }
  
  onselectedType(){
    console.log("sele")
    console.log(this.selectedType);
  //  const VoiceGrammerEvents =this.stateInputForm.get('voiceGrammar')
    const DTMFGrammerEvents =this.stateInputForm.get('dtmfGrammar')
    const GrammerEvents =this.stateInputForm.get('grammar')
if(this.selectedType=="Built-in")
{ this.options2=false
  this.options=true
 
  GrammerEvents.setValidators(Validators.required);
 // VoiceGrammerEvents.clearValidators();
  DTMFGrammerEvents.clearValidators();
 
}else if(this.selectedType=="UserDefined"){
  this.options2=true
  this.options=false

  GrammerEvents.clearValidators();
  //VoiceGrammerEvents.setValidators(Validators.required);
  DTMFGrammerEvents.setValidators(Validators.required);
}
GrammerEvents.updateValueAndValidity();
//VoiceGrammerEvents.updateValueAndValidity();
DTMFGrammerEvents.updateValueAndValidity();

  }
}
interface result {
  value: string;
  viewValue: string;
}
interface InputMode {
  value: string;
  viewValue: string;
}
interface appGroup {

  name: string;
  apps: app[];
  
}
interface app {
  id: string;
  promptGroupName: string;
}
interface Type {
  value: string;
  viewValue: string;
} 