import {Component, OnInit, ViewChild,Inject,Optional} from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import {AppsService} from '../Shared/Services/apps.service';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
@Component({
  selector: 'app-holidaydetails',
  templateUrl: './holidaydetails.component.html',
  styleUrls: ['./holidaydetails.component.css']
})
export class HolidaydetailsComponent implements OnInit {
  displayedColumns: string[] = [ 'holidayName', 'data'];
  dataSource = new MatTableDataSource()
appId:string;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  constructor(private appsService:AppsService,public dialogRef: MatDialogRef<HolidaydetailsComponent>,
    
    @Optional() @Inject(MAT_DIALOG_DATA) public data: any,) {
    this.appId=  data["appId"]
    console.log("app id " +this.appId)
     }

  ngOnInit(): void {
    this.dataSource.paginator = this.paginator;
    this.getHoliday()
  }
  getHoliday()
  {
    this.appsService.getAllHolday(this.appId).subscribe(data=>{
      console.log("get holiday")
      console.log(data)
      this.dataSource.data=data;

    })
  }
}
