
import { Component, ViewChild, ViewChildren, QueryList, ChangeDetectorRef,OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import {MatPaginator} from '@angular/material/paginator';
import { ActivatedRoute,Router } from '@angular/router';
import {StatePromptService} from '../Shared/Services/state-prompt.service';
import {MatSnackBar} from '@angular/material/snack-bar';
import {DeleteConfirmPopupComponent} from 'src/app/delete-confirm-popup/delete-confirm-popup.component';
import {InitStatePopupComponent} from '../init-state-popup/init-state-popup.component';
import {MatDialog,MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {AppDetails} from '../Shared/Models/appsModel';
import {AppsService} from '../Shared/Services/apps.service';
import {NonbackendComponent} from 'src/app/nonbackend/nonbackend.component';
import {BranchingService} from '../Shared/Services/branching.service';
import {NextStatePopupComponent} from 'src/app/next-state-popup/next-state-popup.component';
import {animate, state, style, transition, trigger} from '@angular/animations';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource, MatTable } from '@angular/material/table';
@Component({
  selector: 'app-state-detai',
  templateUrl: './state-detai.component.html',
  styleUrls: ['./state-detai.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({ height: '0px', minHeight: '0' })),
      state('expanded', style({ height: '*' })),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})
export class StateDetaiComponent implements OnInit {
  @ViewChild('outerSort', { static: true }) sort: MatSort;
  @ViewChildren('innerSort') innerSort: QueryList<MatSort>;
  @ViewChildren('innerTables') innerTables: QueryList<MatTable<Address>>;
  deleteStates:string
  dataSource1: MatTableDataSource<User>;
  usersData: User[] = [];
  columnsToDisplay = ['name', 'email', 'phone'];
  innerDisplayedColumns = ['street', 'zipCode', 'city'];
  expandedElement: User | null;
  branchType:string;
  results:string
  appDetails:AppDetails
  displayedColumns: string[] = ['stateName','stateType','promptName', 'Actions'];
  dataSource = new MatTableDataSource();
  inEditPage: boolean;
  isConfirm:boolean
  appId:string;
  appName:string;
  bargin:string;
  termChar:string;
  inputMode:string;
  interDigit:string;
  timeout:string;
  rePrompt:string;
  
   @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;

  constructor(private appService:AppsService,public dialog: MatDialog,private statePromptService:StatePromptService,private _snackBar: MatSnackBar,private router: Router,private _router: ActivatedRoute,private branchingService:BranchingService,  private cd: ChangeDetectorRef) {
   }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }


  ngOnInit(): void {
    USERS.forEach(user => {
      if (user.addresses && Array.isArray(user.addresses) && user.addresses.length) {
        this.usersData = [...this.usersData, {...user, addresses: new MatTableDataSource(user.addresses)}];
      } else {
        this.usersData = [...this.usersData, user];
      }
    });
    this.dataSource1 = new MatTableDataSource(this.usersData);
    this.dataSource1.sort = this.sort;
    this.dataSource1.paginator = this.paginator;
    if(this._router.snapshot.url[0].path =='stateDetail')
    {
      this.inEditPage = true;
      this.appId=this._router.snapshot.params["appId"];
      this.appName= this._router.snapshot.params["appName"];
      this.appName= this._router.snapshot.params["appName"];
      this.bargin= this._router.snapshot.params["bargin"];
    
      this.timeout= this._router.snapshot.params["timeout"];
 
      this.interDigit= this._router.snapshot.params["interDigit"];
      this.inputMode= this._router.snapshot.params["inputMode"];
      this.rePrompt= this._router.snapshot.params["rePrompt"];
    
      this.statePrompttable(this.appId)
    }
  }
statePrompttable(id)
{
  this.statePromptService.getStatePrompt(id).subscribe(data=>{
    console.log("state dev data")
    console.log(data)
    this.dataSource.data=data
    this.dataSource.paginator = this.paginator;
  })
}
  getStatePrompt()
  {
 
    this.appService.getAppsById(this.appId).subscribe(data=>{
      this.appDetails=data;
      this.router.navigate(['dialogbuilder/statePrompt',{appId:this.appId,appName:this.appName,bargin:this.appDetails.bargin,lan:this.appDetails.language}]);
    });
  
  }
  getStateMenu()
  {
   
    this.appService.getAppsById(this.appId).subscribe(data=>{
      this.appDetails=data;
      this.router.navigate(['dialogbuilder/stateMenu',{appId:this.appId,appName:this.appName,bargin:this.appDetails.bargin,lan:this.appDetails.language,inputMode:this.appDetails.inputMode,timeout:this.timeout}]);
    })
  

  }
  getStateInput()
  {
   
    this.appService.getAppsById(this.appId).subscribe(data=>{
      this.appDetails=data;
      this.router.navigate(['dialogbuilder/stateInput',{appId:this.appId,appName:this.appName,bargin:this.appDetails.bargin,inputMode:this.appDetails.inputMode,interDigit:this.appDetails.interDigitTimeout,rePrompt:this.appDetails.rePrompt,termChar:this.appDetails.termChar,timeout:this.appDetails.timeout}]);
    })
  
  }
  getStateBranching()
  {
    this.router.navigate(['dialogbuilder/creatBranch',{appId:this.appId,appName:this.appName}])
  }
  getStateTransfer()
  {
    this.router.navigate(['dialogbuilder/createTransfer',{appId:this.appId,appName:this.appName}])
  }
  getHoursOfOperation()
  {
    this.router.navigate(['dialogbuilder/createHourOfOperation',{appId:this.appId,appName:this.appName}])
  }
  getHoliday()
  {
    this.router.navigate(['dialogbuilder/createStateHoliday',{appId:this.appId,appName:this.appName}])
  }
  getStateNonbackend()
  {
    const dialogRef = this.dialog.open(NonbackendComponent, {
      width:'400px',
      data: {type: "create", appId:this.appId,appname:this.appName}
    });

    dialogRef.afterClosed().subscribe(result => {
    
  
    });
  }
  getEditStatePrompt(element)
  {
if(element.stateType.toLowerCase() =="prompt")
{
  this.appService.getAppsById(this.appId).subscribe(data=>{
    this.appDetails=data;
  this.router.navigate(['dialogbuilder/editStatePrompt',{appId:this.appId,appName:this.appName,stateId:element.stateId}])
  })
}
  else if(element.stateType.toLowerCase()=="menu"){
    this.appService.getAppsById(this.appId).subscribe(data=>{
      this.appDetails=data;
    this.router.navigate(['dialogbuilder/editStateMenu',{appId:this.appId,appName:this.appName,stateId:element.stateId,timeout:this.timeout}])
  })
  }
  else if(element.stateType.toLowerCase()=="input"){
    this.router.navigate(['dialogbuilder/editStateInput',{appId:this.appId,appName:this.appName,stateId:element.stateId,bargin:this.bargin,inputMode:this.inputMode,interDigit:this.interDigit,rePrompt:this.rePrompt,termChar:this.termChar,timeout:this.timeout}])
  }
  else if(element.stateType.toLowerCase()=="branching")
  {
    this.router.navigate(['dialogbuilder/editBranch',{appId:this.appId,appName:this.appName,stateId:element.stateId}])
  }
  else if(element.stateType.toLowerCase()=="holiday")
  {
    this.router.navigate(['dialogbuilder/editStateHoliday',{appId:this.appId,appName:this.appName,stateId:element.stateId}])
  }
  else if(element.stateType.toLowerCase()=="nonbackend")
  {
    const dialogRef = this.dialog.open(NonbackendComponent, {
      width:'400px',
      data: {type: "edit", appId:this.appId,stateId:element.stateId}
    });

    dialogRef.afterClosed().subscribe(result => {
    console.log("results")
    console.log(result)
    });
 
  }
  else if(element.stateType.toLowerCase()=="transfer")
  {
    this.router.navigate(['dialogbuilder/editTransfer',{appId:this.appId,appName:this.appName,stateId:element.stateId}])
  }
  else if(element.stateType.toLowerCase()=="hoursofoperation")
  {
    this.router.navigate(['dialogbuilder/editHourOfOperation',{appId:this.appId,appName:this.appName,stateId:element.stateId}])
  }
  }
  deleteState(element)
  {
    console.log("delee state")
    console.log(element.stateId)
    console.log(element.stateType)
    const dialogRef = this.dialog.open(DeleteConfirmPopupComponent, {
      width: '450px'
    });
  
    dialogRef.afterClosed().subscribe(result => {
      this.isConfirm=result;
       if(this.isConfirm)
      this.deleteStatePrompt(element.stateId,element.stateType)
    });
  }
  deleteStatePrompt(stateId,stateType)
  {
    console.log("delete state Id")
    console.log(stateId)
    console.log(stateType)
    this.deleteStates=stateType+"";
    console.log(this.deleteStates)
   
     this.statePromptService.deleteStatePrompt(stateId,this.deleteStates).subscribe(data=>{
      this.statePrompttable(this.appId)
      if(data==stateId)
      {
        this._snackBar.open("State prompt delete successfully!", "", {
          duration: 2000,
          verticalPosition: "bottom",
          horizontalPosition:'right',
          panelClass: ['success'],
      });
      }
    
     })
  
   
  }
  getNextState(element)
  {
    if(element.stateType.toLowerCase()== "menu" )
    {
    this.router.navigate(['dialogbuilder/nextState',{appId:this.appId,appName:this.appName,stateId:element.stateId,type:"menu",timeout:this.timeout}])
    }
    if(element.stateType.toLowerCase()== "nonbackend" )
    {
    this.router.navigate(['dialogbuilder/nextState',{appId:this.appId,appName:this.appName,stateId:element.stateId,type:"nonbackend"}])
    }
    else if(element.stateType.toLowerCase()== "branching")
    {
      this.branchingService.getBranchingByType(element.stateId).subscribe(data=>{
        console.log("branching type")
        console.log(data)
        console.log(data["type"])
        this.branchType=data["type"]
        this.router.navigate(['dialogbuilder/nextState',{appId:this.appId,appName:this.appName,stateId:element.stateId,type:"branching",branchType:this.branchType}])
      })
    console.log("typ",this.branchType)
    
    }
    else if(element.stateType.toLowerCase()== "input")
    {
      this.router.navigate(['dialogbuilder/nextState',{appId:this.appId,appName:this.appName,stateId:element.stateId,type:"input",}])
    }
    else if(element.stateType.toLowerCase()== "none")
    {
      this.router.navigate(['dialogbuilder/nextState',{appId:this.appId,appName:this.appName,stateId:element.stateId,type:"none",branchType:this.branchType}])
    }
   
    else if(element.stateType.toLowerCase()== "prompt")
    {
      const dialogRef = this.dialog.open(NextStatePopupComponent, {
        width:'400px',
        data: {type: "create", appId:this.appId,state:"stateDevPrompt",stateId:element.stateId}
      });
  
      dialogRef.afterClosed().subscribe(result => {
      
       
      });
    }
  }
  toggleRow(element: User) {
    element.addresses && (element.addresses as MatTableDataSource<Address>).data.length ? (this.expandedElement = this.expandedElement === element ? null : element) : null;
    this.cd.detectChanges();
    this.innerTables.forEach((table, index) => (table.dataSource as MatTableDataSource<Address>).sort = this.innerSort.toArray()[index]);
  }
}

export interface User {
  name: string;
  email: string;
  phone: string;
  addresses?: Address[] | MatTableDataSource<Address>;
}

export interface Address {
  street: string;
  zipCode: string;
  city: string;
}

export interface UserDataSource {
  name: string;
  email: string;
  phone: string;
  addresses?: MatTableDataSource<Address>;
}

