import { Component, OnInit,ViewChild,Inject } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import {Router} from "@angular/router";
import {MatDialog,MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {CreatePromptGroupComponent} from '../create-prompt-group/create-prompt-group.component';
import {GroupPromptService} from '../Shared/Services/group-prompt.service';
import {PromptGroupDetail} from '../Shared/Models/promptsGroupModel';
import {DeleteConfirmPopupComponent} from 'src/app/delete-confirm-popup/delete-confirm-popup.component';
import {MatSnackBar} from '@angular/material/snack-bar';
import {ReferedAppsPopupComponent} from 'src/app/refered-apps-popup/refered-apps-popup.component';


@Component({
  selector: 'app-prompts',
  templateUrl: './prompts.component.html',
  styleUrls: ['./prompts.component.css']
})
export class PromptsComponent implements OnInit {
  Appnames: Appname[] = [];
  dataSource = new MatTableDataSource(); 
  isRecordFound = false;
  isConfirm:boolean;
  appId:string;
 
  displayedColumns: string[] = ['promptGroupName','description','prompt','ReferApps','Actions'];
  selectedType:string="global";
  pagetype: string="prompdata";
  promptId: string="1";
   @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;

  constructor(private router: Router,public dialog: MatDialog,private promptGroupService:GroupPromptService,private _snackBar: MatSnackBar) {
   }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  ngOnInit(){
  console.log("init")
    this.promptGroupService.getAllAppName().subscribe((data)=>{
  
  this.Appnames=data
      });
   this.promptTable();
    this.dataSource.paginator = this.paginator;
  }
  promptTable()
  {
    this.promptGroupService.getPrompGroup("global").subscribe(data=>{
      console.log("global")
      console.log(data)
      if(data.length >0)
      this.dataSource.data=data
      else
      this.isRecordFound = true;
    })
    this.dataSource.paginator = this.paginator;
  }
  openDialog(): void {
    this.pagetype="create"
    const dialogRef = this.dialog.open(CreatePromptGroupComponent, {
      width: '650px',
      data: {id: "", type: "create"}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.pagetype = result;
    });
  }
  openReferedApps(id)
  {
    const dialogRef = this.dialog.open(ReferedAppsPopupComponent, {
      width: '650px',
      data: {promptGrooupId: id, }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.pagetype = result;
    });
  }
  openEditDialog(id){
  
    this.router.navigate(['dialogbuilder/editPromptGroup',id]);
  }
  openPrompt(id)
  {
    console.log("id")
    console.log(id)
    if(this.selectedType=="global")
    {
      this.router.navigate(['dialogbuilder/promptdetails/',{PromptId:id,type:this.selectedType}]);
    }
    else{
      this.appId= this.Appnames.find(x=>x.appName==this.selectedType).id;
      this.router.navigate(['dialogbuilder/promptdetails/',
      {PromptId:id,appId:this.appId,type:this.selectedType}]);
    }
  
   
  }
  onPromptGroup(selected)
  {
    console.log("click")
console.log(selected)

  }
  deletePopUp(DidGroupId){
    const dialogRef = this.dialog.open(DeleteConfirmPopupComponent, {
      width: '450px'
    });

    dialogRef.afterClosed().subscribe(result => {
   
      this.isConfirm=result;
       if(this.isConfirm)
      this.deleteDidGroup(DidGroupId)
    });
  }
  deleteDidGroup(promptGroupId){
    this.promptGroupService.deletePromptGroup(promptGroupId).subscribe(data=>{
    console.log("delete response");
    console.log(data);
    this.promptGroupService.getPrompGroup(this.selectedType).subscribe(data=>{
    
      this.dataSource.data=data
      this.isRecordFound = true;
   
      })
    this._snackBar.open("Prompt group deleted!", "", {
      duration: 2000,
      verticalPosition: "bottom",
      horizontalPosition:'right',
      panelClass: ['success'],
    });
    })
      }
      onselectedType(){
        console.log("sele")
        console.log(this.selectedType);

        if(this.selectedType=="global")
        {
     this.displayedColumns= ['promptGroupName','description','prompt','ReferApps','Actions'];
        }
        else{
          this.displayedColumns= ['promptGroupName','description','prompt','Actions'];
        }
        this.promptGroupService.getPrompGroup(this.selectedType).subscribe(data=>{
    console.log("app group")
    console.log(data)
          this.dataSource.data=data
          this.isRecordFound = true;
       
          })
      }
}
interface Appname {
  id:string;
  appName: string;
}
