import { Component, OnInit ,ViewChild} from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import {AppDetails} from '../Shared/Models/appsModel';
import {MatSnackBar} from '@angular/material/snack-bar';
import {AppsService} from '../Shared/Services/apps.service';
@Component({
  selector: 'app-create-apps',
  templateUrl: './create-apps.component.html',
  styleUrls: ['./create-apps.component.css']
})
export class CreateAppsComponent implements OnInit {
  bargin:boolean = true;
  appDetails:AppDetails
  displayedColumns = ['id', 'Key', 'Value', 'remove'];
  dataSource: MatTableDataSource<UserData>;
  inputKey:string='';
  inputValue:string='';
  isEditable = false;
  appForm: FormGroup;
  AppVariableForm:FormGroup;
  inEditPage: boolean;
  appsId:string;
  Languages: Language[] = [
    {value: 'en-US', viewValue: 'en-US'},
    {value: 'es-US', viewValue: 'es-US'},
    
  ];
  InputModes: InputMode[] = [
    {value: 'dtmf', viewValue: 'DTMF'},
  
  
  ];
  @ViewChild(MatPaginator) paginator: MatPaginator;
  constructor(private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,private _snackBar: MatSnackBar,private appService:AppsService) 
  {
      // Creates random users.
      const users: UserData[] = [];
   
      // Assign the data to the data source for the table to render.
      this.dataSource = new MatTableDataSource(users);
   }

  ngOnInit() {
    console.log("data")
    this.appDetails={
      id:'',
      appName:'',
      inputMode:'',
      interDigitTimeout:'',
      bargin:'',
      getShadowResult:'',
      language:'',
      timeout:'',
      termChar:'',
      rePrompt:''
    }
    this.appDetails.bargin="true"
    this.appDetails.language="en-US"
    this.appDetails.inputMode="dtmf"
    this.appDetails.rePrompt="false"
    this.appDetails.getShadowResult="false"
    this.appDetails.timeout="3"
    this.appDetails.interDigitTimeout="3";
    this.appDetails.termChar="#";
    this.initForm(this.appDetails);
    this.dataSource.paginator = this.paginator;
    if(this._router.snapshot.url[0].path =='appsEdit')
    {
      this.inEditPage = true;
      this.appsId=this._router.snapshot.params["id"];
      this.appService.getAppsById(this.appsId).subscribe(data=>{
   console.log("data")
        console.log(data)
        this.initForm(data)
      })
    }
    this.initForm(this.appDetails);
  }
  initForm( apps:AppDetails) {
   
    this.appForm = this.formBuilder.group({
      
      appName: [apps.appName, Validators.required],
      language: [apps.language, Validators.required],
      inputMode: [apps.inputMode, Validators.required],
      bargin:[apps.bargin, ],
      rePrompt: [apps.rePrompt, ],
      timeout: [apps.timeout, Validators.required],
      interDigitTimeout: [apps.interDigitTimeout, Validators.required],
      termChar: [apps.termChar, Validators.required],
     getShadowResult: [apps.getShadowResult],
     
    
    });

  }
  // convenience getter for easy access to form fields
  get f() { return this.appForm.controls; }  
 // Creates new user.
 createNewUser(id: number): UserData {
 
  return {
    id: id.toString(),
    Key: this.inputKey,
    Value: this.inputValue
  };
}
onSubmit()
{

 if(this.appForm.valid){

   if(this.inEditPage)
   {
    
     this.UpdateApps(new AppDetails(this.appForm.value));       
   }
   else
   {
     this.SaveApps(new AppDetails(this.appForm.value));       
   }
 }
 else{
   this._snackBar.open("Fill input fields!", "", {
     duration: 2000,
     verticalPosition: "bottom",
     horizontalPosition:'right',
     panelClass: ['success'],
 });
     
  this.router.navigateByUrl('/dialogbuilder/app');
 }
 }
 SaveApps(appsDetail:AppDetails){

  appsDetail.bargin=appsDetail.bargin+"";
  appsDetail.rePrompt=appsDetail.rePrompt+"";
  if( appsDetail.bargin=="")
  appsDetail.bargin="false";
  if( appsDetail.rePrompt=="")
  appsDetail.rePrompt="false";
  if( appsDetail.getShadowResult=="")
  appsDetail.getShadowResult="false";
  appsDetail.getShadowResult=appsDetail.getShadowResult+"";
  console.log(appsDetail)
this.appService.SaveApps(appsDetail).subscribe(data=>{
  console.log("app save response")
  console.log(data)
  this._snackBar.open("Apps saved successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
    
  this.router.navigateByUrl('/dialogbuilder/apps');
})
 }
 UpdateApps(appsDetail:AppDetails){
   appsDetail.id=this.appsId
  appsDetail.bargin=appsDetail.bargin+"";
  appsDetail.rePrompt=appsDetail.rePrompt+"";
  appsDetail.getShadowResult=appsDetail.getShadowResult+"";
  if( appsDetail.bargin=="")
  appsDetail.bargin="false";
  if( appsDetail.rePrompt=="")
  appsDetail.rePrompt="false";
  if( appsDetail.getShadowResult=="")
  appsDetail.getShadowResult="false";
  console.log("update")
  console.log(appsDetail)
this.appService.updateApps(appsDetail).subscribe(data=>{
  this._snackBar.open("Apps updated successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
this.router.navigateByUrl('/dialogbuilder/apps');
})
 }
// Adds new user.
addRow() {
this.inputKey=this.AppVariableForm.value.InputKey;
this.inputValue=this.AppVariableForm.value.InputValue;
console.log(this.inputKey);
console.log(this.inputValue)
  this.dataSource.data.push(this.createNewUser(this.dataSource.data.length + 1));
  this.dataSource.filter = "";
}
 omit_special_char(event)
{   
   var k;  
   k = event.charCode;  //k = event.keyCode; (Both can be used)

 return((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8  || (k >= 48 && k <= 57)); 
}
removeAll() {
  this.dataSource.data = [];
}

removeAt(index: number) {
  const data = this.dataSource.data;
  data.splice((this.paginator.pageIndex * this.paginator.pageSize) + index, 1);

  this.dataSource.data = data;
}
}


interface Language {
  value: string;
  viewValue: string;
}
interface InputMode {
  value: string;
  viewValue: string;
}


export interface UserData {
  id: string;
  Key: string;
  Value: string;
 
}