import { Component, OnInit ,ViewChild} from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import { ActivatedRoute,Router } from '@angular/router';
import {DIDGroupDetails} from '../Shared/Models/DIDgroupModel';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {DIDgroupService} from '../Shared/Services/didgroup.service';
import {MatSnackBar} from '@angular/material/snack-bar';
import {AppDetails} from '../Shared/Models/appsModel';
import{DataServiceService} from '../Shared/Services/data-service.service';
@Component({
  selector: 'app-create-didgroup',
  templateUrl: './create-didgroup.component.html',
  styleUrls: ['./create-didgroup.component.css']
})

export class CreateDIDgroupComponent implements OnInit {
  DIDGroup:DIDGroupDetails;
  appDetails:AppDetails;
  DidGroupForm: FormGroup;
  inEditPage: boolean;
  Appnames: Appname[] = [];
  DidGroupId:string;
  constructor(private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,private dialog: MatDialog,
  private  DIDService:DIDgroupService,private _snackBar: MatSnackBar,private dataService:DataServiceService) { }

  ngOnInit() {
   
    this.DIDGroup={
      id:'',
      groupName:'',
      description:'',
      appName :'',
      appId:''
    }

    this.DIDService.getAllAppName().subscribe((data)=>{
  
    this.Appnames=data;
    });
    this.initForm(this.DIDGroup);
    
    if(this._router.snapshot.url[0].path =='DIDcreate')
    {
      var d = new Date();
      let didName="DIDgroup_"+d.getTime()+"";
      this.DIDGroup.groupName=didName+""
      this.initForm(this.DIDGroup);
    }
    if(this._router.snapshot.url[0].path =='DIDedit')
    {
      this.inEditPage = true;
      this.DidGroupId=this._router.snapshot.params["id"];
      this.DIDService.getDIDgroupDetailById(this.DidGroupId).subscribe(data=>{
       
        this.DIDGroup=data;
        this.DIDGroup.appName=data["app"].id;
        this.initForm(this.DIDGroup);
      })
    }
      
  }

  initForm(didGroup:DIDGroupDetails) {
   
    this.DidGroupForm = this.formBuilder.group({
      
      groupName: [didGroup.groupName, Validators.required],
      appId: [didGroup.appName, Validators.required],
      description: [didGroup.description,],
     
    });
  }  
   // convenience getter for easy access to form fields
   get f() { return this.DidGroupForm.controls; }  
   onSubmit()
   {

    if(this.DidGroupForm.valid){
 
      if(this.inEditPage)
      {
       
        this.UpdateDIDGroupDetails(new DIDGroupDetails(this.DidGroupForm.value));       
      }
      else
      {
        this.SaveDIDGroupDetails(new DIDGroupDetails(this.DidGroupForm.value));       
      }
    }
    else{
      this._snackBar.open("Fill input fields", "", {
        duration: 2000,
        verticalPosition: "bottom",
        horizontalPosition:'right',
        panelClass: ['success'],
    });
       
    }
    }
    SaveDIDGroupDetails(DIDGroup:DIDGroupDetails)
    {
      DIDGroup.appId= DIDGroup.appId+"";
  
this.DIDService.SaveDIDgroup(DIDGroup).subscribe(data=>{
  this._snackBar.open("DIDgroup saved successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
   
  this.router.navigateByUrl('/dialogbuilder/didgroup');
},(error)=>{
 
})
    }
    UpdateDIDGroupDetails(DIDGroup:DIDGroupDetails)
    { DIDGroup.appId= DIDGroup.appId+"";
    DIDGroup.id=this.DIDGroup.id;
     
      this.DIDService.updateDIDgroupDetail(DIDGroup).subscribe(data=>{
        this._snackBar.open("DIDgroup updated successfully!", "", {
          duration: 2000,
          verticalPosition: "bottom",
          horizontalPosition:'right',
          panelClass: ['success'],
      });
     
        this.router.navigateByUrl('/dialogbuilder/didgroup');
      },(error)=>{
        
      })
    }
    GetDIDnumberByID()
    {
     
      this.router.navigate(['dialogbuilder/DIDnumber',this.DIDGroup.id]);
     
    }
omit_special_char(event)
{   
   var k;  
   k = event.charCode;  //k = event.keyCode; (Both can be used)

 return((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8  || (k >= 48 && k <= 57)); 
}
}
interface Appname {
  id:string;
  appName: string;
}
