import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import{DataServiceService} from '../Shared/Services/data-service.service';
import {LoginService} from '../Shared/Services/login.service';
import {MatSnackBar} from '@angular/material/snack-bar';
import {LoggedInUser} from '../Shared/Models/loginModel';
@Component({
  selector: 'app-loginbuilder',
  templateUrl: './loginbuilder.component.html',
  styleUrls: ['./loginbuilder.component.css']
})
export class LoginbuilderComponent implements OnInit {
  loginForm: FormGroup;
  loggedIn:LoggedInUser;
  userAlredyExist:boolean=false;
  userMessage:string;
  userRole:string;
  hide = true;
  constructor(private formBuilder: FormBuilder,private router: Router,
    private dataService: DataServiceService,private loginService:LoginService,private _snackBar: MatSnackBar,) { }

  ngOnInit() {
    console.log("login details")
    this.loggedIn={
      username:'',
      password:'',

    }
    this.initForm(this.loggedIn);
  }
  initForm(loggin:LoggedInUser) {

    this.loginForm = this.formBuilder.group({
      username: [loggin.username, Validators.required],
      password: [loggin.password, [Validators.required, Validators.minLength(1)]],

    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.loginForm.controls; }
  getLoginDetails() {
    return this.loginForm.value;
  }
  login() {
    if(this.loginForm.valid) {
      console.log(this.loginForm.value.UserName);
      console.log(this.loginForm.value.Password);

  // this.router.navigate(['dialogbuilder/didgroup'])
    }
}
onSubmit()
{

  if(this.loginForm.valid) {
this.SaveLoginDetails(new LoggedInUser(this.loginForm.value))
  }
}
SaveLoginDetails(loggedIn:LoggedInUser)
{
  console.log("login details")
  console.log(loggedIn)
this.loginService.SaveLogin(loggedIn).subscribe(data=>{
console.log("login response data")
  console.log(data)
  // console.log(data["user"])
  // console.log(data["user"].role)
  console.log(data["message"]);
  if(data["message"] =="invalid user")
  { this.userAlredyExist=true
 this.userMessage="Invalid user!";
  }
  else{
    this.userAlredyExist=false
    if("undefined" != data["user"].role)
    this.userRole=data["user"].role
    
    if(this.userRole =="User")
    this.router.navigate(['dialogbuilder/apps'])
    else
    this.router.navigate(['dialogbuilder/didgroup'])
  
    localStorage.setItem('currentUser', JSON.stringify(data));
  }


}
,(error)=>{
  if (error.status === 500)
 {
  this._snackBar.open("Backend failure!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['error'],
});
 }
 else{
  this._snackBar.open("Server not connected!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['error'],
});
 }
},

)
}
}
