import { Injectable } from '@angular/core';
import { Observable,throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import {HttpClient, HttpErrorResponse} from '@angular/common/http'; 
import {StatePromptDetail} from '../Models/statePromptModel';
import {PromptGroupDetail} from '../Models/promptsGroupModel';
import {DialogConfigServiceService} from '../Configurations/dialog-config-service.service';
import {NextStateDetail} from '../Models/nextStateModel';

@Injectable({
  providedIn: 'root'
})
export class StatePromptService {
  Url :string; 
  header : any;  
  errorMessage:string;
  constructor(private http : HttpClient,config:DialogConfigServiceService) {
    this.Url = config.getConfig().apiBaseUrl;
   }
   getStatePrompt(id:string) : Observable<StatePromptDetail[]> 
   {
    return this.http.get<StatePromptDetail[]>(this.Url+ '/dialogbuilder-ivr/api/app/state/'+id) 
    .pipe(
      retry(1),
    
    );
   }
   getStateAnnouncement(appId:string) : Observable<StatePromptDetail[]> 
   {
    return this.http.get<StatePromptDetail[]>(this.Url+ '/dialogbuilder-ivr/api/app/announcement/all/'+appId) 
    .pipe(
      retry(1),
    
    );
   }
   getGroupPrompt(id:string) : Observable<PromptGroupDetail[]> 
   {
    return this.http.get<PromptGroupDetail[]>(this.Url+ '/dialogbuilder-ivr/api/app/prompts/'+id) 
    .pipe(
      retry(1),
    
    );
   }
   getStatePromptById(id: string) :Observable<StatePromptDetail> 
   {
    return this.http.get<StatePromptDetail>(this.Url+ '/dialogbuilder-ivr/api/state/prompt/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveStatePrompt(id:string,data:StatePromptDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/state/prompt/'+id, data) 
    .pipe(
      retry(1),
    );
   }
   updateStatePrompt(appId:string,stateID:string,update:StatePromptDetail)
   {
     return this.http.put(this.Url +'/dialogbuilder-ivr/api/state/prompt/'+appId+'/'+stateID,update)
     .pipe(
      retry(1),
   
    );
   }
   updatePromptStatus(stateId:string,data:any)
   {
     return this.http.put(this.Url +'/dialogbuilder-ivr/api/app/announcement/status/'+stateId,data)
     .pipe(
      retry(1),
   
    );
   }
   deleteStatePrompt(stateId: number,stateType:string)
   {
     if(stateType=="prompt")
     return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/state/prompt/'+stateId)
     else if(stateType.toLocaleLowerCase()=="input")
     {
      return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/state/input/'+stateId)
     }
     else if(stateType.toLocaleLowerCase()=="menu")
     {
      return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/state/menu/'+stateId)
     }
     else if(stateType.toLocaleLowerCase()=="branching")
     {
      return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/state/branch/'+stateId)
     }
     else if(stateType.toLocaleLowerCase()=="transfer")
     {
      return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/state/transfer/'+stateId)
     }
     else if(stateType.toLocaleLowerCase()=="nonbackend")
     {
      return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/state/nonbackend/'+stateId)
     }
     else if(stateType.toLocaleLowerCase()=="hoursofoperation")
     {
      return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/state/hoursofoperation/'+stateId)
     }
     else if(stateType.toLocaleLowerCase()=="holiday")
     {
      return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/state/holiday/'+stateId)
     }
   }
   getNextStateById(id: string) :Observable<NextStateDetail> 
   {
    return this.http.get<NextStateDetail>(this.Url+ '/dialogbuilder-ivr/api/prompt/nextstate/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveNextState(stateId:string,stateType:string,data:NextStateDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/'+stateType+'/nextstate/'+stateId, data) 
    .pipe(
      retry(1),
    );
   }
   SaveNextStatePrompt(stateId:string,data:NextStateDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/prompt/nextstate/'+stateId, data) 
    .pipe(
      retry(1),
    );
   }
   SaveInitState(id:string,data:NextStateDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/app/initstate/'+id, data) 
    .pipe(
      retry(1),
    );
   }
   getStateInitById(id: string) :Observable<NextStateDetail> 
   {
    return this.http.get<NextStateDetail>(this.Url+ '/dialogbuilder-ivr/api/app/initstate/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveNextStateInput(id:string,data:NextStateDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/input/nextstate/'+id, data) 
    .pipe(
      retry(1),
    );
   }
   getStateInputById(id: string) :Observable<NextStateDetail> 
   {
    return this.http.get<NextStateDetail>(this.Url+ '/dialogbuilder-ivr/api/input/nextstate/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveNextStateTOD(id:string,data:NextStateDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/branch/nextstate/todoption/'+id, data) 
    .pipe(
      retry(1),
    );
   }
   getStateTODById(id: string) :Observable<NextStateDetail> 
   {
    return this.http.get<NextStateDetail>(this.Url+ '/dialogbuilder-ivr/api/branch/nextstate/todoption/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveNextStateMenu(id:string,data:NextStateDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/menu/nextstate/'+id, data) 
    .pipe(
      retry(1),
    );
   }
   getStateMenuById(id: string) :Observable<NextStateDetail> 
   {
    return this.http.get<NextStateDetail>(this.Url+ '/dialogbuilder-ivr/api/menu/nextstate/'+id)
    .pipe(
      retry(1),
    );
   }
  
  
   SaveNextStateDOW(id:string,data:NextStateDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/branch/nextstate/dowoption/'+id, data) 
    .pipe(
      retry(1),
    );
   }
   getStateDOWById(id: string) :Observable<NextStateDetail> 
   {
    return this.http.get<NextStateDetail>(this.Url+ '/dialogbuilder-ivr/api/branch/nextstate/dowoption/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveNextStateHOD(id:string,data:NextStateDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/branch/nextstate/hodoption/'+id, data) 
    .pipe(
      retry(1),
    );
   }
   getStateHODById(id: string) :Observable<NextStateDetail> 
   {
    return this.http.get<NextStateDetail>(this.Url+ '/dialogbuilder-ivr/api/branch/nextstate/hodoption/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveNextStateNone(id:string,data:NextStateDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/branch/nextstate/'+id, data) 
    .pipe(
      retry(1),
    );
   }
   getStateNoneById(id: string) :Observable<NextStateDetail> 
   {
    return this.http.get<NextStateDetail>(this.Url+ '/dialogbuilder-ivr/api/branch/nextstate/'+id)
    .pipe(
      retry(1),
    );
   }
   getAllStateName(appId:string,type:string) : Observable<stateName[]> 
   {
    return this.http.get<stateName[]>(this.Url+ '/dialogbuilder-ivr/api/nextstate/'+type+'/'+appId) 
    .pipe(
      retry(1),
    
    );
   }
}
export class stateName{
  id:string;
  stateName:string;

}