import { Injectable } from '@angular/core';
import { Observable,throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import {HttpClient, HttpErrorResponse} from '@angular/common/http'; 
import {EnvironmentDetail} from '../Models/environmentModel';
import {DialogConfigServiceService} from '../Configurations/dialog-config-service.service';
@Injectable({
  providedIn: 'root'
})
export class EnvironmentService {
  Url :string; 
  header : any;  
  errorMessage:string;
  constructor(private http : HttpClient,config:DialogConfigServiceService) {
    this.Url = config.getConfig().apiBaseUrl;
   }
   getAllenvironment() : Observable<EnvironmentDetail[]> 
   {
    return this.http.get<EnvironmentDetail[]>(this.Url+ '/dialogbuilder-ivr/api/env/') 
    .pipe(
      retry(1),
    );
   }
   getEnvironmentById(id: string) :Observable<EnvironmentDetail> 
   {
    return this.http.get<EnvironmentDetail>(this.Url+ '/dialogbuilder-ivr/api/env/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveEnvironment(data:EnvironmentDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api/env/', data) 
    .pipe(
      retry(1),
    );
   }
   updateEnvironment(update:EnvironmentDetail)
   {
     return this.http.put(this.Url +'/dialogbuilder-ivr/api/env',update)
     .pipe(
      retry(1),
    );
   }
   deleteEnvironment(id: number)
   {
     return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/env/'+id)
     .pipe(
      retry(1),
    );
   }
  
}
