import { Injectable } from '@angular/core';
import {HolidayDetail} from '../Models/holidayModel';
import { Observable,throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import {HttpClient, HttpErrorResponse} from '@angular/common/http';
import {DialogConfigServiceService} from '../Configurations/dialog-config-service.service';
@Injectable({
  providedIn: 'root'
})
export class HolidayService {
  Url :string; 
  header : any;  
  errorMessage:string;
  constructor(private http : HttpClient,config:DialogConfigServiceService) {
    this.Url = config.getConfig().apiBaseUrl;
   }
   getAllHolday(appId) : Observable<HolidayDetail[]> 
   {
    return this.http.get<HolidayDetail[]>(this.Url+ '/dialogbuilder-ivr/api/app/holidays/'+appId) 
    .pipe(
      retry(1),
    );
   }
   getHolidayById(id: string) :Observable<HolidayDetail> 
   {
    return this.http.get<HolidayDetail>(this.Url+ '/dialogbuilder-ivr/api/app/holiday/'+id)
    .pipe(
      retry(1),
    );
   }
   getHolidayName(id: string) :Observable<holiday[]> 
   {
    return this.http.get<holiday[]>(this.Url+ '/dialogbuilder-ivr/api/app/holiday/names/'+id)
    .pipe(
      retry(1),
    );
   }
   SaveHoliday(appId:string,data:HolidayDetail)  
   { 
    return this.http.post(this.Url + '/dialogbuilder-ivr/api//app/holiday/'+appId, data) 
    .pipe(
      retry(1),
    );
   }
   updateHoliday(appId:string,update:HolidayDetail)
   {
     return this.http.put(this.Url +'/dialogbuilder-ivr/api/app/holiday/',update)
     .pipe(
      retry(1),
    );
   }
   deleteHoliday(id: number)
   {
     return this.http.delete(this.Url+ '/dialogbuilder-ivr/api/app/holiday/'+id)
     .pipe(
      retry(1),
    );
   }
}
export class holiday{
  id:string;
  holidayName:string;
}