import { Component ,ViewChild,OnInit,EventEmitter,Output} from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import { ActivatedRoute,Router } from '@angular/router';
import {DIDgroupService} from '../Shared/Services/didgroup.service';
import{DataServiceService} from '../Shared/Services/data-service.service';
import {DIDGroupDetails} from '../Shared//Models/DIDgroupModel';
import {PassDataService} from '../Shared/Services/pass-data.service';
import {MatSnackBar} from '@angular/material/snack-bar';
import {DeleteConfirmPopupComponent} from 'src/app/delete-confirm-popup/delete-confirm-popup.component';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
@Component({
  selector: 'app-create-didnumber-details',
  templateUrl: './create-didnumber-details.component.html',
  styleUrls: ['./create-didnumber-details.component.css']
})
export class CreateDIDnumberDetailsComponent implements OnInit {
  isRecordFound = false;
  isConfirm:boolean;
  didGroup:DIDGroupDetails
  displayedColumns: string[] = ['didNumber', 'description','Actions'];
  dataSource = new MatTableDataSource(); 
  @ViewChild(MatPaginator) paginator: MatPaginator;
  DidGroupId:string;
  DidGroupName:string;
  constructor(private router: Router,private _router: ActivatedRoute,private didGroupService:DIDgroupService,private dataService:DataServiceService,private passDataService:PassDataService,private _snackBar: MatSnackBar,public dialog: MatDialog,) {

   }

  ngOnInit(): void {
    this.dataSource.paginator = this.paginator;

    if(this._router.snapshot.url[0].path =='DIDnumber')
    {
this.DidGroupId=this._router.snapshot.params["id"];
this.didGroupService.getDIDgroupDetailById(this.DidGroupId).subscribe(data=>{
this.didGroup=data
this.dataSource.data=data["didNumbers"]
  this.DidGroupName=this.didGroup.groupName;
})
    }
  }
  GetDIDNumberByID(id){
    
    this.router.navigate(['dialogbuilder/DIDnumberEdit/',{didgroup:this.DidGroupId,didnumber:id}]);
  }
  deletePopUp(DidGroupId){
    const dialogRef = this.dialog.open(DeleteConfirmPopupComponent, {
      width: '450px'
    });

    dialogRef.afterClosed().subscribe(result => {
   
      this.isConfirm=result;
       if(this.isConfirm)
      this.deleteDidGroup(DidGroupId)
    });
  }
  deleteDidGroup(DidNumberId){
this.didGroupService.deleteDIDNumber(DidNumberId).subscribe(data=>{

this.didGroupService.getDIDgroupDetailById(this.DidGroupId).subscribe(data=>{
  this.didGroup=data
  this.dataSource.data=data["didNumbers"]
  this.isRecordFound = true;
    this.DidGroupName=this.didGroup.groupName;
  })
this._snackBar.open("DID number deleted!", "", {
  duration: 2000,
  verticalPosition: "bottom",
  horizontalPosition:'right',
  panelClass: ['success'],
});
})
  }
}
