import { Component, OnInit ,Inject} from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {MatSnackBar} from '@angular/material/snack-bar';
import {AppsService} from '../Shared/Services/apps.service';
import {VariableApps} from '../Shared/Models/variablesAppsModel';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
@Component({
  selector: 'app-app-variable',
  templateUrl: './app-variable.component.html',
  styleUrls: ['./app-variable.component.css']
})
export class AppVariableComponent implements OnInit {
  appsVariable:VariableApps
  appVariableForm: FormGroup;
  inEditPage: boolean;
  id:string;
  appId:string;
  appVariableType:string;
  appVariableId:string;
  isgetCti:boolean=false;
  isputCti:boolean=false;
  constructor(private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,private _snackBar: MatSnackBar,private appService:AppsService,
    public dialogRef: MatDialogRef<AppVariableComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) { }
  ngOnInit() {
    
    console.log("data")
    console.log(this.data)
    console.log(this.data["appId"])
    this.appId=this.data["appId"];
    console.log(this.appId)
this.appVariableType=this.data["appVariableType"];
this.appVariableId=this.data["appVariableId"];
this.appsVariable={
  key:'',
  value:'',
  getCti:'',
  putCti:'',
  id:''
}
    this.initForm(this.appsVariable);
    if(this.appVariableType=="edit")
    {
      this.inEditPage=true
      this.appService.getAppsVariableById(this.appVariableId).subscribe(data=>{
      
        this.initForm(data);
      })
    }
  }
  initForm(appsVariable:VariableApps) {
   
    this.appVariableForm = this.formBuilder.group({
      
      key: [appsVariable.key, Validators.required],
      value: [appsVariable.value, Validators.required],
      getCti: [appsVariable.getCti],
      putCti: [appsVariable.putCti ],
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.appVariableForm.controls; }  
  onSubmit()
   {

    if(this.appVariableForm.valid){
 
    if(this.appVariableType=="create")
        this.SaveAppVariable(new VariableApps(this.appVariableForm.value));       
  else if(this.appVariableType=="edit")
  this.UpdateAppVariable(new VariableApps(this.appVariableForm.value));
    }

    }
    SaveAppVariable(appVariable:VariableApps)
    {
console.log("save app variable")
console.log(appVariable)
 

if(appVariable.getCti =="")
appVariable.getCti="false"
if(appVariable.putCti =="")
appVariable.putCti="false"
appVariable.getCti=appVariable.getCti+"";
appVariable.putCti=appVariable.putCti+"";
console.log(appVariable)
this.appService.SaveAppsVariable(this.appId,appVariable).subscribe(data=>{
  this.dialogRef.close({event:"saved"});
  this.router.navigateByUrl('/dialogbuilder/apps');
  this._snackBar.open("AppsVariable saved successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
   
  this.router.navigateByUrl('/dialogbuilder/apps');
},(error)=>{
 
})
    }
    UpdateAppVariable(appVariable:VariableApps)
    {
      console.log("update app variableS")
      console.log(appVariable)
appVariable.id=this.appVariableId+"";
if(appVariable.getCti =="")
appVariable.getCti="false"
if(appVariable.putCti =="")
appVariable.putCti="false"
appVariable.getCti=appVariable.getCti+"";
appVariable.putCti=appVariable.putCti+"";
console.log(appVariable)
this.appService.updateAppsVariable(appVariable).subscribe(data=>{
  this.dialogRef.close({event:"saved"});
  this.router.navigateByUrl('/dialogbuilder/apps');
  this._snackBar.open("AppsVariable update successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
})
    }
    onNoClick(): void {
      this.dialogRef.close();
    }
}

export interface DialogData {
  appId:string;
  appVariableId: string;
  appVariableType: string;
}