import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import { PasswordValidation } from './../Shared/helpers/password-validator';
import {MatSnackBar} from '@angular/material/snack-bar';
import {UserDetails} from '../Shared/Models/userProfileModel';
import {UserProfileService} from '../Shared/Services/user-profile.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  UserRoles: UserRole[] = [
    {value: 'User', viewValue: 'User'},
    {value: 'Admin', viewValue: 'Admin'},
    {value: 'SuperAdmin', viewValue: 'Super Admin'},
 
  ];
  userBased:boolean=true;
  UserId:string
  UserForm: FormGroup;
  inEditPage: boolean;
  userDetail:UserDetails;
  userRole:string;
  hide = true;
  constructor(private  userService :UserProfileService,private _snackBar: MatSnackBar, private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,) { }

  ngOnInit(){
    this.userDetail={
      id:',',
      firstname:'',
      description:'',
      lastname:'',
      username:'',
      password:'',
      confirmpassword:'',
      role:'',
      phonenumber:'',
      email:''
    }
    var currentUser = JSON.parse(localStorage.getItem('currentUser'));
var token = currentUser.user;
 this.userRole=token.role;
 if(this.userRole =="User")
 this.userBased=false;
    this.initForm(this.userDetail)
    if(this._router.snapshot.url[0].path =='editprofile')
    {
      this.inEditPage = true;
      this.UserId=this._router.snapshot.params["id"];
      this.userService.getUserProfileDetailById(this.UserId).subscribe(data=>{
        console.log(data)
     this.userDetail=data;
     this.userDetail.confirmpassword=this.userDetail.password
     this.initForm(this.userDetail)
      })
           
    }
   
  }
  initForm(userDetail:UserDetails) {
   
    this.UserForm = this.formBuilder.group({
      firstname: [userDetail.firstname, Validators.required],
      lastname: [userDetail.lastname, Validators.required],
      password: [userDetail.password, Validators.required,],
      username: [userDetail.username, Validators.required],
      confirmpassword: [userDetail.confirmpassword, Validators.required],
      phonenumber:[userDetail.phonenumber, Validators.required],
      email:[userDetail.email, [Validators.required, Validators.email,Validators.pattern('^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$')]],
      role: [userDetail.role, Validators.required],
      description: [userDetail.description],
    },
    {
      validator: PasswordValidation.MatchPassword
  }
    );
  }
  // convenience getter for easy access to form fields
  get f() { return this.UserForm.controls; }  
  onSubmit()
  {

   if(this.UserForm.valid){

     if(this.inEditPage)
     {
      
       this.UpdateUserDetails(new UserDetails(this.UserForm.value));       
     }
     else
     {
       this.SaveUserDetails(new UserDetails(this.UserForm.value));       
     }
   }
   }
   SaveUserDetails(userDetail:UserDetails)
   {
     console.log(userDetail)
      this.userService.saveUserProfile(userDetail).subscribe(data=>{
        this._snackBar.open("User saved uccessfully!", "", {
          duration: 2000,
          verticalPosition: "bottom",
          horizontalPosition:'right',
          panelClass: ['success'],
      });
        this.router.navigate(['dialogbuilder/profiledetails']);
      })
   }
   UpdateUserDetails(userDetail:UserDetails)
   {
     userDetail.phonenumber=userDetail.phonenumber+"";
     console.log(userDetail.phonenumber)
     userDetail.id=this.UserId;
     userDetail.confirmpassword=userDetail.password;
   
this.userService.updateUserProfile(userDetail).subscribe(data=>{

  this._snackBar.open("User updated successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});

if(this.userRole !="User")
{
  this.userBased=false;
  this.router.navigate(['dialogbuilder/profiledetails']);
}


})
   }
  omit_special_char(event)
{   
   var k;  
   k = event.charCode;  //k = event.keyCode; (Both can be used)

 return((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8  || (k >= 48 && k <= 57)); 
}
}
interface UserRole {
  value:string;
  viewValue: string;
}
