import { Component, OnInit,Inject ,ViewChild} from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
export interface DialogData {

}

@Component({
  selector: 'app-import-export-popup',
  templateUrl: './import-export-popup.component.html',
  styleUrls: ['./import-export-popup.component.css']
})
export class ImportExportPopupComponent implements OnInit {
  formGroup: FormGroup;
  constructor(private formBuilder: FormBuilder,  public dialogRef: MatDialogRef<ImportExportPopupComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) { }

  ngOnInit() {
    this.createForm();
  }
  onNoClick(): void {
    this.dialogRef.close();
  }
  createForm() {
   
    this.formGroup = this.formBuilder.group({
      
      DatabaseType: [null, Validators.required],
      HostName: [null, Validators.required],
      Port: [null, Validators.required],
      UserName: [null, Validators.required],
      Password: [null, Validators.required],
      ServerName: [null, Validators.required],
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.formGroup.controls; } 
}
