import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {DIDNumberDetails} from '../Shared/Models/didNumberModel';
import {DIDgroupService} from '../Shared/Services/didgroup.service';

import{DataServiceService} from '../Shared/Services/data-service.service';
import {DIDGroupDetails} from '../Shared/Models/DIDgroupModel';
import {PassDataService} from '../Shared/Services/pass-data.service';
import {MatSnackBar} from '@angular/material/snack-bar';
@Component({
  selector: 'app-create-didnumber',
  templateUrl: './create-didnumber.component.html',
  styleUrls: ['./create-didnumber.component.css']
})
export class CreateDIDnumberComponent implements OnInit {
  receivedChildMessage: string;
  didNumberForm: FormGroup;
  didNumberId:string;
  inEditPage: boolean;
  didNumber:DIDNumberDetails;
  didGroup:DIDGroupDetails
  didGroupIdEdit:string;
  didGroupId:string;
  constructor(private  DIDService:DIDgroupService,private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,private _snackBar: MatSnackBar,private didService:DataServiceService,private passDataService:PassDataService) { }

  ngOnInit() {
    this.passDataService.currentMessage.subscribe(
      
      message=> this.didGroupIdEdit = message
    
      )
      console.log("did group id")
      console.log(this.didGroupIdEdit);
    this.didNumber={
      id:'',
      didGroupId:'',
      groupNumber:'',
      description:''
    }
    if(this._router.snapshot.url[0].path =='DIDnumberCreate')
    {
      this.didGroupId=this._router.snapshot.params["id"];
    }
    this.initForm( this.didNumber);
    if(this._router.snapshot.url[0].path =='DIDnumberEdit')
    {
      this.inEditPage = true;
     this.didNumberId= this._router.snapshot.params["didnumber"];
     this.didGroupId=this._router.snapshot.params["didgroup"];
     console.log("didnumber : ", this.didNumberId)
  
      this.DIDService.getDIDNumberDetailById(this.didNumberId).subscribe(data=>{
      
        this.didNumber=data
     console.log(data["didNumber"])
     this.didNumber.groupNumber=data["didNumber"];
        this.initForm( this.didNumber);
      })
    }
   
  }
initForm( didNumber:DIDNumberDetails) {
   
    this.didNumberForm = this.formBuilder.group({
      
      groupNumber: [didNumber.groupNumber, Validators.required],
      description: [didNumber.description,],
    
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.didNumberForm.controls; } 
  onSubmit()
  {

   if(this.didNumberForm.valid){
    
     if(this.inEditPage)
     {
      
       this.UpdateDIDNumberDetails(new DIDNumberDetails(this.didNumberForm.value));       
     }
     else
     {
       this.SaveDIDNumberDetails(new DIDNumberDetails(this.didNumberForm.value));       
     }
   }
   }
   SaveDIDNumberDetails(didNumber:DIDNumberDetails)
   {

     didNumber.didGroupId= this.didGroupId+"";
 
        this.DIDService.SaveDIDNumber(didNumber).subscribe(data=>{
          this._snackBar.open("DID number saved successfully!", "", {
            duration: 2000,
            verticalPosition: "bottom",
            horizontalPosition:'right',
            panelClass: ['success'],
        });
          this.router.navigate(['dialogbuilder/DIDnumber',didNumber.didGroupId]);
        
        },(error)=>{
          
        
        }) 
    
   }
   UpdateDIDNumberDetails(didNumber:DIDNumberDetails){
    didNumber.didGroupId= this.didGroupId+"";
    didNumber.id=this.didNumber.id+"";
    didNumber.groupNumber=didNumber.groupNumber+"";
    console.log(didNumber)
        this.DIDService.updateDIDNumber(didNumber).subscribe(data=>{
          this._snackBar.open("DID number saved successfully!", "", {
            duration: 2000,
            verticalPosition: "bottom",
            horizontalPosition:'right',
            panelClass: ['success'],
        });
          this.router.navigate(['dialogbuilder/DIDnumber',didNumber.didGroupId]);
        },(error)=>{
         
        })
   }
  omit_special_char(event)
  {   
     var k;  
     k = event.charCode;  //k = event.keyCode; (Both can be used)
  
   return((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8  || (k >= 48 && k <= 57)); 
  }

}
