 import {NextStatePopupComponent} from 'src/app/next-state-popup/next-state-popup.component';
@Component({
  selector: 'app-state-option-detail',
  templateUrl: './state-option-detail.component.html',
  styleUrls: ['./state-option-detail.component.css']
})
export class StateOptionDetailComponent implements OnInit {
  disableOption:boolean=true
  inEditPage:boolean
  disablebyInput:boolean=false
  appId:string;
  type:string;
  branchType:string;
  appName:string;
  isConfirm:string;
  stateId:string;
  displayedColumns: string[] = ['Option','Nextstate','Actions'];
  dataSource = new MatTableDataSource();
  Appnames: Appname[] = [
     {value: '1', viewValue: 'PTL-Demo'},
     {value: '2', viewValue: 'PTL-Demo1'},
     {value: '3', viewValue: 'PTL-Demo2'},
     {value: '3', viewValue: 'PTL-Demo2'},

   ];
   DefaultOptions:string[]=[
     'NoInput','NoMatch','Error','Success','Failure','Default' ]
   @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;

  constructor(private _snackBar: MatSnackBar,private optionService:OptionService, private router: Router,private _router: ActivatedRoute,public dialog: MatDialog,private branchOptionsService:BranchOptionsService) {
   }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }


  ngOnInit(){
    this.disablebyInput=false
    this.dataSource.paginator = this.paginator;
    if(this._router.snapshot.url[0].path =='nextState')
    {
      this.appId=this._router.snapshot.params["appId"];
      this.stateId=this._router.snapshot.params["stateId"];
      this.appName=this._router.snapshot.params["appName"];
      this.type=this._router.snapshot.params["type"];
      this.branchType=this._router.snapshot.params["branchType"];
      console.log("branch type")
      console.log(this.branchType)
      if(this.type.toLowerCase()=="branching" && this.branchType.toLowerCase() == "day of week")
      {
this.displayedColumns=['Option','fromWeek','toWeek','Nextstate','Actions']
this.getDayofWeek()
      }
      if(this.type.toLowerCase()=="branching" &&  this.branchType.toLowerCase() == "time of day")
      {
        console.log("time of day")
        this.displayedColumns=['Option','fromDay','toDay','Nextstate','Actions']
        this.getTimeofDay()
      }
      if(this.type.toLowerCase()=="branching" && this.branchType.toLowerCase() == "none" )
      {
this.displayedColumns=['Option','conditionKey','conditionValue','Nextstate','Actions']
this.branchOptionsService.getAllBranchOptionNone(this.stateId).subscribe(data=>{
  console.log("getall branchnone ")
  console.log(data)
  this.dataSource.data=data["options"];
})
      }
      if(this.type.toLowerCase()=="branching" && this.branchType.toLowerCase() == "holiday" )
      {
this.displayedColumns=['Option','holidayName','date','Nextstate','Actions']
this.getHoliday()
      }
      else if( this.type=="menu")
      {

        this.displayedColumns=  ['Option','Nextstate','Actions'];
      this.optionService.getAllStateMenuOption(this.stateId).subscribe(data=>{
console.log("getall ")
console.log(data)



this.dataSource.data=data;
      })
    }
    else if(this.type=="input")
    {
      this.disablebyInput=true
      this.displayedColumns=  ['Option','Nextstate','Actions'];
      this.optionService.getAllStateInputOption(this.stateId).subscribe(data=>{
        console.log("getall input")
console.log(data)
this.dataSource.data=data;
      })
    }
    else if(this.type=="nonbackend")
    {
    
      this.displayedColumns=  ['Option','Nextstate','Actions'];
      this.optionService.getAllStateBackendOption(this.stateId).subscribe(data=>{
        console.log("getall input")
console.log(data)
this.dataSource.data=data;
      })
    }
    }

    // if(this._router.snapshot.url[0].path =='nextState')
    // {
    //   this.inEditPage = true;
    //   this.appId=this._router.snapshot.params["appId"];
    //   this.appName= this._router.snapshot.params["appName"];
    //   this.stateId= this._router.snapshot.params["stateId"];
  
    // }
  }
  EditOptionDetailByID(id)
  {
    if(this.type.toLowerCase()=="input")
    {
        this.router.navigate(['dialogbuilder/editOption',{appId:this.appId,appName:this.appName,stateId:this.stateId,optionId:id,type:this.type,branchType:this.branchType}]);
    }
    else if(this.type.toLowerCase() =="branching" && this.branchType.toLowerCase()=="time of day")
    {
      this.router.navigate(['dialogbuilder/editBranchOptionTime',{appId:this.appId,appName:this.appName,stateId:this.stateId,optionId:id,type:this.type,branchType:this.branchType}]);
    }
    else if(this.type.toLowerCase() =="branching" && this.branchType.toLowerCase()=="day of week")
    {
      this.router.navigate(['dialogbuilder/editBranchOptionDay',{appId:this.appId,appName:this.appName,stateId:this.stateId,optionId:id,type:this.type,branchType:this.branchType}]);
    }
    else if(this.type.toLowerCase()=="menu")
    {
      this.router.navigate(['dialogbuilder/editMenuOption',{appId:this.appId,appName:this.appName,stateId:this.stateId,optionId:id,type:this.type}]);
    }
    else if(this.type.toLowerCase()=="nonbackend")
    {
      this.router.navigate(['dialogbuilder/editNonbackend',{appId:this.appId,appName:this.appName,stateId:this.stateId,optionId:id,type:this.type}]);
    }
    else if(this.type.toLowerCase() =="branching" && this.branchType.toLowerCase()=="none")
    {
      this.router.navigate(['dialogbuilder/editNone',{appId:this.appId,appName:this.appName,stateId:this.stateId,optionId:id,type:this.type,branchType:this.branchType}]);
    }
    else if(this.type.toLowerCase() =="branching" && this.branchType.toLowerCase()=="holiday")
    {
      this.router.navigate(['dialogbuilder/editBranchHoliday',{appId:this.appId,appName:this.appName,stateId:this.stateId,optionId:id,type:this.type,branchType:this.branchType}]);
    }
  }
  
  EditOptioinByID(id)
  {
    console.log("id : ", id)
    if(this.type=="branching" && (this.branchType == "Time of Day" || 
    this.branchType == "Day of Week"))
    { 
      this.router.navigate(['dialogbuilder/editBranchOptionDay',{appId:this.appId,appName:this.appName,stateId:this.stateId,optionId:id}]);
    }
  }
  backStateDetail()
  {
    console.log("appId"+this.appId)
    console.log("appId"+this.appName)
   this.router.navigate(['dialogbuilder/stateDetail',{appId:this.appId,appName:this.appName,stateId:this.stateId}]);
  }
  getNextState(element)
  {
    if( this.type=="menu")
    {
      this.nextStaatePopup(element.id,"stateMenu")
    }
    else if( this.type=="input")
    {
      this.nextStaatePopup(element.id,"stateInput")
    }
    else if( this.type.toLowerCase()=="nonBackend")
    {
      this.nextStaatePopup(element.id,"stateNonbackend")
    }
    else if(this.type.toLowerCase() =="branching" && this.branchType.toLowerCase()=="time of day")
    {
      this.nextStaatePopup(element.id,"TOD")
    }
    else if(this.type.toLowerCase() =="branching" && this.branchType.toLowerCase()=="day of week")
    {
      this.nextStaatePopup(element.id,"DOW")
    }
    else if(this.type.toLowerCase() =="branching" && this.branchType.toLowerCase()=="holiday")
    {
      this.nextStaatePopup(element.id,"HOD")
    }
    else if(this.type.toLowerCase() =="branching" && this.branchType.toLowerCase()=="none")
    {
      this.nextStaatePopup(element.id,"None")
    }
  }
  nextStaatePopup(id,statetype)
  {
    const dialogRef = this.dialog.open(NextStatePopupComponent, {
      width:'400px',
      data: {type: "create", appId:this.appId,state:statetype,optionId:id}
   });

   dialogRef.afterClosed().subscribe(result => {
   
    
   });
  }
  CreateOption()
  {
//    this.optionService.getStateOptionMode(this.stateId).subscribe(data=>{ 
//      console.log("input mode")
//      console.log(data)
//  })
if(this.type.toLowerCase()=="input")
{
    this.router.navigate(['dialogbuilder/createOption',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:this.type,branchtype:this.branchType}]);
}
else if(this.type.toLowerCase() =="branching" && this.branchType.toLowerCase()=="time of day")
{
  this.router.navigate(['dialogbuilder/createBranchOptionTime',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:this.type,branchType:this.branchType}]);
}
else if(this.type.toLowerCase() =="branching" && this.branchType.toLowerCase()=="day of week")
{
  this.router.navigate(['dialogbuilder/createBranchOptionDay',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:this.type,branchType:this.branchType}]);
}
else if(this.type.toLowerCase()=="menu")
{
  this.router.navigate(['dialogbuilder/createMenuOption',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:this.type,branchType:this.branchType}]);
}
else if(this.type.toLowerCase()=="nonbackend")
{
  this.router.navigate(['dialogbuilder/createNonbackend',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:this.type,branchType:this.branchType}]);
}
else if(this.type.toLowerCase() =="branching" && this.branchType.toLowerCase()=="none")
{
  this.router.navigate(['dialogbuilder/createNone',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:this.type,branchType:this.branchType}]);
}
else if(this.type.toLowerCase() =="branching" && this.branchType.toLowerCase()=="holiday")
{
  this.router.navigate(['dialogbuilder/createBranchHoliday',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:this.type,branchType:this.branchType}]);
}
  }
  deleteOptionByID(optionId)
  {
    const dialogRef = this.dialog.open(DeleteConfirmPopupComponent, {
      width: '450px'
    });
  
    dialogRef.afterClosed().subscribe(result => {
   
      this.isConfirm=result;
       if(this.isConfirm)
    {
      this.deleteOptions(optionId);
    }
    });
  
  }
  deleteOptions(optionId)
  {
    if(this.type.toLowerCase()=="branching" &&  this.branchType.toLowerCase() == "time of day")
    {
      console.log("time of day")
      this.branchOptionsService.deleteBranchOptionTOD(optionId).subscribe(data=>{
        this.getTimeofDay()
        this.deleteNotification();
      })
    }
    else if(this.type.toLowerCase()=="branching" && this.branchType.toLowerCase() == "day of week")
    {
      this.branchOptionsService.deleteBranchOptionDOW(optionId).subscribe(data=>{
        this.getDayofWeek();
        this.deleteNotification();
      })
    
    }
    else if(this.type.toLowerCase()=="branching" &&  this.branchType.toLowerCase() == "none")
    {
      this.branchOptionsService.deleteBranchOptionNone(optionId).subscribe(data=>{
        this.deleteNotification()
      })
      
    }
    else if(this.type.toLowerCase()=="branching" &&  this.branchType.toLowerCase() == "holiday")
    {
      this.branchOptionsService.deleteBranchOptionHoliday(optionId).subscribe(data=>{
        this.getHoliday()
        this.deleteNotification();

      })
      
    }
    else if(this.type.toLowerCase()=="branching" &&  this.branchType.toLowerCase() == "none")
    {
      this.branchOptionsService.deleteBranchOptionNone(optionId).subscribe(data=>{
        this.getHoliday()
        this.deleteNotification();

      })
      
    }
  }
  deleteNotification()
  {
    this._snackBar.open("Option deleted successfully!", "", {
      duration: 2000,
      verticalPosition: "bottom",
      horizontalPosition:'right',
      panelClass: ['success'],
    });
  }
  getTimeofDay()
  {
    this.branchOptionsService.getAllBranchOptionTODS(this.stateId).subscribe(data=>{
      console.log("getall branchoption ")
    console.log(data)
    this.dataSource.data=data["options"];
    })
  }
  getDayofWeek()
  {
    this.branchOptionsService.getAllBranchOptionDOW(this.stateId).subscribe(data=>{
      console.log("getall branchoption input")
      console.log(data)
      this.dataSource.data=data["options"];
    })
  }
  getHoliday()
  {
    this.branchOptionsService.getAllBranchOptionHoliday(this.stateId).subscribe(data=>{
      console.log("getall holiday ")
      console.log(data)
      this.dataSource.data=data["options"];
    })
  }
  getNone()
  {
    this.branchOptionsService.getAllBranchOptionNone(this.stateId).subscribe(data=>{
      console.log("getall holiday ")
      console.log(data)
      this.dataSource.data=data["options"];
    })
  }
}

 interface Appname {
   value: string;
   viewValue: string;
 }
 