import { Component, OnInit,ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import { ActivatedRoute,Router } from '@angular/router';
export interface PeriodicElement {
  Key: string;
  Value: string;
 
 }

 const ELEMENT_DATA: PeriodicElement[] = [
   { Key: '1234',Value:'ptl1'},

 ];
@Component({
  selector: 'app-db-transaction',
  templateUrl: './db-transaction.component.html',
  styleUrls: ['./db-transaction.component.css']
})
export class DbTransactionComponent implements OnInit {
  displayedColumns: string[] = ['Key','Value','Actions'];
  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
  inEditPage: boolean;
   @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;


  formGroup: FormGroup;
  constructor(private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,) { }

  ngOnInit() {
    if(this._router.snapshot.url[0].path =='dbtransactionEdit')
    {
      this.inEditPage = true;
    }
    this.createForm();
  }
  createForm() {
   
    this.formGroup = this.formBuilder.group({
      
      TransactionName: [null, Validators.required],
      Query: [null, Validators.required],
      InputKey: [null, Validators.required],
      InputValue: [null, Validators.required],
      OutputKey: [null, Validators.required],
      OutputValue: [null, Validators.required],
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.formGroup.controls; }  
}
