import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {BranchingOptionDetail} from '../Shared/Models/branchingOptionModel';
import {MatSnackBar} from '@angular/material/snack-bar';
import {BranchOptionsService} from '../Shared/Services/branch-options.service';

@Component({
  selector: 'app-branch-day',
  templateUrl: './branch-day.component.html',
  styleUrls: ['./branch-day.component.css']
})
export class BranchDayComponent implements OnInit {
  toppings = new FormControl();
  toppingList: string[] = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thurday', 'Friday','Saturday'];
  branchingOptionDetail:BranchingOptionDetail
  branchOptionDayForm: FormGroup;
  inEditPage: boolean;
  appId:string;
  stateId:string;
  appName:string;
  type:string;
  branchType:string;
  optionId:string;
  MethodTypes: MethodType[] = [
    {value: 'sunday', viewValue: 'Sunday'},
    {value: 'monday', viewValue: 'Monday'},
    {value: 'tuesday', viewValue: 'Tuesday'},
    {value: 'wednesday', viewValue: 'Wednesday'},
    {value: 'thursday', viewValue: 'Thursday'},
    {value: 'friday', viewValue: 'Friday'},
    {value: 'satturday', viewValue: 'Saturday'}
  ];
  constructor(private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,private _snackBar: MatSnackBar,private branchOptionsService:BranchOptionsService,) { }

  ngOnInit() {
    
    this.branchingOptionDetail={
      id:'',
      option:'',
      from:'',
      to:'',
      type:'',
      day:''
    }
    if(this._router.snapshot.url[0].path =='createBranchOptionDay')
    {
     
      this.appId=this._router.snapshot.params["appId"];
      this.stateId=this._router.snapshot.params["stateId"];
      this.appName=this._router.snapshot.params["appName"];
      this.type=this._router.snapshot.params["type"];
      this.optionId=this._router.snapshot.params["optionId"];
      this.branchType=this._router.snapshot.params["branchType"];
    }
    if(this._router.snapshot.url[0].path =='editBranchOptionDay')
    {
      this.inEditPage = true;
      this.appId=this._router.snapshot.params["appId"];
      this.stateId=this._router.snapshot.params["stateId"];
      this.appName=this._router.snapshot.params["appName"];
      this.type=this._router.snapshot.params["type"];
      this.optionId=this._router.snapshot.params["optionId"];
      this.branchType=this._router.snapshot.params["branchType"];
      console.log("this.branchType")
      console.log(this.branchType)
      this.branchOptionsService.getBranchOptionDOWById(this.optionId).subscribe(data=>{
        console.log("get update")
        console.log(data)
        this.branchingOptionDetail=data
        this.initForm(this.branchingOptionDetail);
      })
    }
    this.initForm(this.branchingOptionDetail);
  }
  initForm(branchingDay:BranchingOptionDetail) {
   
    this.branchOptionDayForm = this.formBuilder.group({
      option: [branchingDay.option, Validators.required],
      day: [branchingDay.day, Validators.required],
      from: [branchingDay.from, Validators.required],
      to: [branchingDay.to, Validators.required],
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.branchOptionDayForm.controls; }  
  onSubmit()
  {
console.log("from date")
console.log(  this.branchOptionDayForm.get('from').value)
   if(this.branchOptionDayForm.valid){
    
     if(this.inEditPage)
     {
      
       this.UpdateBranchOptionDay(new BranchingOptionDetail(this.branchOptionDayForm.value));       
     }
     else
     {
       this.SaveBranchOptionDay(new BranchingOptionDetail(this.branchOptionDayForm.value));       
     }
   }
   
   }
   SaveBranchOptionDay(branchingOptionDOW:BranchingOptionDetail)
   {
     branchingOptionDOW.from+"";
     branchingOptionDOW.to+"";
    branchingOptionDOW.day=branchingOptionDOW.day+""
     console.log(branchingOptionDOW)
      this.branchOptionsService.SaveBranchOptionDOW(this.stateId,branchingOptionDOW).subscribe(data=>{
        this._snackBar.open("Option saved successfully!", "", {
          duration: 2000,
          verticalPosition: "bottom",
          horizontalPosition:'right',
          panelClass: ['success'],
      });
      this.router.navigate(['dialogbuilder/nextState',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:"branching",branchType:this.branchType}])
      
      })
   }
   UpdateBranchOptionDay(branchingOptionDOW:BranchingOptionDetail)
   {
    branchingOptionDOW.from+"";
    branchingOptionDOW.to+"";
    branchingOptionDOW.day=branchingOptionDOW.day+""
    console.log(branchingOptionDOW)
this.branchOptionsService.updateBranchOptionDOW( this.branchingOptionDetail.id,branchingOptionDOW).subscribe(data=>{
  this._snackBar.open("Option updated successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
this.router.navigate(['dialogbuilder/nextState',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:"branching",branchType:this.branchType}])

})

   }
   backtoOption()
   {
     if(this.type.toLowerCase()== "branching")
     {
      console.log("branch type")
      console.log(this.branchType)
         this.router.navigate(['dialogbuilder/nextState',{appId:this.appId,appName:this.appName,stateId:this.stateId,type:"branching",branchType:this.branchType}])
 
     }
   }
}
interface MethodType {
  value: string;
  viewValue: string;
}