import { Component, OnInit ,Inject,ViewChild} from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {MatSnackBar} from '@angular/material/snack-bar';
import {AppsService} from '../Shared/Services/apps.service';
import { HttpClient, HttpResponse, HttpEventType } from '@angular/common/http';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import{RetryPromptDetail} from '../Shared/Models/reTryPromptModel';
import {GroupPromptService} from '../Shared/Services/group-prompt.service';
import {StatePromptDetail} from '../Shared/Models/statePromptModel';
import {StatePromptService} from '../Shared/Services/state-prompt.service';
import {UploadService} from '../Shared/Services/upload.service';
import { MatRadioChange } from '@angular/material/radio';
interface appGroup {

  name: string;
  apps: app[];
  
}
interface app {
  id: string;
  promptGroupName: string;
}
@Component({
  selector: 'app-retry-prompt',
  templateUrl: './retry-prompt.component.html',
  styleUrls: ['./retry-prompt.component.css']
})
export class RetryPromptComponent implements OnInit {
  audioName:string;
  isRadioPromptChange:boolean
  editBtn:boolean
  isradioChange:boolean
  showPromptName:boolean;
  showOptions:boolean
  audioURl:any;
AudioCollections: AType[] = []
showPrompts:boolean=false
  audio = new Audio();
Playstate:boolean=true;
  selectedAudio:string;
  selectedFiles: FileList
  currentFileUpload: File
  showprogress:boolean=false
  editAudio:boolean=false;
  showAudioOptions:boolean=false;
  progress: { percentage: number } = { percentage: 0 }
  @ViewChild("selectFile") selectFile;
  appGroups: appGroup[] = []
  appName:string
  retryPromptDetail:RetryPromptDetail
  Types: Type[] = [{id:'NoMatch',retryType:'No Match'},
  {id:'NoInput',retryType:'No Input'}];
  Prompts:Prompt[]=[]
  retryPromptForm: FormGroup;
  inEditPage: boolean;
  id:string;
  appId:string;
  appNameRetry:string;
  appVariableType
  retryId;
  promptUrl:string;
  match:string;
  retryname:string;
  promptName:string
  constructor(private uploadService: UploadService,private statePromptService:StatePromptService,private promptGroupService:GroupPromptService,private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,private _snackBar: MatSnackBar,private appService:AppsService,
    public dialogRef: MatDialogRef<RetryPromptComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) { }
    ngOnInit() {
      console.log("data")
      console.log(this.data)
      console.log(this.data["appId"])
      this.appId=this.data["appId"];
  this.appVariableType=this.data["appVariableType"];
  this.retryId=this.data["appVariableId"];
  this.appName=this.data["appNameRetry"]
  this.match=this.data["match"];
  this.retryname=this.data["retryName"]
 
  console.log("match")
  console.log(this.match)


 
  this.retryPromptDetail={
   retryName:'',
   retryType:'',
   id:'',
   prompt:'',
   promptname:''
  }
  this.retryPromptDetail.retryName=this.retryname;
  this.retryPromptDetail.retryType=this.match;
      this.initForm(this.retryPromptDetail);
      if(this.appVariableType=="create")
      {
        this.isRadioPromptChange=true
        this.showOptions=false
this.promptGroupNames();
      }
      if(this.appVariableType=="edit")
      {
        this.showPromptName=true
        this.editBtn=true
        this.isRadioPromptChange=false
  this.inEditPage=true
  this.promptGroupNames();
        this.appService.getRetryPromptById(this.retryId).subscribe(data=>{
          console.log("get retry data")
          console.log(data)
          this.retryPromptDetail=data
          this.promptName=this.retryPromptDetail.promptname
    this.audioURl= this.retryPromptDetail.prompt;
    this.promptUrl=this.retryPromptDetail.prompt;
    this.audio.src=this.audioURl;
    this.audio.load()
          console.log("prompt name :" + this.promptName)
          this.initForm( this.retryPromptDetail);
        })
      }
    }
    initForm(retryPrompt:RetryPromptDetail) {
     
      this.retryPromptForm = this.formBuilder.group({
        retryName: [retryPrompt.retryName, ],
        retryType: [retryPrompt.retryType, ],
        prompt: [retryPrompt.prompt,],
        choosePrompt:[retryPrompt.prompt,],
      });
    }
    // convenience getter for easy access to form fields
    get f() { return this.retryPromptForm.controls; }  
  
    saveRetryPrompt()
    {
      if(this.retryPromptForm.valid){
        this.SaveRetryPrompt(new RetryPromptDetail(this.retryPromptForm.value));
      }
    }
    updateRetryPrompt()
    {
      if(this.retryPromptForm.valid){
        this.UpdateRetryPrompt(new RetryPromptDetail(this.retryPromptForm.value));
      }
    }
    SaveRetryPrompt(retryPrompt:RetryPromptDetail)
    {
      retryPrompt.prompt=this.promptUrl+"";
      
      console.log("save re prompt")
      console.log(retryPrompt)
this.appService.SaveRetryPrompt(this.appId,retryPrompt).subscribe(data=>{
  this.dialogRef.close({event:data["type"]});
  this.router.navigateByUrl('/dialogbuilder/apps');
  this._snackBar.open("Retry prompt saved successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
})
    }
    UpdateRetryPrompt(retryPrompt:RetryPromptDetail)
    {
      retryPrompt.prompt=this.promptUrl+"";
      retryPrompt.id= this.retryId
      console.log("update vale ")
      console.log(retryPrompt)
this.appService.updateRetryPrompt(retryPrompt).subscribe(data=>{
  console.log("after update")
  console.log(data["type"])
  this.dialogRef.close({event:data["type"]});
  this.router.navigateByUrl('/dialogbuilder/apps');
  this._snackBar.open("Retry prompt updated successfully!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
})

    }
    promptGroupNames()
    {
      this.statePromptService.getGroupPrompt(this.appId).subscribe(data=>{
        this.Prompts=data["global"];
        console.log("data appname");
        console.log(data[this.appName])
        this.appGroups = [
          {
            name: 'Global',
            apps:data["global"]
          
          },
          {
            name:'Apps',
            apps: data[this.appNameRetry]
            
          }
        ];
       console.log("prompt group")
       console.log(this.appGroups)
      });
    }
    onNoClick(): void {
      this.dialogRef.close();
    }
    radioChange($event: MatRadioChange)
    {
      console.log( $event.value);
      const promptEvents =this.retryPromptForm.get('prompt')
     const choosePromptEvents =this.retryPromptForm.get('choosePrompt')
      
      if ( $event.value === 'Exists') 
      {
        this.isRadioPromptChange=false
        this.isradioChange=false
        this.showPrompts=true
        this.showOptions=true
        this.showPromptName=false
        this.showAudioOptions=true
       promptEvents.setValidators(Validators.required);
     choosePromptEvents.clearValidators();
        // this.showOptions=true
        this.uploadService.getUploadAudio(this.appName).subscribe(data=>{
          console.log("audio files")
          console.log(data)
  
          for(var k in data) {
  
            this.AudioCollections.push({'value':data[k],'viewValue':k})
          }
      
       
        })
      }
      else if($event.value === 'New')
      {
        this.isRadioPromptChange=true
         this.showAudioOptions=false
        this.isradioChange=true
        this.showPrompts=false
        this.showOptions=false
      promptEvents.clearValidators();
      choosePromptEvents.setValidators(Validators.required);
      }
     promptEvents.updateValueAndValidity();
     choosePromptEvents.updateValueAndValidity();
    }
    onAudioSelection(){
      this.audio.src=""
      console.log("selected audio name")
      console.log(this.selectedAudio)
      this.promptUrl=this.selectedAudio;
      this.audio.src = this.selectedAudio;
      this.audio.load();
  
    }
    selectFile2(event) {
      this.selectedFiles = event.target.files;
      this.upload() ;
    }
    upload() {
      this.progress.percentage = 0;
      this.showprogress=true
      this.currentFileUpload = this.selectedFiles.item(0)
      this.uploadService.pushFileToStorage(this.currentFileUpload,this.appName).subscribe(event => {
        console.log("response")
        console.log(event["body"])
        this.audioURl=event["body"]
      this.audio.src = this.audioURl;
      this.audio.load();
      this.showprogress=false
      this.promptUrl=this.audioURl;
        console.log("get audio")
        console.log(this.audioURl)
       
        if (event.type === HttpEventType.UploadProgress) {
          this.progress.percentage = Math.round(100 * event.loaded / event.total);
        } else if (event instanceof HttpResponse) {
          console.log('File is completely uploaded!');
    
        //   this.audioURl=event["body"]
        //  let audio = new Audio();
        //  audio.src =this.audioURl
        //  audio.load();
        //  audio.play();
        }
      })
   
      this.selectedFiles = undefined
    }
    play()
    {
      this.Playstate=false
      this.audio.play();
      this.audio.onended=()=>{
        this.Playstate=true
    
      }
    }
    pause()
    {
      this.Playstate=true
    this.audio.pause()
    }
    GotoPrompts(){
      this.dialogRef.close();
      this.router.navigateByUrl('/dialogbuilder/promptGroupDetail');
    }
    audioEdit(){
      this.showPromptName=false;
      this.editBtn=false
      this.isRadioPromptChange=true;
      this.showOptions=false
      this.editAudio=true
      this.showPrompts=false;
    }
    audioCancel()
    {
      this.editBtn=true
      this.showPromptName=true;
      this.isRadioPromptChange=false;
      this.showOptions=true
      this.editAudio=false
    }
    omit_special_char(event)
{   
   var k;  
   k = event.charCode;  //k = event.keyCode; (Both can be used)

 return((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8  || (k >= 48 && k <= 57)); 
}
}
export interface DialogData {
  appId:string;
  appVariableId: string;
  appVariableType: string;
  appName:string;
}
interface Type {
  id:string;
  retryType: string;
}
interface AType {
  value: string;
  viewValue: string;
}
interface Prompt{
  id:string;
  promptGroupName: string;
}