import { Component, OnInit,Inject ,ViewChild} from '@angular/core';
import {NextStateDetail} from '../Shared/Models/nextStateModel';
import {StatePromptService} from '../Shared/Services/state-prompt.service';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import {StatePromptDetail} from '../Shared/Models/statePromptModel';
import {MatSnackBar} from '@angular/material/snack-bar';
import { ActivatedRoute,Router } from '@angular/router';
export interface DialogData {

}
@Component({
  selector: 'app-init-state-popup',
  templateUrl: './init-state-popup.component.html',
  styleUrls: ['./init-state-popup.component.css']
})
export class InitStatePopupComponent implements OnInit {
  branchValue:string;
appId:string;
state:string;
stateId:string;
selectedType:string
statePromptDetail:stateName
  initNextStateForm: FormGroup;
  nextStateDetail:NextStateDetail
  stateType:boolean;
  type:string;
  stateTypes: stateType[] = []
  MethodTypes: MethodType[] = [
    {value: 'prompt', viewValue: 'Prompt'},
    {value: 'menu', viewValue: 'Menu'},
    {value: 'input', viewValue: 'Input'},
    {value: 'nonBackend', viewValue: 'Backend'},
    {value: 'branching', viewValue: 'Branching'},
    {value: 'transfer', viewValue: 'Transfer'}
  ];
  constructor(private _snackBar: MatSnackBar,private statePromptService:StatePromptService,private formBuilder: FormBuilder,  public dialogRef: MatDialogRef<InitStatePopupComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,private router: Router) { }

  ngOnInit() {
   this.appId= this.data["appId"]
   this.state= this.data["state"]
   this.type= this.data["type"]
   console.log("type", this.type)
console.log("state : "+this.state)

    this.nextStateDetail={
      id:'',
      nextState:'',
      stateName:'',
      nextStateType:'',
      type:''
    }
    this.initForm(this.nextStateDetail);
  
      this.appId= this.data["appId"]
      this.state= this.data["state"]
      this.type= this.data["type"]
   console.log("app id")
   console.log(this.appId)
      this.statePromptService.getStateInitById(this.appId).subscribe(data=>{
        console.log("init state ")
        console.log(data)
        this.nextStateDetail=data
        this.selectedType=this.nextStateDetail.nextStateType
        this.statePromptService.getAllStateName(this.appId, this.nextStateDetail.nextStateType).subscribe(data=>{
          this.stateTypes=data
        this.nextStateDetail.nextState=  this.stateTypes.find(x=>x.id==this.nextStateDetail.nextState).stateName
          console.log(this.stateTypes)
        })
        this.initForm( this.nextStateDetail)
      })
    

this.initForm(  this.nextStateDetail)
 


    }
  onNoClick(): void {
    this.dialogRef.close();
  }
  initForm(nextState:NextStateDetail) {
   
    this.initNextStateForm = this.formBuilder.group({
      
      
      nextStateType: [nextState.nextStateType, ],
      nextState: [nextState.nextState, ],
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.initNextStateForm.controls; } 
  onSubmit()
  {

    if(this.initNextStateForm.valid){

 
          this.SaveInitState(new NextStateDetail(this.initNextStateForm.value));       
 
      }


  }
  SaveInitState(nextStateDetail:NextStateDetail)
  {
    console.log("save init state")
    

      nextStateDetail.nextState=nextStateDetail.nextState+"";
      console.log(nextStateDetail)
this.statePromptService.SaveInitState(this.appId,nextStateDetail).subscribe(data=>{
  this.dialogRef.close();
  if(data==this.appId)
  this.getNotification();
})
 
 

  }

  onTypeChange()
  {
    console.log("selecteed ")
    console.log(this.selectedType)
    this.getStateType()
 
  }
  getStateType()
  {
    if(this.selectedType=="branching" )
    {
      this.branchValue="branch"
    }
    else if(this.selectedType=="nonBackend")
    {
      this.branchValue="nonbackend"
    }
   else{
     this.branchValue=this.selectedType
   }
    this.statePromptService.getAllStateName(this.appId, this.branchValue).subscribe(data=>{
      this.stateTypes=data
      console.log("init state")
      console.log(data)
      console.log(   this.stateTypes)
      if( this.stateTypes.length>0)
      this.stateTypes=data
      else
      this.stateTypes=[]
      console.log("state types")
      console.log(this.stateTypes)
    })
  }
  getNotification()
  {
    this._snackBar.open("Init saved successfully!", "", {
      duration: 2000,
      verticalPosition: "bottom",
      horizontalPosition:'right',
      panelClass: ['success'],
  });
  }
}
interface MethodType {
  value: string;
  viewValue: string;
}
interface stateType{
  id:string;
  stateName:string;
}
export class stateName {
  id:string;
  stateName:string;
}
export interface DialogData {
  isConfirm:boolean;
  type:string;
}