import { Component ,ViewChild,OnInit} from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import {Router} from "@angular/router";
export interface PeriodicElement {
  ServiceName: string;
  MethodType: string;
  UserName: string;
  Password: string;
 
 }

 const ELEMENT_DATA: PeriodicElement[] = [
   { ServiceName: '1234',MethodType:'Get',UserName:'ptl1',  Password: 'Test'},
   { ServiceName: '1234',MethodType:'Get',UserName:'ptl1',  Password: 'Test'},
   { ServiceName: '1234',MethodType:'Get',UserName:'ptl1',  Password: 'Test'},
   { ServiceName: '1234',MethodType:'Get',UserName:'ptl1',  Password: 'Test'},
 ];

@Component({
  selector: 'app-rest-detail',
  templateUrl: './rest-detail.component.html',
  styleUrls: ['./rest-detail.component.css']
})
export class RestDetailComponent implements OnInit {
  displayedColumns: string[] = ['ServiceName','MethodType','UserName', 'Password','Actions'];
  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);

   @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;

  constructor(private router: Router) {
   }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }


  ngOnInit(): void {
  }
  GetRestConfigByID(){
    
    this.router.navigateByUrl('dialogbuilder/restEdit');
  }
}
