import { Component ,ViewChild} from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import {MatPaginator} from '@angular/material/paginator';

import {MatTableDataSource} from '@angular/material/table';
import {Router} from "@angular/router";
import {DIDgroupService} from '../Shared/Services/didgroup.service';
import {MatSnackBar} from '@angular/material/snack-bar';
import{DataServiceService} from '../Shared/Services/data-service.service';
import {DeleteConfirmPopupComponent} from 'src/app/delete-confirm-popup/delete-confirm-popup.component';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';

 interface Appname {
   value: string;
   viewValue: string;
 }

@Component ({
   selector: 'app-header-component',
   templateUrl: '/home.component.html',
   styleUrls: ['./home.component.css']
})
export class HomeComponent  {
  animal: string;
  name: string;
  isConfirm:boolean;
  titleAlert: string = 'This field is required';
  post: any = '';
  displayedColumns: string[] = ['groupName','appName', 'description','DIDNumber','Actions'];
  dataSource = new MatTableDataSource(); 
  isRecordFound = false;
  Appnames: Appname[] = [
     {value: '1', viewValue: 'PTL-Demo'},
     {value: '2', viewValue: 'PTL-Demo1'},
     {value: '3', viewValue: 'PTL-Demo2'},
     {value: '3', viewValue: 'PTL-Demo2'},

   ];
   @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;

  constructor(public dialog: MatDialog,private router: Router,private DIDgroup:DIDgroupService,private dataService:DataServiceService,private _snackBar: MatSnackBar,) {

this.didGroupTable();
   }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

   ngOnInit() {
    this.dataSource.paginator = this.paginator;
    
    }
    didGroupTable(){
      
      this.DIDgroup.getDIDgroupDetail().subscribe(data=>{
        console.log("get didgroup data response");
        console.log(data);
       
       if(data.length >0)
       this.dataSource.data=data
       else
       this.isRecordFound = true;
      }
      ,(error)=>{
        if (error.status == 500)
       {
        this._snackBar.open("Backend failure!", "", {
          duration: 2000,
          verticalPosition: "bottom",
          horizontalPosition:'right',
          panelClass: ['error'],
      });
       }
       else if(error.status ==0)
       {
        this._snackBar.open("Server connection refused!", "", {
          duration: 2000,
          verticalPosition: "bottom",
          horizontalPosition:'right',
          panelClass: ['error'],
      });
       }
       else{
        this._snackBar.open("Server not connected!", "", {
          duration: 2000,
          verticalPosition: "bottom",
          horizontalPosition:'right',
          panelClass: ['error'],
      });
       }
      },
     
      );
    }
    refresh()
    {
      this.didGroupTable();
    }
    GetDIDByID(DidGroupId){   
    
      this.router.navigate(['dialogbuilder/DIDedit',DidGroupId]);
    }
    GetDIDnumberByID(didGroup)
    {
     
      this.router.navigate(['dialogbuilder/DIDnumber',didGroup.id]);
     
    }
    deletePopUp(DidGroupId){
      const dialogRef = this.dialog.open(DeleteConfirmPopupComponent, {
        width: '450px'
      });
  
      dialogRef.afterClosed().subscribe(result => {
     
        this.isConfirm=result;
         if(this.isConfirm)
        this.deleteDidGroup(DidGroupId)
      });
    }
    deleteDidGroup(DidGroupId){
this.DIDgroup.deleteDIDgroupDetail(DidGroupId).subscribe(data=>{
  console.log("delete response");
  console.log(data);
  this.DIDgroup.getDIDgroupDetail().subscribe(data=>{
    if(data.length >0)
    this.dataSource.data=data
    else
    this.isRecordFound = true;
  })
  this._snackBar.open("DID group deleted!", "", {
    duration: 2000,
    verticalPosition: "bottom",
    horizontalPosition:'right',
    panelClass: ['success'],
});
})
    
    }
}