import { Component, OnInit,Inject ,ViewChild} from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import {MatTableDataSource} from '@angular/material/table';
import {MatPaginator} from '@angular/material/paginator';

export interface DialogData {

}
export interface PeriodicElement {
  Key: string;
  Value: string;
 }
 const ELEMENT_DATA: PeriodicElement[] = [
   { Key: '1234',  Value: 'Test'},
 ];
@Component({
  selector: 'app-state-popup',
  templateUrl: './state-popup.component.html',
  styleUrls: ['./state-popup.component.css']
})
export class StatePopupComponent implements OnInit {
  selectedPrompt: string;
  selectedStateType:string;
  IsAnnouncement:boolean=false;
  IsAttachData:boolean=false;
  IsInput:boolean=false;
  IsTransefer:boolean=false;
  IsApplication:boolean=false;
  IsTable:boolean=false;
  IsBackEnd:boolean=false;
  Prompts: Prompt[] = [
    {value: 'Prompt', viewValue: 'Prompt'},
    {value: 'Menu', viewValue: 'Menu'},
    {value: 'Transfer', viewValue: 'Transfer'},
    {value: 'Application', viewValue: 'Application'},
    {value: 'Input', viewValue: 'Input'},
    {value: 'Backend', viewValue: 'Backend'}
  ];
  
  announcecs = [
    {value: 'Announcement', viewValue: 'Announcement'},
   
  ];
  @ViewChild(MatPaginator, ) paginator: MatPaginator;
  formGroup: FormGroup;
  displayedColumns: string[] = ['Key', 'Value','Actions'];
  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
  constructor(private formBuilder: FormBuilder,  
  ) { }


    ngOnInit() {
      this.createForm();
    }

    createForm() {
     
      this.formGroup = this.formBuilder.group({
        
        StateType: [null, Validators.required],
        DialogName: [null, Validators.required],
        PromptType: [null, Validators.required],
        TermChar: [null, Validators.required],
        
        MinimumLength: [null, Validators.required],
        MaximumLength: [null, Validators.required],
        Grammar: [null, Validators.required],
        Result: [null, Validators.required],
        AppName: [null, Validators.required],
        AppURL: [null, Validators.required],
        
        TransactionName: [null, Validators.required],
        TransactionType: [null, Validators.required],
        TransferNumber: [null, Validators.required],
      });
    }
    // convenience getter for easy access to form fields
    get f() { return this.formGroup.controls; }  
    onAnnounceSelection() {
    
      if(this.selectedPrompt=="Announcement")
      {
        this.IsAnnouncement=true;
      }
    }
    onStateTypeSelection()
    {
      if(this.selectedStateType=="Prompt")
      {
        this.IsAnnouncement=false;
        this.IsAttachData=false;
        this.IsInput=false;
        this.IsTransefer=false;
        this.IsApplication=false;
        this.IsBackEnd=false
      }
      if(this.selectedStateType=="Menu")
      {
        this.IsAnnouncement=false;
        this.IsAttachData=true;
      }
      if(this.selectedStateType=="Input")
      {
        this.IsAnnouncement=false;
        this.IsAttachData=true;
        this.IsInput=true;
        
      }
      if(this.selectedStateType=="Transfer")
      {
        this.IsAnnouncement=false;
        this.IsAttachData=false;
        this.IsInput=false;
        this.IsTransefer=true;
      }
      if(this.selectedStateType=="Application")
      {
        this.IsAnnouncement=false;
        this.IsAttachData=false;
        this.IsInput=false;
        this.IsTransefer=false;
        this.IsApplication=true;
      }
      
      if(this.selectedStateType=="Backend")
      {
        this.IsAnnouncement=false;
        this.IsAttachData=false;
        this.IsInput=false;
        this.IsTransefer=false;
        this.IsApplication=false;
        this.IsBackEnd=true;
        
      }
    }
}
interface Prompt {
  value: string;
  viewValue: string;
}
