import { Component, OnInit ,Inject,ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute,Router } from '@angular/router';
import {MatSnackBar} from '@angular/material/snack-bar';
import {HolidayService} from '../Shared/Services/holiday.service';
import{ HolidayDetail} from '../Shared/Models/holidayModel';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';

@Component({
  selector: 'app-holiday-popup',
  templateUrl: './holiday-popup.component.html',
  styleUrls: ['./holiday-popup.component.css']
})
export class HolidayPopupComponent implements OnInit {
  holidayDetail:HolidayDetail
  holidayForm: FormGroup;
  inEditPage: boolean;
 type:string;
 holidayId:string;
  id:string;
  appId:string;
  appVariableType:string;
  appVariableId:string;
  constructor(private holidayService:HolidayService,private formBuilder: FormBuilder,private router: Router,private _router: ActivatedRoute,private _snackBar: MatSnackBar,
    public dialogRef: MatDialogRef<HolidayPopupComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) { }

    ngOnInit() {
      console.log(this.data["appId"])
      this.appId=this.data["appId"];
      this.type=this.data["type"];
      this.holidayId=this.data["holidayId"];
      console.log(this.type)
      this.holidayDetail={
        id:'',
        holidayName:'',
        date:''
      }
      if(this.type=="edit")
      {
      this.inEditPage = true;
      this.holidayService.getHolidayById(this.holidayId).subscribe(data=>{
        this.initForm(data)
      })
      }
      this.initForm(this.holidayDetail);
    }
    initForm(holidayDetail:HolidayDetail) {
     
      this.holidayForm = this.formBuilder.group({
        
        holidayName: [holidayDetail.holidayName, Validators.required],
      
        date: [holidayDetail.date, Validators.required],
      });
    }
    // convenience getter for easy access to form fields
    get f() { return this.holidayForm.controls; }  
    onSubmit()
    {
      console.log("form value holiday")
      console.log(this.holidayForm.value)
      if(this.holidayForm.valid){
 
        if(this.type=="create")
            this.SaveHoliday(new HolidayDetail(this.holidayForm.value));       
      else
      this.UpdateHoliday(new HolidayDetail(this.holidayForm.value));
        }
 

    }
    SaveHoliday(holiday:HolidayDetail)
    {
      console.log("holiday dave")
      console.log(holiday)
      this.holidayService.SaveHoliday(this.appId,holiday).subscribe(data=>{
        this.dialogRef.close({event:"saved"});
        this.router.navigateByUrl('/dialogbuilder/apps');
        this._snackBar.open("Holiday saved successfully!", "", {
          duration: 2000,
          verticalPosition: "bottom",
          horizontalPosition:'right',
          panelClass: ['success'],
      });
      })
    }
    UpdateHoliday(holiday:HolidayDetail)
    {
      holiday.id=this.holidayId+"";
      holiday.date=holiday.date+"";
      console.log("update")
      console.log(holiday)
     this.holidayService.updateHoliday(this.appId,holiday).subscribe(data=>{
      this.dialogRef.close({event:"saved"});
      this.router.navigateByUrl('/dialogbuilder/apps');
      this._snackBar.open("Holiday updated successfully!", "", {
        duration: 2000,
        verticalPosition: "bottom",
        horizontalPosition:'right',
        panelClass: ['success'],
    });
     })
    }
    onNoClick(): void {
      this.dialogRef.close({event:"closed"});
    }
}
export interface DialogData {
  appId:string;
type:string;
holidayId:string;
}

interface MethodType {
  value: string;
  viewValue: string;
}
